/**
 * Sticker elements setup
 *
 * 	new CDQ.component.Stickers()
 *
 * @class Stickers
 * @constructor
 * @namespace component
 */
CDQ.component.Stickers = Class.extend({
	/**
	 * Stickers constructor
	 *
	 * @method init
	 */
	init: function() {
		this.stickers = jQuery('.sticker:not(.sticker-up, .sticker-issues.mobile)');
		this.stickerUp = jQuery('.sticker-up');
		this.scrollTimer = null;
		this.stickerToggle = jQuery('.sticker-toggle');
		this.stickerIssues = jQuery('.sticker-issues');
		this.stickerIssuesToggle = this.stickerIssues.find('.sticker-toggle');
		this.contentEnd = jQuery('[data-contentEnd]');

		this.bind();
		this.checkHeight();
		this.fixForTouch();
		this.setupBackToTop();
	},

	/**
	 * Bind events
	 *
	 * @method bind
	 */
	bind: function() {
		var _this = this;

		this.stickers.each(function() {
			var sticker = jQuery(this);

			sticker.find('.sticker-toggle').on(CDQ.global.activateAction, function() {
				_this.toggleSticker(jQuery(this));
			});
		});

		jQuery(document).on(CDQ.global.activateAction, function(ev) {
			var sticker = jQuery('.sticker:not(.sticker-up)');

			if (!sticker.is(ev.target) && sticker.has(ev.target).length === 0) {
				sticker.removeClass('opened inactive');
			}
		});

		jQuery(window).resize(function() {
			_this.checkHeight();
		});

		jQuery(window).on('scroll', function() {
			clearTimeout(_this.scrollTimer);

			_this.scrollTimer = setTimeout(function() {
				_this.setupBackToTop();

				if(_this.contentEnd.length) {
					_this.stickerIssuesBehavior();
				}
			}, 100);
		});

		this.stickerUp.find('.sticker-toggle').on(CDQ.global.activateAction, function() {

			_this.stickers.removeClass('opened');
			jQuery('html, body').animate({
				scrollTop: 0
			}, function() {
				_this.setupBackToTop();
			});

			return false;
		});
	},

	/**
	 * Show/Hide stickers
	 *
	 * @method setupBackToTop
	 * @param {object} stickerToggle Object of the element to open/hide
	 */
	toggleSticker: function(stickerToggle) {
		var el = stickerToggle,
			p = el.parent(),
			siblings = jQuery('.sticker-toggle').parent().not(p);

		if (!p.hasClass('opened')) {
			p.addClass('opened');
			p.removeClass('inactive');
			siblings.removeClass('opened').addClass('inactive');
		} else {
			p.removeClass('opened inactive');
			siblings.removeClass('inactive');
		}
	},

	/**
	 * Defines the behavior of the Sticker Issues.
	 * When the scroll reaches the element with "data-contentEnd" the sticker will open
	 *
	 * @method stickerIssuesBehavior
	 */
	stickerIssuesBehavior: function() {
		var _this = this,
			windowScrollTop = jQuery(window).scrollTop(),
			windowHeight = jQuery(window).height(),
			documentHeight = jQuery(document).height(),
			contentEndOffset = _this.contentEnd.offset().top - windowHeight,
			isOutsideContent = windowScrollTop >= contentEndOffset,
			isInBottom = (windowScrollTop + windowHeight) == documentHeight;

	   	if(isOutsideContent && !isInBottom) {
	   		if(!_this.isOpen(_this.stickerIssues)){
	   			_this.toggleSticker(_this.stickerIssuesToggle);
	   		}
	   	} else if(isInBottom || !isOutsideContent) {
	       	if(_this.isOpen(_this.stickerIssues)){
	       		_this.toggleSticker(_this.stickerIssuesToggle);
	       	}
	   	}
	},

	isOpen: function(sticker) {
		if(sticker.hasClass('opened')){
			return true;
		}

		return false;
	},

	checkHeight: function() {
		var windowHeight = window.innerHeight;

		if (windowHeight < 580) {
			return this.stickers.hide();
		}

		return this.stickers.show();
	},

	/**
	 * Show/Hide the back to top button according to document position
	 *
	 * @method setupBackToTop
	 */
	setupBackToTop: function() {
		var scrollTop = jQuery(document).scrollTop();

		if (scrollTop > 0) {
			return this.stickerUp.show();
		}

		jQuery('.social-bar__wrapper').removeClass('active');
		return this.stickerUp.attr("style", "display: none !important");

	},

	/**
	 * Setup fixes for touch elements
	 *
	 * @method fixForTouch
	 */
	fixForTouch: function() {
		if(!Modernizr.touch) {
			return;
		}

		var b = jQuery('body'),
			d = jQuery(document);

		/* bind events */
		jQuery(document).on('focus', 'input', function() {
			b.addClass('fixfixed');
		});

		jQuery(document).on('blur', 'input', function() {
			b.removeClass('fixfixed');
		});

		jQuery(window).on('touchmove', function() {
			d = jQuery(document);
		});
	}
});
