/**
 * Total returns widget class
 *
 *	// Settings object isn't required. The example bellow shows the default values
 *	new CDQ.component.TotalReturnsWidget({
 *		container: '.total-returns-widget',
 *		select: 'select[name="total-returns-period"]',
 *		period: 'input:radio[name="period"]',
 *		table: '.total-returns-table',
 *		tabs: '.total-returns-options',
 *		url: null,
		totalColumns: {
			'#cummulative': 5,
			'#average_annualized': 4
		}
 *	});
 *
 * @class TotalReturnsWidget
 * @constructor
 * @namespace component
 */
CDQ.component.TotalReturnsWidget = Class.extend({
	/**
	 * Widget constructor
	 *
	 * @method init
	 * @param {Object} settings Constructor settings
	 */
	init: function(settings) {
		this.settings = this.getSettings(settings);
		this.container = jQuery(this.settings.container);
		this.select = this.container.find(this.settings.select);
		this.period = this.container.find(this.settings.period);
		this.activeTab = '#average_annualized';
		this.table = this.container
			.find(this.settings.table + '[data-total-returns=' + this.activeTab + ']');

		this.startFrom = 0;

		this.load();
	},

	/**
	 * Merge default settings with constructor settings
	 *
	 * @method getSettings
	 * @param {Object} settings Constructor settings
	 * @return {Object} Merged default and constructor settings
	 */
	getSettings: function(settings) {
		var defaultSettings = {
			container: '.total-returns-widget',
			select: 'select[name="total-returns-period"]',
			period: 'input:radio[name="period"]',
			table: '.total-returns-table',
			tabs: '.total-returns-options',
			url: null,
			trackingCallback: null,
			totalColumns: {
				'#cummulative': 4,
				'#average_annualized': 5
			}
		};

		return jQuery.extend(true, defaultSettings, settings);
	},

	/**
	 * Load json data
	 *
	 * @method load
	 */
	load: function() {
		var _this = this;

		jQuery.ajax({
			url: this.settings.url,
			dataType: 'JSON',
			success: function(data) {
				_this.data = data;

				_this.bind();
			}
		});
	},

	/**
	 * Bind events
	 *
	 * @method bind
	 */
	bind: function() {
		var _this = this,
			tabLinks = jQuery(this.settings.tabs).find('a');

		this.select.on('change', function() {
			_this.setData();
		});

		this.period.on('change', function() {
			var type = this.value;
			_this.changeSelect(type);
			_this.setData();
		});

		tabLinks.on(CDQ.global.activateAction, function(ev) {
			_this.changeTab(ev);

			return false;
		});
	},

	/**
	 * Get data according to period and type
	 *
	 * @method getData
	 * @param {String} period The date range to get data
	 * @param  {String} type The type of data. If quarter or month
	 */
	getData: function(period, type) {
		var _this = this,
			data = this.data.totalReturnData[type];


		for (var i = data.length - 1; i >= 0; i--) {
			if (data[i].period == period) {
				_this.populateByData(data[i]);
			}
		}
	},

	/**
	 * For each data object, populate the widget
	 *
	 * @method populateByData
	 * @param  {Object} data Table object data
	 */
	populateByData: function(data) {
		var _this = this;
		jQuery.each(data, function(index) {
			if (index != "period") {
				_this.populate(index, this);
			}
		});
	},

	/**
	 * Change select options
	 *
	 * @method changeSelect
	 * @param  {String} Data type
	 */
	changeSelect: function(type) {
		var obj = this.data.totalReturnData[type];

		if (!CDQ.global.util.isMobile()) {
			this.select.data('chosen').destroy();
		}

		this.select.html('');

		for (var i = 0; i < obj.length; i++) {
			this.select.append('<option value="' + obj[i].period + '">' + obj[i].period + '</option>');
		}

		if (!CDQ.global.util.isMobile()) {
			this.select.chosen();
		}
	},

	/**
	 * Clean table rows
	 *
	 * @method cleanRows
	 */
	cleanRows: function() {
		var body = this.table.find('tbody');

		body.find('tr:not(.main-row, [data-name])').remove();
		body.find('.main-row:not([data-name="fundData"])').hide();
		body.find('[data-name="marketPriceReturnData"], [data-name="navData"]').find('td:not(td:first)').html('-');
	},

	/**
	 * Populate table
	 *
	 * @method populate
	 * @param {String} name Selector to get the correct table element to fill in
	 * @param  {Object} data The selected data to fill in
	 */
	populate: function(name, data) {
		var _this = this,
			element = this.table.find('[data-name="' + name + '"]');

		if (name === "marketPriceReturnData" || name === "navData") {
			element.children().each(function(i) {
				var td = jQuery(this),
					index = i > 0 ? i + _this.startFrom : 0;
				td.html(data[index]);
			});
		} else {
			if (jQuery.isArray(data)) {
				element.show();
				for (var i = 0; i < data.length; i++) {
					var row = this.getRows(data[i]);

					element.after(row);
				}
			}
		}
	},


	/**
	 * Get rows to append to table
	 *
	 * @method getRows
	 * @param  {Object} data Selected data table
	 * @return {HTMLElement} Created row
	 */
	getRows: function(data) {
		var row = jQuery('<tr>'),
			totalColumns = this.settings.totalColumns[this.activeTab],
			index,
			dataValue = '';

		for (var i = 0; i <= totalColumns; i++) {
			index = i > 0 ? i + this.startFrom : 0;
			dataValue = data[index] ? data[index] : '-';

			row.append('<td>' + dataValue + '</td>');
		}

		return row;
	},

	/**
	 * Set data according to selected values
	 *
	 * @method setData
	 */
	setData: function() {
		var period = this.select.val(),
			type = jQuery(this.settings.period + ':checked').val();

		this.cleanRows();
		this.getData(period, type);

		if(this.settings.trackingCallback) {
			this.settings.trackingCallback(period);
		}
	},

	/**
	 * Action executed when tab changes
	 *
	 * @method changeTab
	 * @param {Object} ev jQuery Event
	 * @return {Boolean} Return false to stop link execution
	 */
	changeTab: function(ev) {
		var link = jQuery(ev.target),
			tabLists = jQuery(this.settings.tabs).find('li');

		tabLists.removeClass('active');
		link.parent().addClass('active');
		this.container.find(this.settings.table).addClass('hide');

		this.activeTab = link.attr('href');

		this.table = this.container
			.find(this.settings.table + '[data-total-returns=' + this.activeTab + ']');

		this.table.removeClass('hide');

		if (this.activeTab == '#average_annualized') {
			this.startFrom = 0;
		} else {
			this.startFrom = 5;
		}

		this.setData();

		return false;
	}
});
