/**
 * Setup chosen selects in all pages
 *
 *	// Settings object isn't required. The example bellow shows the default values
 *  	new CDQ.component.Chosen({
 *  		selector: '.chosen'
 *    	});
 *
 * @class Chosen
 * @constructor
 * @namespace component
 */
CDQ.component.Chosen = Class.extend({
	/**
	 * Chosen constructor
	 *
	 * @method init
	 * @param  {Object} settings receive an object that contains settings to be used.
	 * 		@param  {String} settings.selector The selector to apply chosen to selects.
	 */
	init: function(settings) {
		this.chosenTimer = null;
		this.settings = settings;

		var wW = jQuery(window).width();

		if ((CDQ.global.util.isMobile() && wW >= CDQ.global.util.getBreakpoints('large')) ||
			!CDQ.global.util.isMobile()) {
			this.create();
			this.bind();
		}
	},

	/**
	 * Replace selects by chosen elements and remove no initialized class
	 *
	 * @method create
	 */
	create: function() {
		this.chosenElements = jQuery(this.settings.selector).chosen({
			display_disabled_options: false
		});

		jQuery('.no-chosen').removeClass('no-chosen');
	},

	/**
	 * Resize chosen on window resize
	 *
	 * @method bind
	 */
	bind: function() {
		var _this = this;

		jQuery(window).on('resize Accordion.opened', function() {
			clearTimeout(_this.chosenTimer);

			_this.chosenTimer = setTimeout(function() {
				_this.chosenElements.each(function() {
					jQuery(this).data('chosen').destroy();
				});

				_this.create();
			}, 100);
		});
	}
});
