/**
 * Print only selected section
 *
 *	new CDQ.component.SectionPrint();
 *
 * @class SectionPrint
 * @constructor
 * @namespace component
 */
CDQ.component.SectionPrint = Class.extend({
	/**
	 * Start the application
	 * @method init
	 * Collects all rows and space between the print button and the next print button and hides the rest for print
	 */
	init: function() {
		var printBtn = jQuery('.js-section-print'),
			elements = jQuery('.content-wrap').children('div')
		printBtn.each(function(i) {
			jQuery(this).on('click', function() {
				var sectionStart = jQuery(this).closest('div.row'),
					sectionEnd = jQuery(printBtn[i + 1]).closest('div.row');
				/*Hide all elements*/
				elements.addClass('hide-for-print')
				/*Show all elements from this section*/
				sectionStart.nextUntil(sectionEnd).removeClass('hide-for-print')
				sectionStart.removeClass('hide-for-print')
				window.print();
				elements.removeClass('hide-for-print');
			})
		})
	}

});
