// jshint ignore: start
CDQ.component.DonutChartLayered = Class.extend({
	init: function(selector, data, chartOptions, extraOptions) {
		var _this = this;


		jQuery(function() {
			_this.data = data;

			Highcharts.setOptions(extraOptions);
			_this.container = $(selector);
			_this.containerWidth = _this.container.width();
			_this.namespace = 'donut-chart';
			_this.legendName = 'legend';

			_this.colors = Highcharts.getOptions().colors;
			_this.mainData = [];
			_this.subSeriesData = [];
			_this.chartOptions = chartOptions;
			_this.lastWidth = jQuery(window).width();
			_this.bind();
			_this.setupData();
		});
	},

	bind: function() {
		var _this = this,
			rebuildEvent = CDQ.global.util.isMobile() ? 'orientationchange' : 'resize';

		this.toggleViewClass();

		if (typeof this.data.legend === 'undefined') {

			jQuery(window).on('orientationchange resize', function() {
				_this.setupLegendsHTML();

				if(width === _this.lastWidth) {
					return false;
				}

				_this.lastWidth = width;
				_this.rebuildChart();
			});
		}
	},

	rebuildChart: function () {
		var _this = this;
		clearTimeout(this.timer);
		this.timer = setTimeout(function() {
			_this.toggleViewClass();
			if (_this.chartElement) {
				if (Highcharts.charts[_this.chartElement.data('highchartsChart')]) {
					Highcharts.charts[_this.chartElement.data('highchartsChart')].destroy();
					Highcharts.charts.shift(_this.chartElement.data('highchartsChart'));
				}
				_this.chartElement = null;
				setTimeout(function () {
					_this.createChart();
				}, 100)
			}
		}, 100);
	},

	rebuildChart: function () {
		var _this = this;

		clearTimeout(this.timer);
		this.timer = setTimeout(function() {
			_this.toggleViewClass();
			if (_this.chartElement && Highcharts.charts[_this.chartElement.data('highchartsChart')]) {
				Highcharts.charts[_this.chartElement.data('highchartsChart')].destroy();
				Highcharts.charts.shift(_this.chartElement.data('highchartsChart'));
			}

			setTimeout(function () {
				_this.createChart();
			}, 200);
		}, 200);
	},

	setupData: function() {
		// Build the data arrays
		this.mainData = [];
		this.subSeriesData = [];

		for (var i = 0; i < this.data.series.length; i++) {
			// add main data
			this.mainData.push({
				name: this.data.categories[i],
				y: this.data.series[i].y,
				color: this.data.series[i].color,
				legendIndex: this.data.series[i].legendIndex
			});

			// add sub series data
			if (typeof this.data.series[i].drilldown != 'undefined') {
				for (var j = 0; j < this.data.series[i].drilldown.data.length; j++) {
					var brightness = 0.2 - (j / this.data.series[i].drilldown.data.length) / 5;
					this.subSeriesData.push({
						name: this.data.series[i].drilldown.categories[j],
						y: this.data.series[i].drilldown.data[j],
						color: Highcharts.Color(this.data.series[i].color).brighten(brightness).get(),
						legendIndex: this.data.series[i].legendIndex
					});
				}
			}
		}

		this.createChart();
	},

	createChart: function() {
		var _this = this,
			responsiveData = this.getResponsiveData();

		var defaultSettings = {
			chart: {
				type: 'pie',
				backgroundColor: '#e6eaed',
				events: {
					load: function() {
						_this.setupLegendsHTML();
					}
				}
			},
			title: {
				text: null
			},
			plotOptions: {
				pie: {
					shadow: false,
					center: ['40%', '50%'],
					showInLegend: true,
					point: {
						events: {
							legendItemClick: function(e) {
								return false;
							},
							mouseOver: function(e) {
								if (jQuery(this.legendItem).length === 0) {
									return true
								}
								var legend = jQuery(this.legendItem.element),
									span = legend.find('.' + _this.namespace + '-main-legend');

								if (span.length == 0) {
									span = legend.find('.' + _this.namespace + '-data-legend .' + _this.namespace + '-legend-icon');
								}

								span.css('background-color', Highcharts.Color(this.color).brighten(0.1).get());
							},
							mouseOut: function() {
								if (jQuery(this.legendItem).length === 0) {
									return true
								}
								var legend = jQuery(this.legendItem.element),
									span = legend.find('.' + _this.namespace + '-main-legend');

								if (span.length == 0) {
									span = legend.find('.' + _this.namespace + '-data-legend .' + _this.namespace + '-legend-icon');
								}

								span.css('background-color', this.color);
							}
						}
					},
					allowPointSelect: false // it highlights one piece of donut
				}
			},
			tooltip: {
				useHTML: true,
				formatter: function() {
					return this.key + ' <strong>' + Highcharts.numberFormat(this.point.percentage, 1) + '%</strong>';
				}
			},
			legend: {
				layout: 'vertical',
				align: 'right',
				verticalAlign: 'top',
				width: 282,
				borderWidth: 0,
				symbolRadius: 0,
				symbolWidth: 0,
				symbolHeight: 0,
				padding: 0,
				useHTML: true,
				labelFormat: '<span class="' + this.namespace + '-data-legend">{name}</span>'
			},
			series: [{
				name: this.data.mainName,
				data: this.mainData,
				size: '100%',
				innerSize: '71%',
				index: 1,
				dataLabels: {
					formatter: function() {
						return Highcharts.numberFormat(this.point.y, 1) + '%';
					},
					color: 'white',
					distance: -25,
					style: {
						fontSize: '13px',
						fontWeight: 'normal'
					}
				},
				borderColor: 'none'
			}, {
				name: this.data.subSeriesName,
				data: this.subSeriesData,
				size: '65%',
				innerSize: '55%',
				index: 2,
				dataLabels: {
					formatter: function() {
						return Highcharts.numberFormat(this.point.y, 1) + '%';
					},
					color: 'white',
					distance: -13
				},
				borderColor: 'none'
			}],
			credits: false
		};

		var settings = jQuery.extend(true, defaultSettings, this.data, responsiveData);

		this.chartElement = this.container.highcharts(settings);
	},

	setupLegendsHTML: function() {
		var _this = this,
			legends = jQuery('.' + this.namespace + '-data-legend'),
			pK = 0,
			currentColor = 0;

		this.counter = 0;
		this.alreadySet = [];
		this.colorsUsed = [];

		legends.each(function(index) {
			var el = this;
			setTimeout(function() {
				var legend = jQuery(el),
					mainSerieColor;

				legend.attr('id', _this.legendName + '-' + pK++);

				mainSerieColor = _this.setupMainSeries(legend, currentColor);
				if (mainSerieColor) {
					currentColor = +mainSerieColor;
					_this.alreadySet.push(index);
					_this.getLastFromPrevSerie(legend);

					legend.on('mouseenter', function() {
						var newColor = Highcharts.Color(legend.css('background-color')).brighten(0.1).get();
						legend.css('background-color', newColor);
					});

					legend.on('mouseleave', function() {
						legend.css('background-color', legend.attr('data-color'));
					});
				}

				_this.setupSubSeries(legend, index);
			}, 500);
		});
	},

	setupMainSeries: function(legend, currentColor) {
		var info = this.getResponsiveData().legend;
		for (var i in this.mainData) {
			if (this.mainData[i].name === legend.text()) {
				var legendItem = legend.closest('.highcharts-legend-item');

				legend.addClass(this.namespace + '-main-legend');
				legend.css('background-color', this.mainData[i].color);
				legend.attr('data-color', this.mainData[i].color);
				legend.append('<i class="icons-chart-legend-arrow"></i>');

				if (info.layout == 'vertical' && info.xsmall) {
					legendItem.css({
						"top": this.counter,
						"left": "20px"
					});
					this.counter += legend.parent().height() + 10;
				} else if (info.layout == 'horizontal') {
					legendItem.css({
						"top": 0,
						"left": this.counter
					});
					this.counter += legend.parent().width() + 5;
				}
				return currentColor + 1;
			}
		}
	},

	setupSubSeries: function(legend, index) {
		for (var i in this.subSeriesData) {
			if (jQuery.inArray(index, this.alreadySet) == -1 && jQuery.inArray(i, this.colorsUsed) == -1 && this.subSeriesData[i].name == legend.text()) {
				var symbol = jQuery('<span class="' + this.namespace + '-legend-icon"></span>'),
					percentage = jQuery('<span class="' + this.namespace + '-legend-percentage">' + Highcharts.numberFormat(this.subSeriesData[i].y, 1) + '%</span>');

				legend.attr('data-color', this.subSeriesData[i].color);
				symbol.css('background-color', this.subSeriesData[i].color);
				legend.prepend(symbol);
				legend.append(percentage);

				this.alreadySet.push(index);
				this.colorsUsed.push(i);

				this.bindLegend(legend);
			}
		}
	},

	getLastFromPrevSerie: function(legend) {
		var currentLegendId = parseInt(legend.attr('id').replace(this.legendName + '-', '')),
			prevLegendId = currentLegendId - 1,
			prevElement;

		if (prevLegendId >= 0) {
			prevElement = jQuery('#' + this.legendName + '-' + prevLegendId);
			prevElement.addClass(this.namespace + '-no-row');
		}

	},

	bindLegend: function(legend) {
		var _this = this;
		legend.on('mouseenter', function() {
			var icon = legend.find('.' + _this.namespace + '-legend-icon'),
				newColor = Highcharts.Color(icon.css('background-color')).brighten(0.1).get();
			icon.css('background-color', newColor);
		});

		legend.on('mouseleave', function() {
			var icon = legend.find('.' + _this.namespace + '-legend-icon');
			icon.css('background-color', legend.attr('data-color'));
		});
	},

	getResponsiveData: function() {
		var _this = this,
			win = window,
			viewPortWidth = win.innerWidth;

		if (viewPortWidth <= 768 && viewPortWidth > 480) {
			return {
				legend: {
					layout: 'horizontal',
					align: 'center',
					verticalAlign: 'bottom'
				},
				plotOptions: {
					pie: {
						center: ['50%', '50%']
					}
				}
			};
		} else if (viewPortWidth <= 480) {
			return {
				legend: {
					layout: 'vertical',
					align: 'center',
					verticalAlign: 'bottom',
					xsmall: true
				},
				plotOptions: {
					pie: {
						center: ['50%', '50%']
					}
				},
				series: [{
					dataLabels: {
						distance: -15,
						style: {
							fontSize: '11px'
						}
					}
				}, {
					dataLabels: {
						distance: -10,
						style: {
							fontSize: '9px'
						}
					}
				}]
			}
		}
		return {
			legend: {
				align: 'right',
				y: 13
			},
			plotOptions: {
				pie: {
					center: ['50%', '50%']
				}
			}
		};
	},

	toggleViewClass: function() {
		if (window.innerWidth <= 768) {
			this.container.addClass('small-chart');
		} else {
			this.container.removeClass('small-chart');
		}
	}
});
