/**
 * Setup autocomplete that goes on header of pages
 *
 * 	// Settings object isn't required. The example bellow shows the default values
 * 	new CDQ.component.AutoCompleteSearch({
 * 		resultSelector: '.autocomplete-search-result',
 *		url: null,
 *		sourceData: {},
 *		input: '.search-text',
 *		button: '.btn-search',
 *		mainMenu: '.main-navigation'
 * 	});
 *
 * @class AutoCompleteSearch
 * @constructor
 * @namespace component
 */

CDQ.component.AutoCompleteSearch = Class.extend({
	/**
	 * Constructor that receives some settings and start to create the autocomplete search
	 *
	 * @method init
	 * @param {Object} [settings] receive an object that contains settings to be used in the method.
	 *                            @param  {String} [settings.resultsSelector='#autocomplete-search-result'] The string where autocomplete results will be appended to
	 *                            @param  {String} settings.url=null The url of search
	 *                            @param  {Object} [settings.sourceData={}] An object where you can send parameters in your url
	 *                            @param  {Object} [settings.input='.search-text'] The input field selector
	 *                            @param  {Object} [settings.button='.btn-search'] The search button selector
	 *                            @param  {Object} [settings.mainMenu='.main-navigation'] The main navigation selector
	 */
	init: function(settings) {
		/**
		* Settings property
		*
		* @property settings
		* @type Object
		* @default {
			resultSelector: '#autocomplete-search-result',
			url: null,
			sourceData: {},
			input: '#nav-search',
			mainMenu: '.main-navigation'
		}
		*/
		this.settings = this.getSettings(settings);

		this.searchInput = jQuery(this.settings.input);

		this.setup();
		this.bind();
	},

	/**
	 * Extend default settings and settings from constructor
	 *
	 * @method getSettings
	 * @param {Object} [settings] receive an object that contains settings to replace default ones.
	 * @return {Object} Extended Settings
	 */
	getSettings: function(settings) {
		var defaultSettings = {
			resultSelector: '.autocomplete-search-result',
			url: null,
			sourceData: {},
			input: '.search-text',
			button: '.btn-search',
			mainMenu: '.main-navigation',
			onHTMLReady: function () {
			}
		}

		return jQuery.extend(true, defaultSettings, settings);
	},

	/**
	 * Bind events
	 *
	 * @method bind
	 */
	bind: function() {
		var _this = this;

		jQuery(this.settings.button).on('click', function() {
			jQuery(window).trigger('Search.doSearch', [_this.searchInput]);
			return false;
		});

		this.searchInput.on('keydown', function(ev) {
			if (ev.keyCode === jQuery.ui.keyCode.ENTER) {
				jQuery(window).trigger('Search.doSearch', [_this.searchInput]);
				return false;
			}
		});
	},

	/**
	 * Setup the autocomplete widget (requires jquery ui autocomplete)
	 *
	 * @method setup
	 */
	setup: function() {
		var _this = this,
			source = this.settings.sourceData,
			stickers = jQuery('.sticker');

		jQuery.widget("custom.catcomplete", jQuery.ui.autocomplete, {
			_create: function() {
				this._super();
				this.widget().menu("option", "items", "> :not(.ui-menu-item-results)");
			},
			_renderMenu: function(ul, items) {
				var _this = this;

				jQuery.each(items, function(index, item) {
					setTimeout(function() {
						_this._renderCategoryItem(ul, item);
						if (item.hasOwnProperty('results')) {
							jQuery.each(item.results, function(n, record) {
								_this._renderItem(ul, record, item);

								if (items.length - 1 == index && item.results.length - 1 == n) {
									_this._renderTotalItem(ul, record, item);
								}
							});
						}
					}, 0);
				});
			},

			_renderItem: function(ul, record, item) {
				var li,
					a,
					searchTitle,
					ticker,
					title,
					divl,
					divr;


				if (record.hasOwnProperty('ticker')) {
					ticker = jQuery(document.createElement('span'))
						.text(record.ticker)
						.addClass('label label-grey pull-left hide-md');

					title = jQuery(document.createElement('span'))
						.addClass('ticker-item-title')
						.append(jQuery(document.createElement('span')).html(record.marketingFundPrefix + '<br />'))
						.append(record.text);

					searchTitle = jQuery(document.createElement('span'))
						.addClass('row')
						.append(ticker)
						.append(title);
				} else {
					searchTitle = jQuery(document.createElement('span'))
						.addClass('ellipsis')
						.text(record.text);
				}

				a = jQuery(document.createElement('a'))
					.addClass('clearfix row');

				if (record.hasOwnProperty('videoId') && record.videoId.length > 0) {
					a.attr('href', 'javascript:;');
					a.attr('data-video', record.videoId);
					a.attr('title', record.text);
				} else {
					a.attr('href', record.url)
				}

				if (CDQ.i.hasOwnProperty('disclaimerLightbox') && !record.url.match(CDQ.i.disclaimerLightbox.pattern)) {
					CDQ.i.disclaimerLightbox.bindDisclaimer(a);
				}

				divl = jQuery(document.createElement('div'))
					.addClass('item-result col-xl-8 no-padding-right')
					.append(searchTitle);

				divr = jQuery(document.createElement('div'))
					.addClass('col-xl-4 text-right hide-md')
					.html(record.type);

				a.append(divl).append(divr);

				li = jQuery(document.createElement('li'))
					.addClass('ui-menu-item')
					.append(a).data("ui-autocomplete-item", item);

				ul.append(li);
			},

			_renderCategoryItem: function(ul, item) {
				var coll,
					colr,
					title,
					a,
					li;

				coll = jQuery(document.createElement('div'))
					.addClass('col-3-4');

				colr = jQuery(document.createElement('div'))
					.addClass('col-1-4 text-right hide-md');

				title = jQuery(document.createElement('p'))
					.append(jQuery(document.createElement('span')).addClass('text-highlighted').text(item.name))
					.append(jQuery(document.createElement('span')).addClass('hide-md').text(item.resultText));

				coll.append(title);

				if (item.hasOwnProperty('link')) {
					a = jQuery(document.createElement('a')).html(item.link.text)
						.attr('href', item.link.url)
						.addClass('text-nowrap');

					if (CDQ.i.hasOwnProperty('disclaimerLightbox') && !item.link.url.match(CDQ.i.disclaimerLightbox.pattern)) {
						a.append('<i class="icons-external-link"></i>');

						CDQ.i.disclaimerLightbox.bindDisclaimer(a);
					} else {
						a.append('<i class="icons-internal-link"></i>');
					}

					colr.append(a);
				}

				li = jQuery(document.createElement('li'))
					.addClass('ui-category-name clearfix ui-menu-item')
					.append(coll)
					.append(colr)
					.data("ui-autocomplete-item", item);

				ul.append(li);
			},

			_renderTotalItem: function(ul, item) {
				var coll,
					colr,
					total,
					a,
					li;

				coll = jQuery(document.createElement('div'))
					.addClass('col-1-2 hide-md');

				colr = jQuery(document.createElement('div'))
					.addClass('col-1-2 text-right pull-right-lg');

				total = jQuery(document.createElement('p'))
					.html(_this.totalResults);

				coll.append(total);

				a = jQuery(document.createElement('a'))
					.html(_this.link.text)
					.attr('href', _this.link.url)
					.append('<i class="icons-internal-link"></i>');

				colr.append(a);

				li = jQuery(document.createElement('li'))
					.addClass('ui-menu-item-results ui-menu-item clearfix')
					.append(coll)
					.append(colr)
					.data("ui-autocomplete-item", item);

				ul.append(li);
			}
		});


		this.searchInput.catcomplete({
			appendTo: this.settings.resultSelector,
			source: function(request, response) {
				jQuery.ajax({
					url: _this.settings.url,
					dataType: "json",
					data: jQuery.extend(source, {
						searchterm: request.term
					}),
					success: function(data) {
						_this.totalResults = data.totalResults;
						_this.link = data.link;
						response(data.categories);
						_this.settings.onHTMLReady(data);
					}
				});
			},
			minLength: 3,
			open: function(event) {
				stickers.addClass('hide');

				var resultsElement = jQuery(_this.settings.resultSelector),
					mainMenu = jQuery(_this.settings.mainMenu),
					uiAutocomplete = resultsElement.find('.ui-autocomplete');

				mainMenu.addClass('no-open');
				resultsElement.css('display', 'block');
				uiAutocomplete.css({
					position: 'absolute',
					left: 'auto',
					right: '-6px',
					top: '38px'
				});

				if (jQuery(window).width() <= CDQ.global.util.getBreakpoints("large")) {
					uiAutocomplete.css({
						width: uiAutocomplete.parent().prev().outerWidth()
					});

					jQuery('.form-search-wrapper').css({
						height: uiAutocomplete.height()
					});
				}

				setTimeout(function() {
					_this.highlightTerms(event.target.value);
				}, 0);
			},
			close: function() {
				jQuery(_this.settings.resultSelector).css('display', 'none');
				jQuery(_this.settings.mainMenu).removeClass('no-open');

				jQuery('.form-search-wrapper').css({
					height: 'auto'
				});
				stickers.removeClass('hide');
				jQuery(window).trigger('Search.contractInput');
			},
			focus: function(ev) {
				ev.preventDefault();
			}
		});
	},

	/**
	 * Search and highlight the search terms in all search results
	 *
	 * @method highlightTerms
	 * @param {String} term Search term
	 */
	highlightTerms: function(term) {
		var _this = this,
			results = jQuery(this.settings.resultSelector).find('.ui-menu-item:not(.ui-menu-item-results, .ui-category-name) .item-result');

		results.each(function() {
			var element = jQuery(this),
				text = jQuery(this).html();

			element.html(_this.highlight(term, text));
		});
	},

	/**
	 * Replace the current search result text by a new one with highlighted element
	 *
	 * @method highlight
	 * @param {String} q Search term
	 * @param {String} s Search result text
	 * @return {String} Highlighted text
	 */
	highlight: function(q, s) {
		var text = this.clean(s.toLowerCase()),
			term = this.clean(q.toLowerCase()),
			start = text.indexOf(term),
			end = start + q.length,
			t = start >= 0 ? s.substring(start, end) : null,
			highlighted = t ? '<span class="autocomplete-highlight">' + t + '</span>' : null;

		return s.replace(t, highlighted);
	},

	/**
	 * Remove accents from string
	 *
	 * @method clean
	 * @param {String} string String to replace
	 * @return {String} replaced string
	 */
	clean: function(string) {
		var rExps = [{
			re: /[\xC0-\xC6]/g,
			ch: "A"
		}, {
			re: /[\xE0-\xE6]/g,
			ch: "a"
		}, {
			re: /[\xC8-\xCB]/g,
			ch: "E"
		}, {
			re: /[\xE8-\xEB]/g,
			ch: "e"
		}, {
			re: /[\xCC-\xCF]/g,
			ch: "I"
		}, {
			re: /[\xEC-\xEF]/g,
			ch: "i"
		}, {
			re: /[\xD2-\xD6]/g,
			ch: "O"
		}, {
			re: /[\xF2-\xF6]/g,
			ch: "o"
		}, {
			re: /[\xD9-\xDC]/g,
			ch: "U"
		}, {
			re: /[\xF9-\xFC]/g,
			ch: "u"
		}, {
			re: /[\xC7-\xE7]/g,
			ch: "c"
		}, {
			re: /[\xD1]/g,
			ch: "N"
		}, {
			re: /[\xF1]/g,
			ch: "n"
		}];

		jQuery.each(rExps, function() {
			string = string.replace(this.re, this.ch);
		});

		return string;
	}
})
