/**
 * Build gateway infographic buttons
 *
 * @class GatewayButtons
 * @constructor
 * @namespace component
 * @extend core.CanvasStage
 */

CDQ.component.GatewayButtons = CDQ.core.CanvasStage.extend({
	/**
	 * GatewayButtons constructor
	 *
	 * @method init
	 * @param {Object} settings Settings object
	 * @param {Object} parent GatewayInfographic methods and properties
	 */
	init: function(settings, parent) {
		this.inherited().init(settings);

		this.parent = parent;
		this.topY = 220;
		this.padding = 10;
		this.x = 3;
		this.increaseValue = 48;
		this.buttonHeight = 181;
		this.maskHeight = this.buttonHeight + this.increaseValue;
		this.duration = 500;
		this.animationData = this.getAnimationData();
		this.set = {
			leftBtn: this.paper.set(),
			rightBtn: this.paper.set()
		};

		this.iconsSet = this.paper.set();

		this.contentActive = false;
		this.currentContent;
		this.touchCount = {};
		this.maskId = 0;

		this.drawButtons();
	},

	/**
	 * Draw buttons
	 *
	 * @method drawButtons
	 */
	drawButtons: function() {
		var _this = this,
			btn1,
			btn2,
			animation = Raphael.animation(this.animationData.leftBtn, this.duration);

		btn1 = this.drawRectangle(this.x, this.topY + this.animationData.leftBtn.height, 238, 0, {
			fill: this.settings.colors[this.settings.data.buttons.leftBtn.bgColor],
			stroke: 'none'
		}).data('ref', 'leftBtn');

		btn2 = this.drawRectangle(this.x + 243, this.topY + this.animationData.leftBtn.height, 404, 0, {
			fill: this.settings.colors[this.settings.data.buttons.rightBtn.bgColor],
			stroke: 'none'
		}).data('ref', 'rightBtn');

		btn1.animate(animation, this.duration);
		btn2.animateWith(btn1, animation, this.animationData.rightBtn, this.duration, function() {
			_this.populate([btn1, btn2]);
		});

		this.set.leftBtn.push(btn1);
		this.set.rightBtn.push(btn2);
	},

	/**
	 * Populate buttons and add events
	 *
	 * @method populate
	 * @param {Array} buttons Buttons collection
	 */
	populate: function(buttons) {
		var i,
			buttonsLength = buttons.length;

		for (i = 0; i < buttonsLength; i++) {
			this.drawTextInButton(buttons[i], (i === buttonsLength - 1));
			this.drawIcon(buttons[i]);
			this.addEvents(buttons[i]);
		}
	},

	/**
	 * Write text in a button
	 *
	 * @method drawTextInButton
	 * @param {RaphaelJSPath} button Button path
	 * @param {Boolean} last A boolean to inform this is the last button
	 */
	drawTextInButton: function(button, last) {
		var _this = this,
			content = this.settings.data.buttons[button.data('ref')],
			bbox = button.getBBox(),
			animParams = {
				opacity: 1
			},
			animation = Raphael.animation(animParams, this.duration, null, function() {
				if (last) {
					_this.done();
				}
			}),
			title,
			subtitle,
			headlines,
			i;

		title = this.drawText(content.btnTitle, bbox.x + (bbox.width / 2), bbox.y + (bbox.height / 2) - 5, {
			fill: this.settings.colors[content.textColor],
			font: '18px Arial',
			'text-anchor': 'middle',
			opacity: 0
		}).data('title', true);

		subtitle = this.drawText(content.btnSubtitle, bbox.x + (bbox.width / 2), (title.getBBox().y + title.getBBox().height), {
			fill: this.settings.colors[content.textColor],
			font: '13px Arial',
			'text-anchor': 'middle',
			opacity: 0
		}).data('subtitle', true);

		title.animate(animation);

		this.set[button.data('ref')].push(title);

		if (content.headline) {
			headlines = this.drawHeadline(button, content);

			for (i = 0; i < headlines.length; i++) {
				headlines[i].animateWith(title, animation, animParams, this.duration);
				this.set[button.data('ref')].push(headlines[i]);
			}
		}

	},

	/**
	 * Write the headline
	 *
	 * @method drawHeadline
	 * @param {RaphaelJSPath} button Button path
	 * @param {Object} content An object that contain some content properties
	 * @return {Array} An array containing text to write
	 */
	drawHeadline: function(button, content) {
		var text1,
			text2,
			text3,
			padding = this.padding * 2 + 5,
			topPadding = padding + 7,
			line,
			bbox = button.getBBox(),
			lineTop = bbox.y,
			lineData,
			h,
			topText,
			higherElementHeight;

		text1 = this.drawText(content.headline[0], bbox.x + padding, bbox.y + topPadding, {
			fill: this.settings.colors[content.textColor],
			font: '13px Arial',
			'text-anchor': 'start',
			opacity: 0
		});

		text2 = this.drawText(content.headline[1], bbox.x + padding + 135, bbox.y + topPadding, {
			fill: this.settings.colors[content.textColor],
			font: '13px Arial',
			'text-anchor': 'start',
			opacity: 0
		});

		text3 = this.drawText(content.headline[2], bbox.x + padding + 260, bbox.y + topPadding, {
			fill: this.settings.colors[content.textColor],
			font: '13px Arial',
			'text-anchor': 'start',
			opacity: 0
		});

		h = this.getHigher([text1, text2, text3]);
		topText = h > 40 ? 10 : topPadding;
		higherElementHeight = parseInt(h + lineTop + topText, 10) + 0.5;

		lineData = [
			[
				bbox.x + padding,
				higherElementHeight + 10,
				bbox.x + bbox.width - padding,
				higherElementHeight + 10
			]
		];

		line = this.drawLine(lineData, {
			stroke: this.settings.colors.text2,
			opacity: 0
		});

		return [text1, text2, text3, line[0]];
	},

	getHigher: function(textsArray) {
		var bbox,
			currentHeight = 0;

		for (var i = textsArray.length - 1; i >= 0; i--) {
			bbox = textsArray[i].getBBox();

			if (bbox.height > currentHeight) {
				currentHeight = bbox.height;
			}
		}

		return currentHeight;
	},

	/**
	 * Draw info icon
	 *
	 * @method drawIcon
	 * @param {RaphaelJSPath} button Button path
	 */
	drawIcon: function(button) {
		var height = 32,
			width = height,
			bbox = button.getBBox(),
			x = bbox.x + bbox.width - (width + this.padding),
			y = bbox.y + bbox.height - (height + this.padding),
			styles = {
				fill: this.settings.colors.text2,
				'stroke': 'none',
				opacity: 0
			},
			icon,
			iconBBox,
			iconMiddleX,
			iconMiddleY,
			text,
			textColor = this.settings.data.buttons[button.data('ref')].bgColor;

		icon = this.drawRectangle(x, y, width, height, styles).data('icon', true);

		iconBBox = icon.getBBox();
		iconMiddleX = iconBBox.x + (iconBBox.width / 2);
		iconMiddleY = iconBBox.y + (iconBBox.height / 2);

		text = this.drawText('i', iconMiddleX, iconMiddleY, {
			fill: this.settings.colors[textColor],
			'font-size': '18px',
			'font-weight': 'normal',
			'text-anchor': 'middle'
		}).data('iconText', true);

		this.set[button.data('ref')].push(icon, text);
		this.iconsSet.push(icon, text);
	},

	/**
	 * Add events to button
	 *
	 * @method addEvents
	 * @param {RaphaelJSPath} button Button path
	 */
	addEvents: function(button) {
		var _this = this,
			mask = button.clone();


		mask.attr({
			opacity: 0,
			cursor: 'pointer'
		}).toFront().data('cloneFrom', button.id);

		if (Modernizr.touch) {
			return this.iPadEvents(button, mask);
		}

		mask.mouseover(function() {
			if (!_this.contentActive) {
				_this.activateButton(this);
			}
		});

		mask.mouseout(function() {
			if (!_this.contentActive) {
				_this.deactivateButton(this);
			}
		});

		mask.click(function() {
			var identifier = button.data('ref');

			if (!_this.contentActive && !_this.animating) {
				_this.activeMask = mask;
				_this.contentActive = true;
				_this.parent.content.show(identifier);
				_this.iconsSet.attr({
					opacity: 0
				});
			}
		});
	},

	/**
	 * Events for ipad/touch devices
	 *
	 * @method iPadEvents
	 * @param {RaphaelJSPath} button Button path
	 * @param {RaphaelJSPath} mask Mask to bind elements
	 * @return {Array} An array containing text to write
	 */
	iPadEvents: function(button, mask) {
		var _this = this,
			identifier = button.data('ref');

		this.touchCount[identifier] = 0;

		mask.touchstart(function() {
			if (!_this.contentActive) {
				if (_this.maskId !== 0 && _this.maskId != mask.id) {
					_this.deactivateButton(_this.getMask(_this.maskId));

					_this.touchCount = {};
					_this.touchCount[identifier] = 0;
					_this.animating = false;
				}

				_this.touchCount[identifier] ++;

				if (_this.touchCount[identifier] === 1) {
					_this.activateButton(this);
				} else if (_this.touchCount[identifier] === 2 && !_this.animating) {
					_this.touchCount[identifier] = 0;
					_this.activeMask = mask;
					_this.contentActive = true;
					_this.parent.content.show(identifier);
					_this.iconsSet.attr({
						opacity: 0
					});
				}

				_this.maskId = mask.id;
			}
		});
	},

	/**
	 * Fired when mouse over the button
	 *
	 * @method activateButton
	 * @param {RaphaelJSPath} mask Button mask
	 */
	activateButton: function(mask) {
		this.animating = true;
		this.over(mask);

		mask.attr({
			height: this.maskHeight
		});

		this.activeMask = mask;
	},

	/**
	 * Fired when mouse out the button
	 *
	 * @method deactivateButton
	 * @param {RaphaelJSPath} mask Button mask
	 */
	deactivateButton: function(mask) {
		this.animating = true;
		mask = mask || this.activeMask;

		if (!this.contentActive) {
			this.out(mask);

			mask.attr({
				height: this.buttonHeight
			});
		}

	},

	/**
	 * Get the mask from id
	 *
	 * @method getMask
	 * @param {String} id Mask id
	 * @return {RaphaelJSPath} Mask path
	 */
	getMask: function(id) {
		return this.paper.getById(id);
	},

	/**
	 * Mouse over event
	 *
	 * @method over
	 * @param {RaphaelJSPath} element Button mask
	 */
	over: function(element) {
		var button,
			_this = this,
			animationParams = {
				height: this.buttonHeight + this.increaseValue
			},
			animation = Raphael.animation(animationParams, this.duration, function() {
				_this.animating = false;
			}),
			currentButton,
			bbox;


		button = this.paper.getById(element.data('cloneFrom'));
		button.animate(animation, this.duration);
		bbox = button.getBBox();
		currentButton = button.data('ref');

		this.applyOpacity(currentButton, 0.5);

		this.rolloverInnerElements(button, animation);
		this.moveAllBelow(button, animation);

		return button;
	},

	/**
	 * Mouse out event
	 *
	 * @method out
	 * @param {RaphaelJSPath} element Button mask
	 */
	out: function(element) {
		var button,
			animationParams = {
				height: this.buttonHeight
			},
			animation = Raphael.animation(animationParams, this.duration),
			currentButton,
			_this = this;


		button = this.paper.getById(element.data('cloneFrom'));
		button.animate(animation, this.duration, function() {
			_this.animating = false;
		});
		currentButton = button.data('ref');

		this.applyOpacity(currentButton, 1);

		this.rolloutInnerElements(button, animation);
		this.moveAllBelow(button, animation, true);

		return button;
	},

	/**
	 * Change icon state
	 *
	 * @method toggleIcon
	 * @param {Object} object Icon contents object
	 * @param {RaphaelJSPath} button Button path
	 * @param {RaphaelJSAnimation} animation Raphael animation
	 * @param {Boolean} reset A boolean that indicates if it must reset or not
	 */
	toggleIcon: function(object, button, animation, reset) {
		var bbox = button.getBBox(),
			originalWidth = 32,
			expandedWidth,
			iconWidth = reset ? originalWidth : 1,
			transformString = reset ? 't0,0' : 't0,' + this.increaseValue,
			opacityVal = reset ? 1 : 0.6,
			middleX,
			textLabel = reset ? 'i' : this.settings.data.details,
			textFont = reset ? '18px' : '13px',
			textWeight = reset ? 'normal' : 'bold',
			textSize = this.textBugFix(textLabel, textFont, textWeight);
			
		object.icon.attr({
			width: iconWidth,
			x: bbox.x + bbox.width - (iconWidth + this.padding)
		});

		object.text.attr({
			text: textLabel,
			'font-size': textFont,
			'font-weight': textWeight
		});

		expandedWidth = textSize + 16;
		iconWidth = reset ? originalWidth : expandedWidth;
		middleX = iconWidth / 2;

		object.icon.attr({
			width: iconWidth,
			x: bbox.x + bbox.width - (iconWidth + this.padding)
		});

		object.text.attr({
			x: bbox.x + bbox.width - (middleX + this.padding)
		});

		object.icon.animateWith(button, animation, {
			transform: transformString,
			opacity: opacityVal
		}, this.duration);

		object.text.animateWith(button, animation, {
			transform: transformString
		}, this.duration);
	},

	/**
	 * Align titles
	 *
	 * @method moveTitles
	 * @param {Array} titles Titles elements
	 * @param {Boolean} isLeftBtn Indicates if it is left aligned or not
	 */
	moveTitles: function(titles, isLeftBtn) {
		var _this = this,
			animSubtitle = {
				opacity: 1
			},
			transformTitle = isLeftBtn ? 't0,-24' : 't0,0',
			magicNumber = isLeftBtn ? 44 : 29;

		titles[0].stop();
		titles[1].stop();

		titles[0].animate({
			transform: transformTitle
		}, this.duration, function() {
			var titleBBox = titles[0].getBBox();

			titles[1].attr({
				y: (titleBBox.y + titleBBox.height) + magicNumber + 15
			}).animate(animSubtitle, _this.duration);
		});

	},

	/**
	 * Reset titles alignment
	 *
	 * @method resetTitles
	 * @param {Array} titles Titles elements
	 */
	resetTitles: function(titles) {
		var _this = this,
			animSubtitle = {
				opacity: 0
			},
			transformTitle = 't0,0';

		titles[0].stop();
		titles[1].stop();


		titles[1].animate(animSubtitle, 0, function() {
			titles[0].animate({
				transform: transformTitle
			}, _this.duration);
		});


	},

	/**
	 * Get all elements bellow the current element
	 *
	 * @method getAllBelow
	 * @param {RaphaelJSPath} element The reference element
	 * @return {Array} All elements bellow the referenced element
	 */
	getAllBelow: function(element) {
		var bbox = element.getBBox(),
			minY = bbox.y + bbox.height,
			allBelow = [];

		this.paper.forEach(function(el) {
			if (el.getBBox().y > minY) {
				allBelow.push(el);
			}
		});

		return allBelow;
	},

	/**
	 * Get all elements inside the current element
	 *
	 * @method getAllInside
	 * @param {RaphaelJSPath} element The reference element
	 * @return {Array}
	 */
	getAllInside: function(element) {
		var bbox = element.getBBox(),
			minX = bbox.x,
			minY = bbox.y,
			maxX = minX + bbox.width,
			maxY = minY + bbox.height,
			allInside = [];

		this.paper.forEach(function(el) {
			var x = el.getBBox().x,
				y = el.getBBox().y;

			if (x > minX && x < maxX && y > minY && y < maxY) {
				allInside.push(el);
			}
		});

		return allInside;
	},

	/**
	 * Mount an object containing animation data
	 *
	 * @method getAnimationData
	 * @return {Object} Animation data for left and right buttons of infographic
	 */
	getAnimationData: function() {
		var obj = {
			leftBtn: {
				height: this.buttonHeight,
				y: this.topY
			},
			rightBtn: {
				height: this.buttonHeight,
				y: this.topY
			}
		};

		return obj;
	},

	/**
	 * Apply animation to all internal elements when mouse over
	 *
	 * @method rolloverInnerElements
	 * @param {RaphaelJSPath} button The button hovered
	 * @param {RaphaelJSAnimation} animation The animation to apply
	 */
	rolloverInnerElements: function(button, animation) {
		var i,
			isLeftBtn = button.data('ref') == 'leftBtn',
			allInside = this.getAllInside(button),
			allInsideLength = allInside.length,
			bbox,
			currentInside,
			iconElements = {},
			titles = [];

		bbox = button.getBBox();

		for (i = 0; i < allInsideLength; i++) {
			currentInside = allInside[i];

			if (currentInside.data('title') || currentInside.data('subtitle')) {
				titles.push(currentInside);
			}

			if (currentInside.data('icon')) {
				iconElements.icon = currentInside;
			} else if (currentInside.data('iconText')) {
				iconElements.text = currentInside;
			}

			if (i == (allInsideLength - 1)) {
				this.toggleIcon(iconElements, button, animation);

				this.moveTitles(titles, isLeftBtn);
			}
		}
	},

	/**
	 * Apply animation to all internal elements when mouse out
	 *
	 * @method rolloutInnerElements
	 * @param {RaphaelJSPath} button The button hovered
	 * @param {RaphaelJSAnimation} animation The animation to apply
	 */
	rolloutInnerElements: function(button, animation) {
		var i,
			isLeftBtn = button.data('ref') == 'leftBtn',
			allInside = this.getAllInside(button),
			allInsideLength = allInside.length,
			currentInside,
			iconElements = {},
			titles = [];


		for (i = 0; i < allInsideLength; i++) {
			currentInside = allInside[i];

			if (currentInside.data('title') || currentInside.data('subtitle')) {
				titles.push(currentInside);
			}

			if (currentInside.data('icon')) {
				iconElements.icon = currentInside;
			} else if (currentInside.data('iconText')) {
				iconElements.text = currentInside;
			}

			if (i == (allInsideLength - 1)) {
				this.toggleIcon(iconElements, button, animation, true);
				this.resetTitles(titles, isLeftBtn);
			}
		}
	},

	/**
	 * Apply opacity to a set of elements
	 *
	 * @method applyOpacity
	 * @param {RaphaelJSSet} exception  The set that won't receive opacity value
	 * @param {Number} opacityVal The value of percentage
	 */
	applyOpacity: function(exception, opacityVal) {
		var set,
			colorName;

		for (set in this.set) {
			if (this.set.hasOwnProperty(set)) {
				if (set != exception) {
					colorName = this.settings.data.buttons[set].bgColor;

					this.set[set].attr({
						opacity: opacityVal
					});

					CDQ.global.infograficColorsSet[colorName].attr({
						opacity: opacityVal
					});
				}
			}
		}
	},

	/**
	 * Move all elements that are below the referenced element
	 *
	 * @method moveAllBelow
	 * @param {RaphaelJSPath} button The reference button
	 * @param {RaphaelJSAnimation} animation The animation to apply
	 * @param {Boolean} reset To reset to initial state it must be true
	 */
	moveAllBelow: function(button, animation, reset) {
		var transform = reset ? 't0,0' : 't0,' + this.increaseValue;

		this.parent.title.set.bottom.animateWith(button, animation, {
			transform: transform
		}, this.duration);
	},

	/**
	 * Blink icons 3 times when the animation has finished
	 *
	 * @method animateIcons
	 */
	animateIcons: function() {
		var _this = this,
			duration = this.duration / 2;

		/* GMB use repeat instead */
		this.iconsSet.animate({
			opacity: 1
		}, duration, function() {
			_this.iconsSet.animate({
				opacity: 0
			}, duration, function() {
				_this.iconsSet.animate({
					opacity: 1
				}, duration, function() {
					_this.iconsSet.animate({
						opacity: 0
					}, duration, function() {
						_this.iconsSet.animate({
							opacity: 1
						}, duration, function() {
							CDQ.global.screenLockEl.hide();
						});
					});
				});
			});
		});
	},

	done: function() {
		this.inherited().done();

		var _this = this;

		jQuery(window).on('Infographic_closeContent', function() {
			_this.contentActive = false;
			_this.currentContent = null;
			_this.deactivateButton();
			_this.iconsSet.attr({
				opacity: 1
			});
		});
	},

	/**
	 * Bugfix for text render issue
	 *
	 * @method textBugFix
	 * @param {String} text Text to draw
	 * @param {String} size Font size
	 * @param {String} weight Font weight
	 */
	textBugFix: function(text, size, weight) {
		return this.drawText(text, 100, 100, {
			fill: '#FFFFFF',
			opacity: 0,
			'font-size': size,
			'font-weight': weight,
			'text-anchor': 'middle'
		}).getBBox().width;
	}
});
