/**
 * Disclaimer lightbox - It must show a popup message for all non defined domains in whitelist.
 *
 *	// Settings object isn't required. The example bellow shows the default values
 *	new CDQ.component.DisclaimerLightbox({
 *		whitelist: [],
 *		buttonText: '#{error: missing translation}',
 *		wrapper: document
 *	});
 *
 * @class DisclaimerLightbox
 * @constructor
 * @namespace component
 */
CDQ.component.LinkHandler = Class.extend({

	/**
	 * Lightbox constructor
	 *
	 * @method init
	 * @param {Object} [settings] receive an object that contains settings to be used in the method.
	 */
	init: function(settings) {
		/**
		* Settings property
		*
		* @property settings
		* @type Object
		* @default {
			whitelist: [],
			wrapper: document
		}
		*/
		this.settings = this.getSettings(settings);
		this.host = window.location.host;
		this.whitelist = this.getWhiteList();
		this.pattern = new RegExp(this.whitelist.join('|'), 'gi'),

		this.bindLinks(this.settings.wrapper);
		this.bind();
	},

	/**
	 * Extend default settings and settings from constructor
	 *
	 * @method getSettings
	 * @param {Object} [settings] receive an object that contains settings to replace default ones.
	 * @return {Object} Extended Settings
	 */
	getSettings: function(settings) {
		var defaultSettings = {
			whitelist: [],
			wrapper: document,
			target: '#disclaimer-whitelist'
		};

		return jQuery.extend(defaultSettings, settings);
	},

	/**
	 * Bind all links with domains not in the white list
	 *
	 * @method bind
	 */
	bind: function() {
		var _this = this;

		this.setupCountryChosen();

		jQuery(window).on('LinkHandler.bind', function(wrapper) {
			_this.bindLinks(wrapper);
		});
	},

	bindLinks: function(wrapper) {
		var _this = this,
			links = jQuery(wrapper).find('a');

		links.each(function() {
			_this.bindDisclaimer(this);
		});
	},

	bindDisclaimer: function(element) {
		var _this = this,
			a = jQuery(element),
			href = a.attr('href');

		if (href && !href.match(this.pattern)) {
			a.on('click', function() {
				_this.openDisclaimer(this.href, this.target);

				return false;
			});
		}
	},

	/**
	 * Open disclaimer to show the leaving message
	 *
	 * @method openDisclaimer
	 * @param  {String} href The URL to redirect when success button is pressed
	 * @param  {String} windowTarget The url target when success button is pressed
	 */
	openDisclaimer: function(href, windowTarget) {
		CDQ.i.popbox.open(this.settings.target, href, windowTarget);
	},

	/**
	 * Get the white list of domains and common kinds of links are used
	 *
	 * @method getWhiteList
	 * @return {Array} An array containing all regular expressions that we might include in the white list
	 */
	getWhiteList: function() {
		var arr = [window.location.hostname, '^javascript', '^\\/', '^#', '^tel:', '^mailto:'];
		for (var i = this.settings.whitelist.length - 1; i >= 0; i--) {
			arr.push('https?\\:\\/\\/(.*\\.)?' + this.escape(this.settings.whitelist[i]));
		}

		return arr;
	},

	/**
	 * Escape an string to be used as regular expression
	 *
	 * @method escape
	 * @param  {String} str The string to be escaped
	 * @return {String} The escape string
	 */
	escape: function(str) {
		return str.replace(/[\-\[\]\/\{\}\(\)\*\+\?\.\\\^\$\|]/g, "\\$&");
	},

	/**
	 * It will add events to open other websites on dropdown change. It also checks if the url is valid
	 * and if the domain is in whitelist, to show the leaving disclaimer.
	 * @return {[type]} [description]
	 */
	setupCountryChosen: function() {
		var _this = this,
			countryChoosen = jQuery('.chosen-select[name="countries"]'),
			target = !CDQ.global.util.isIOS() ? '_blank' : '_self';

		countryChoosen.on('change', function() {
			var href = this.value;

			if (CDQ.global.util.isValidUrl(this.value)) {

				if (href && href.match(_this.pattern)) {
					return window.open(href, target);
				}

				return _this.openDisclaimer(href);
			}
		});
	}
});
