/**
 * Responsive slider
 *
 * 	// Settings object isn't required. The example bellow shows the default values
 * 	new CDQ.component.Slider({
 * 		container: jQuery('.is-slider'),
 *		navigationText: [
 *			'<span class="icons-slider-prev"></span>',
 *			'<span class="icons-slider-next"></span>'
 *		]
 * 	});
 *
 * @class Slider
 * @constructor
 * @namespace component
 */
CDQ.component.Slider = Class.extend({
	/**
	 * Slider constructor
	 *
	 * @method init
	 * @param {Object} settings Settings to create slider
	 */
	init: function(settings) {
		this.settings = this.getSettings(settings);
		this.sliderWrapper = this.settings.container;
		this.each();


		if (CDQ.global.releaseData.project != "deawm") {
			if (this.sliderWrapper.parent().hasClass('col-xl-12')) {
				this.sliderWrapper.parent().css('overflow', 'hidden')
			}
		}
	},

	each: function() {
		var _this = this;

		this.settings.container.each(function() {
			var el = this;

			_this.cleanUp(jQuery(el), function() {
				_this.build(_this.setup(jQuery(el)));
			});
		})
	},


	build: function(settings) {
		var _this = this;

		if (!settings.itemsCustom) {
			settings.itemsCustom = this.getBreakpoints(settings.items);
		}

		settings.afterInit = function(slider) {
			_this.alignTeasers(slider);
		};

		settings.afterUpdate = function(slider) {
			_this.checkHeight(slider);
		};

		settings.beforeMove = function() {
			_this.pauseVideos(this.$elem);
		};

		settings.container.owlCarousel(settings);
	},

	alignTeasers: function(slider) {
		var _this = this,
			controlWrapper = jQuery(document.createElement('div')),
			controls = slider.find('.owl-controls');

		controlWrapper.addClass('control-wrapper').append(controls);
		slider.append(controlWrapper);

		imagesLoaded(jQuery(slider), function() {
			_this.setHeight(slider);
		});

		if (slider.next().hasClass('product-link')) {
			controlWrapper.css('min-height', '40px');
		}
	},

	setHeight: function(slider) {
		var sliderHeight = slider.find('.owl-wrapper').outerHeight(),
			teaserItems = slider.find('.m-block');

		sliderHeight = slider.find('.owl-wrapper').outerHeight();
		teaserItems.css('height', sliderHeight);
	},

	checkHeight: function(slider) {
		var teaserItems = slider.find('.m-block');

		teaserItems.css('height', 'auto');

		this.setHeight(slider);
	},

	cleanUp: function(wrapper, callback) {
		var prefix = 'col-',
			sliderEls = wrapper.find('div, a'),
			cb = callback || function() {};

		sliderEls.each(function(index, value) {
			var classes = jQuery.map(value.className.split(' '), function(c) {
				if (c.indexOf(prefix)) {
					return c;
				}
			});

			value.className = jQuery.trim(classes.join(" "));
		});

		cb();
	},

	/**
	 * Setup extends the settings to use the options set by user and get the default ones when it isn't set
	 *
	 * @method getSettings
	 */
	getSettings: function(settings) {
		/**
		 * Default settings supported in this class
		 * @property settings
		 * @type {Object}
		 */
		return jQuery.extend({
			container: jQuery('.is-slider'),
			navigationText: [
				'<span class="icons-slider-prev"></span>',
				'<span class="icons-slider-next"></span>'
			]
		}, settings);

	},

	setup: function(el) {
		var attributeOptions = {
			container: el,
			items: el.data('column'),
			navigation: el.data('navigation'),
			autoPlay: el.data('autoplay'),
			itemsCustom: el.data('custom')
		};


		return jQuery.extend({
			scrollPerPage: false,
			slideSpeed: 600,
			mouseDrag: false,
			items: 1,
			navigation: true,
			autoPlay: false,
			navigationText: this.settings.navigationText
		}, attributeOptions);
	},

	getBreakpoints: function(items) {
		switch (items) {
			case 1:
				return [
					[0, 1]
				];
			case 2:
				return [
					[768, 2],
					[600, 2],
					[0, 1]
				];
			case 3:
				return [
					[769, 3],
					[480, 2],
					[0, 1]
				];
			case 4:
				return [
					[1024, 4],
					[768, 3],
					[480, 2],
					[0, 1]
				];

			default:
				return [
					[1024, 1]
				];
		}
	},

	/**
	 * Check if there are videos inside the current slider element and pause them before change
	 *
	 * @method pauseVideos
	 * @param {HTMLElement} element The current slider wrapper
	 */
	pauseVideos: function(element) {
		jQuery(element).find('.video-js').each(function() {
			var video = CDQ.i.brightcovePlayers.getById(this.id);
			if (video && !video.paused()) {
				video.pause();
			}
		});
	},

	destroy: function() {
		this.sliderEl.data('owlCarousel').destroy();

		this.sliderEl.find('.control-wrapper').remove();
	}
});
