/**
 * Main Class
 *
 *	new CDQ.core.Main();
 *
 * @class Main
 * @constructor
 * @namespace core
 */
CDQ.core.Main = Class.extend({
	init: function() {
		this.setup();
	},

	setup: function() {
		if (jQuery.browser.msie && parseInt(jQuery.browser.version, 10) == 10) {
			jQuery('html').addClass('lt-ie11');
		}

		new CDQ.core.PreloadFont(['UniversDeutscheBank']);

		new CDQ.component.MainMenu();
		new CDQ.component.Stickers();

		CDQ.i.popbox = new CDQ.component.Popbox();

		new CDQ.component.Chosen({
			selector: '.chosen-select'
		});

		CDQ.global.responsiveTables = new CDQ.component.ResponsiveTables();

		if (CDQ.global.util.isTouch()) {
			window.touchInputNav();
		}

		new CDQ.component.CollapsibleTables();

		CDQ.i.brightcovePlayers = new CDQ.core.BrightcovePlayers();

		if (CDQ.global.releaseData.project == 'passive') {
			this.setupSliders();
			new CDQ.component.Scrollable();
		}

		if (CDQ.global.releaseData.project == 'deawm') {
			new CDQ.component.ImageToggler();
			new CDQ.component.Slider();
			new CDQ.component.Scrollable();
		}

		this.bind();
	},

	bind: function() {
		jQuery(window).on('Tabs.Switch', function(ev, element) {
			element.find('.dynamic-player').each(function() {
				CDQ.i.brightcovePlayers.createPlayer(this);
			});

			jQuery(window).trigger('LinkHandler.bind', [element]);
		});

		jQuery('.social-bar__btn').on(CDQ.global.activateAction, function() {
			jQuery(this).parent('.social-bar__wrapper').toggleClass('active');
		});
		jQuery('.lnk-language > a').on('click', function() {
			jQuery(this).parent('.lnk-language').toggleClass('active');
		/*jQuery('.social-bar__btn').on('click', function() {
			jQuery(this).parent('.social-bar__wrapper').toggleClass('active');*/
		});


		//TEMPORARY
		jQuery('.flash-message__close').on('click', function() {
			var el = jQuery(this);
			el.closest('.flash-message__wrapper').hide();
		});

		//TEMPORARY
		jQuery('.unsubscribe-cancel').on('click', function() {
			var el = jQuery(this);
			el.closest('.unsubscribe-form__wrapper').hide();
		});

		//TEMPORARY
		jQuery('.article-navigation-mobile').find('.active').on('click', function() {
			var el = jQuery(this);
			el.parents('.article-navigation-mobile').toggleClass('active');
		})
	},

	setupSliders: function() {
		new CDQ.component.Slider({
			theSlider: jQuery('.el-3'),
			itemsCustom: [
				[0, 1],
				[480, 1],
				[600, 2],
				[769, 3]
			]
		});

		new CDQ.component.Slider({
			theSlider: jQuery('.el-2'),
			itemsCustom: [
				[0, 1],
				[768, 2]
			]
		});

		new CDQ.component.Slider({
			theSlider: jQuery('.el-1'),
			itemsCustom: [
				[0, 1]
			]
		});
	}
});

CDQ.global.util = new CDQ.core.Util();
/*CDQ.global.activateAction = CDQ.global.util.isTouch() ? 'touchstart' : 'click';*/

CDQ.global.activateAction = '';
if(CDQ.global.util.isTouch()){
	CDQ.global.activateAction += 'touchstart';
}
if(CDQ.global.util.hasMouse()){
	CDQ.global.activateAction += ' click';
}

(function() {
	new CDQ.core.Main();
})();
