/**
 * Scrollable tabs with click event and with sticky Active
 * When the active element is outside the container
 *
 *
 * @class StickyActive
 * @constructor
 * @namespace component
 */
CDQ.component.StickyActive = Class.extend({
	/**
	 * StickyActive constructor
	 *
	 * @method init
	 */
	init: function(settings) {
		this.hasTransform = jQuery('html').hasClass('csstransforms');
		this.nl = 0;
		this.settings = this.getSettings(settings);
		this.timmer = '';

		var _this = this;

		/**
		 * All scrollable elements available
		 *
		 * @property elements
		 * @type jQueryObject
		 */
		this.elements = jQuery(this.settings.element);

		this.elements.each(function () {
			_this.setup(jQuery(this));
		});

		this.index = 0;
	},

	/**
	 * Setup the element and show buttons only when content width is bigger than the current row
	 *
	 * @method setup
	 * @param {jQueryObject} element The current scrollable element
	 */
	setup: function(element) {
		var _this = this;

		_this.index = 0;

		_this.wrapperWidth = jQuery(element.find(_this.settings.wrapper)).width();

		_this.setSizes(element);
		_this.bind(element);
		_this.generateSticky(element);
		_this.calcSizes(element);

		_this.showStickyElement(element);
		element.find(_this.settings.prevBtn).addClass(_this.settings.hideBtnClass);
	},

	/**
	 * Set size of current scrollable element wrapper
	 *
	 * @method setSizes
	 * @param {jQueryObject} element The current scrollable element
	 */
	setSizes: function(element) {
		var _this = this,
			wrapper = element.find(this.settings.wrapper),
			width = 2,
			scrollable = element.find(_this.settings.scrollable),
			actualWidth = wrapper.width();

		scrollable.children().each(function() {
			width += jQuery(this).outerWidth(true);
		});

		scrollable.width(width);

		if(_this.wrapperWidth != actualWidth){
			_this.wrapperWidth = jQuery(element.find(_this.settings.wrapper)).width();
			_this.resetPosition();
			_this.setButtons(0);
		}

		if ((wrapper.width()) >= width) {
			_this.hideButtons(element);
			return false;
		}
	},

	calcSizes: function(element) {
		var _this = this;
		var scrollable = element.find(this.settings.scrollable);
		this.sizes = [];

		scrollable.find('li').each(function() {
			var item = jQuery(this),
				index = item.index(),
				width = item.width();

			_this.sizes[index] = width;
		});
	},

	/**
	 * Bind buttons and add swipe event
	 *
	 * @method bind
	 * @param {jQueryObject} element The current scrollable element
	 */
	bind: function(element) {
		var _this = this,
			prevBtn = element.find(this.settings.prevBtn),
			nextBtn = element.find(this.settings.nextBtn);


		CDQ.global.util.tappable(prevBtn, function() {
			if (_this.animating) {
				return false;
			}

			_this.animating = true;
			_this.moveToPrev(_this.elements);
		});

		CDQ.global.util.tappable(nextBtn, function() {
			if (_this.animating) {
				return false;
			}

			_this.animating = true;
			_this.moveToNext(_this.elements);
		});

		if (CDQ.global.util.isTouch()) {
			element.find(this.settings.scrollable).swipe({
				swipe: function(event, direction) {
					switch (direction) {
						case 'left':
							_this.moveToNext(_this.elements);
							break;
						case 'right':
							_this.moveToPrev(_this.elements);
							break;
						default:
					}
				},
				threshold: 10,
				excludedElements: ''
			});
		}

		jQuery(window).on('resize orientationchange', function() {
			_this.setSizes(element);
		});

		jQuery(window).on('scroll touchmove', function () {
			_this.elements.each(function () {
				_this.showStickyElement(jQuery(this));
			});
		});

		this.elements.on('webkitTransitionEnd otransitionend oTransitionEnd msTransitionEnd transitionend', function() {
			_this.animating = false;
		});
	},

	/**
	 * Generate div with the sticky-active class
	 *
	 * @method generateSticky
	 */
	generateSticky: function(element) {
		var _this = this,
			tabsWrapper = element.find(_this.settings.wrapper),
			activeEl = tabsWrapper.find('.active');

		tabsWrapper.append("<div class='sticky-active'>"+ activeEl.html() +"</div>");
	},

	/**
	 * Get default settings
	 *
	 * @method getSettings
	 * @return {Object} Default Settings
	 */
	getSettings: function(settings) {
		var defaultSettings = {
			element: '.scrollable-sticky-tabs',
			wrapper: '.tabs-wrapper',
			scrollable: 'ul',
			prevBtn: '.scroll-link--prev',
			nextBtn: '.scroll-link--next',
			hideBtnClass: 'scroll-link--hidden',
			withArrowClass: 'scrollable-tabs--with-arrows',
			move: 50,
			index: 0
		};

		return jQuery.extend(defaultSettings, settings);
	},


	/**
	 * Verify if the item is visible in the tabs container
	 *
	 * @method isOutsideContainer
	 * @param  {jQueryObject} item The element to verify
	 */
	isOutsideContainer: function (item) {
		var container = item.parents('.tabs-wrapper'),
		 	itemWidth = item.width(),
		 	itemLeft = item.offset().left,
			offsetLeft = container.offset().left,
			offsetRight = container.offset().left + container.width() - itemWidth,
			containerLeft = offsetLeft,
			containerRight = offsetRight;

		if (itemLeft < containerLeft) {
			return "left";
		} else if (itemLeft > containerRight) {
			return "right";
		}
		return false;
	},

	/**
	 * Show the active item sticky when it's outside the container
	 *
	 * @method showStickyElement
	 */
	showStickyElement: function () {
		var _this = this;

		clearTimeout(this.timmer);

		function apply() {
			this.elements.each(function () {
				var element = jQuery(this);
				var activeEl = element.find('.active'),
					stickyActive = element.find('.sticky-active');

				stickyActive.removeClass('left right');

				var outside = _this.isOutsideContainer(activeEl);

				if(outside) {
					stickyActive.show();
					stickyActive.addClass(outside);
				} else {
					stickyActive.hide();
					stickyActive.addClass(outside);
				}
			});
		}

		this.timmer = setTimeout(apply.bind(this), 500);
	},

	moveFrom: function (element, fromIndex, toIndex) {
		var distance = '',
			direction = '',
			ARROW_SIZE = 33;

		if(toIndex === 0) {
			direction = 'left';
			distance = 0;
		}
		else if(fromIndex !== toIndex) {
			direction = 'right';
			distance = this.getLeft(toIndex) + ARROW_SIZE;
		}

		this.index = toIndex;

		var limit = element.find(this.settings.scrollable).width() - element.find(this.settings.wrapper).width();

		if (distance < limit*-1) {
			distance = limit*-1;
		}

		this.setButtons(distance, limit);

		this.translate(element.find(this.settings.scrollable), distance);
	},

	getLeft: function (index) {
		var left = 0;
		for (var i = 0; i < index; i++) {
			left += this.sizes[i];
		}

		return left * -1;
	},

	moveToPrev: function(element) {
		var newIndex = this.index - 1;

		if (newIndex < 0) {
			return;
		}

		this.moveFrom(element, this.index, newIndex);
	},

	moveToNext: function(element) {
		var newIndex = this.index + 1;

		if (newIndex > this.sizes.length) {
			return;
		}

		this.moveFrom(element, this.index, newIndex);
	},

	/**
	 * Setup buttons opacity based on scrolling element position
	 *
	 * @method setButtons
	 * @param {Integer} position The scrolling element position
	 * @param {Integer} limit Limit of last element visibility
	 */
	setButtons: function(position, limit) {
		var _this = this;

		this.elements.each(function () {
			var element = jQuery(this),
				prevBtn = element.find(_this.settings.prevBtn),
				nextBtn = element.find(_this.settings.nextBtn);

			element.addClass(_this.settings.withArrowClass);

			if (position === 0) {
				prevBtn.addClass(_this.settings.hideBtnClass);
				nextBtn.removeClass(_this.settings.hideBtnClass);
			} else if (position <= limit * -1) {
				prevBtn.removeClass(_this.settings.hideBtnClass);
				nextBtn.addClass(_this.settings.hideBtnClass);
			} else {
				nextBtn.removeClass(_this.settings.hideBtnClass);
				prevBtn.removeClass(_this.settings.hideBtnClass);
			}

			_this.showStickyElement(element);
		})
	},

	/**
	 * Hide buttons
	 *
	 * @method hideButtons
	 * @param {jQueryObject} element The current scrollable element
	 */
	hideButtons: function(element) {
		element.removeClass(this.settings.withArrowClass);
		element.find(this.settings.prevBtn).addClass(this.settings.hideBtnClass);
		element.find(this.settings.nextBtn).addClass(this.settings.hideBtnClass);
	},

	/**
	 * Reset current scrollable element to it's initial position
	 *
	 * @method resetPosition
	 * @param {jQueryObject} element The current scrollable element
	 */
	resetPosition: function() {
		var scrollable = this.elements.find(this.settings.scrollable);

		this.nl = 0;

		this.calcSizes(this.elements);

		if (!this.hasTransform) {
			return scrollable.css('left', 0);
		}

		return this.translate(scrollable, 0);
	},

	/**
	 * Apply CSS3 translate rules to scrollable elements
	 *
	 * @method translate
	 * @param  {jQueryObject} element The element to apply the css property
	 * @param  {Integer} x The x position
	 */
	translate: function (element, x) {
		var _this = this;
		if(!this.hasTransform || (jQuery.browser.msie && parseInt(jQuery.browser.version, 10) < 10)) {
			return element.animate({left: x}, function(){_this.animating = false;});
		}

		// translateZ(0) will avoid enable GPU aceleration on mobile/safari
		element.css({
			'-webkit-transform': 'translate(' + x + 'px, 0) translateZ(0)',
			'-moz-transform': 'translate(' + x + 'px, 0) translateZ(0)',
			'-ms-transform': 'translate(' + x + 'px, 0)',
			'transform': 'translate(' + x + 'px, 0) translateZ(0)'
		});
	}
});
