
/**
 * Rate class
 *
 *  the objective of this class is instanciate a Rate element and comunicate changes
 *  between other objects. It is composed by other two classes `CDQ.component.RateOverview` and
 *  `CDQ.component.RateVotable`
 *
 * @example
 *	new CDQ.component.Rate();
 *
 *	<div>
 *		<div data-article-rate>
 *			<div data-rate-overview>
 *				<p data-rate-title="">CIO article rating</p>
 *				<div data-rate="stars"></div>
 *				<div><span itemprop="ratingValue" data-rate="rating">5</span> / <span itemprop="bestRating">5</span></div>
 *				<div>Votes <span data-rate="votes" itemprop="ratingCount">10</span></div>
 *			</div>
 *
 *			<div data-rate-vote>
 *				<p data-rate-title="">How do you like this article?</p>
 *				<div data-rate="stars"></div>
 *				<div data-rate="hint"></div>
 *				<div data-rate="message"></div>
 *			</div>
 *		</div>
 *	</div>
 *
 * @class Rate
 * @constructor
 * @namespace component
 */
CDQ.component.Rate = Class.extend({

	/**
	 * init
	 * @param  {Object} settings
	 * @param {Boolean} [options.alreadyVoted=false] the vote of user, example: 2.5
	 */
	init: function (settings) {
		this.settings = this.setup(settings);

		this.$selector = jQuery(this.settings.selector);
		this.$RateOverview = this.$selector.find('[data-rate-overview]');
		this.$RateVotable = this.$selector.find('[data-rate-vote]');

		this.settings.votes = Number(this.$RateOverview.find('[data-rate="votes"]').html());
		this.settings.score = Number(this.$RateOverview.find('[data-rate="score"]').html());

		this.RateOverview = new CDQ.component.RateOverview({selector: this.$RateOverview, score: this.settings.score, votes: this.settings.votes});
		this.RateOverview.run();

		this.RateVotable = new CDQ.component.RateVotable(jQuery.extend(this.settings, { selector: this.$RateVotable, parent: this.$selector}));

		this.RateVotable.run();

		this.RateAnchor = new CDQ.component.RateAnchor({selector: '[data-rate-anchor]', scrollToElement: jQuery('[data-content]')});
		this.RateAnchor.run();

		this.mainContent = jQuery(this.settings.stickyContext);
		this.container = jQuery(this.settings.container);
		this.container.css({top: this.otherSticky});

		this.setupProperties();

		this.bind();
	},

	setup: function(settings) {
		this.defaultOptions = {
			selector: '[data-article-rate]',
			id: 0,
			vote: 1,
			alreadyVoted: false,
			label: {
				tooltip: ['Upgradable', 'Readable', 'Interesting', 'Recommendable', 'Must read!'],
				tapAgainToSave: 'Tap again to save your vote',
				titleAfterVote: 'Thank you for your opinion!'
			},
			messageDuration: 5000,
			url: 'http://localhost:8082/article-rate',
			stickyContext: '[progress-bar="context"]',
			container: '.article-aside__container'
		};

		return jQuery.extend(this.defaultOptions, settings);
	},

	/**
	 * watch for changes on the Rate element, then dispatch for other objects
	 */
	bind: function () {
		var self = this;

		this.$selector.on('rate.voted', function(event, data) {
			self.RateOverview.updateTotalOfVotes(data);
		});

		jQuery(window).resize(this.applySticky.bind(this));
		this.applySticky();
	},

	applySticky: function() {
		clearTimeout(this.stickyTimmer);

		function apply() {
			var viewport = CDQ.global.util.getViewportSize().width,
			mediaLarge = CDQ.global.util.getBreakpoints('large');

			this.setupProperties();

			if (viewport > mediaLarge) {
				jQuery(window).scroll(this.applySticky.bind(this))
				return this.sticky();
			}

			jQuery(window).unbind('scroll', this.applySticky);

			this.resetSticky();
		}

		this.stickyTimmer = setTimeout(apply.bind(this), 25);
	},

	setupProperties: function() {
		this.stickyTimmer = '';
		this.otherSticky = jQuery('.sticky-header-wrapper').outerHeight();
		this.mainContentHeight = this.mainContent.outerHeight();
		this.offsetTop = this.mainContent.offset().top;
		this.minOffsetTop = this.offsetTop - this.otherSticky;
		this.maxOffsetTop = this.mainContentHeight + this.otherSticky;
	},

	resetSticky: function() {
		this.stickyTimmer = '';

		this.container
			.removeClass('fixed-el bottom')
			.css({top: this.otherSticky});
	},

	sticky: function() {
		var scrollTop = jQuery(document).scrollTop(),
			ramainingContentHeight = this.mainContent.height() + this.mainContent.offset().top - scrollTop - this.otherSticky,
			isAtBottom = ramainingContentHeight < this.container.outerHeight(),
			isBetweenMainContent = scrollTop >= this.minOffsetTop && scrollTop <= this.maxOffsetTop;

		if(isAtBottom) {
			return this.container.addClass('fixed-el bottom');
		}

		this.container.addClass('fixed-el')

		if(isBetweenMainContent) {
			return this.container.removeClass('bottom').css({top: this.otherSticky});
		}

		if(scrollTop < this.minOffsetTop) {
			this.container.removeClass('fixed-el bottom').css({top: this.otherSticky});
		}
	}
});


/**
 * RateBase class
 *
 * @class RateBase
 * @constructor
 * @namespace component
 */

CDQ.component.RateBase = Class.extend({
	/**
	 * Rate Constructor
	 *
	 * @param {String} settings.selector the container of all others inner elements
	 * @method init
	 */
	init: function(settings) {
		var self = this;
		this.lastStarTouched = false;

		this.$container = jQuery(settings.selector);
		this.$stars = this.$container.find('[data-rate="stars"]');
		this.$hint = this.$container.find('[data-rate="hint"]');
		this.$rating = this.$container.find('[data-rate="rating"]');
		this.$votes = this.$container.find('[data-rate="votes"]');
		this.$title = this.$container.find('[data-rate-title]');
		this.$message = this.$container.find('[data-rate="message"]');

		/**
		 * If the article never was voted, the star is marked by
		 * default to one, but `raty` plugin triggers `click` action
		 * when you set a star, so this flag serve for prevent initial state
		 * of of being voted
		 * @type {Number}
		 */
		this.SET_DEFAULT_STAR_FLAG = false;

		this.settings = {
			theme: 1,
			space: false,
			half: true,
			rating: 0,
			number: 5,
			hints: ['', '', '', '', ''],
			click: function (rating, event) {
				self.handleClick(rating, event);
			},

			mouseover: function(rating, evt) {
				self.displayHint(rating, evt);
			}
		};

		self.setTheme('dark');

		this.setup(settings);

	},

	setTheme: function (type) {
		var theme = '../../img/rate/theme-dark';

		if(type === 'white') {
			theme = '../../img/rate/theme-white'
		}

		this.settings.path = theme;
	},

	/**
	 * Setup method responsible for setting up
	 * basic configuration/
	 *
	 * @property {Object} settings
	 * @param {Number} [settings.theme=1] defines the color os the stars, 1 for dark, 2 for orange background
	 * @param {Number} [settings.rating=0] the initial number of marked stars, also if provided it will be displayed
	 * @param {Number} [settings.number=5] the total number of stars
	 * @param {Number} [settings.votes] if is provided the total of votes it will be displayed
	 * @param {String} [path] the path for star image
	 * @param {Number} [settings.readOnly=false]
	 * @param settings.selector will be always replaceted by this.$votes element
	 * @method setup
	 * @param {Object} settings The settings of a single toggle element
	 */
	setup: function(settings) {
		this.settings = jQuery.extend(this.settings, settings);
	},

	run: function () {
		this.bind();
		this.build();
	},

	build: function () {
		this.$stars.raty(this.settings);
		this.initialState();
	},

	bind: function() {},

	displayHint: function() {},

	initialState: function() {},

	shouldSubmitVote: function() {
		return true;
	},

	submitVote: function() {},

	/**
	 * On touch devices it set alreadyTouched at first time, and at seccond it sends the vote
	 * @return {[type]} [description]
	 */
	handleClick: function(rating) {
		if(this.SET_DEFAULT_STAR_FLAG) {
			this.SET_DEFAULT_STAR_FLAG = false;
			return false;
		}

		if(this.shouldSubmitVote(rating)) {
			this.submitVote(rating);
		}
	}
});


CDQ.component.RateAnchor = CDQ.component.RateBase.extend({
	init: function (settings) {
		this.inherited().init(settings);
		this.setTheme('white');
		this.settings.selector = '[data-rate-anchor]';
	},

	initialState: function () {
		this.SET_DEFAULT_STAR_FLAG = true;
		this.$stars
			.raty('click', 5)
			.raty('readOnly', true);
	},

	scrollToVotableArea: function () {
		var offsetTop = this.settings.scrollToElement.offset().top;
		$('html, body').animate({scrollTop: offsetTop});
	},

	bind: function () {
		this.$container.click(this.scrollToVotableArea.bind(this));
	}
});

/**
 *
 * @example
 * new CDQ.component.RateOverview({selector: '[data-my-selector]', ...other options});
 */
CDQ.component.RateOverview = CDQ.component.RateBase.extend({
	init: function (settings) {
		this.inherited().init(settings);
		this.settings.readOnly = true;
	},

	calcScore: function(sum) {
		return (this.settings.rating + (sum || 0)) / this.settings.votes;
	},

	render: function(rating) {
		var votes = this.settings.votes === 0 ? 0 : rating;

		this.$votes.html(this.settings.votes);

		this.$rating.html(votes);
	},

	/**
	 * Update Total Of Votes and recalculate rating
	 */
	updateTotalOfVotes: function(data) {
		this.settings.votes = data.votes;
		this.settings.rating = data.rating;
		this.render(this.settings.rating);
	}
});


/**
 * RateVotable build a votable Rate element
 * @param {Object} settings
 * @param {String} settings.url for vote
 * @param {Boolen} settings.alreadyVoted if user already was voted
 * @param {Number} settings.vote user vote rate
 * @example
 * new CDQ.component.RateVotable({url: "http://urlForVote.com", vote: 4.5, al})
 * new CDQ.component.RateVotable({url: "http://urlForVote.com", alreadyVoted: true, vote: 4 })
 */
CDQ.component.RateVotable = CDQ.component.RateBase.extend({
	init: function (settings) {
		this.inherited().init(settings);
		this.setTheme('white');
		this.messageTimeout;

		if(this.settings.alreadyVoted) {
			this.oldVote = this.settings.vote;
		}
	},

	/**
	 * if user has touch it's set `lastStarTouched` and wait for a next click
	 * then if the vote is the same return true, otherwise false
	 * @overwrite
	 */
	shouldSubmitVote: function(rating) {
		if(Modernizr.touch) {

			if(this.lastStarTouched === rating) {
				return true;
			}

			this.$message.html(this.settings.label.tapAgainToSave);

			this.lastStarTouched = rating;
			return false;
		}

		return true;
	},

	bind: function() {
		var _this = this;

		this.$stars.on('mouseenter', function() {
			_this.$message.html('');
		});
	},

	initialState: function() {
		var alreadyVoted = this.settings.alreadyVoted;

		if(this.settings.votes === 0 || !alreadyVoted) {
			this.SET_DEFAULT_STAR_FLAG = true;
			this.displayHint(1);
			this.$stars.raty('click', 1);
		}

		if(alreadyVoted) {
			this.$title.html(this.settings.label.titleAfterVote);

			this.SET_DEFAULT_STAR_FLAG = true;
			this.displayHint(alreadyVoted);

			this.$stars
				.raty('click', this.settings.vote)
		}

		this.bind();
	},

	displayMessage: function () {
		var self = this,
			messageDuration = this.settings.messageDuration;

		function remove() {
			self.$message.html('');
		}

		clearTimeout(this.messageTimeout);
		this.messageTimeout = setTimeout(remove, messageDuration);
	},

	/**
	 * Make the RateVotable unvotable and notify parent element
	 * @return {[type]} [description]
	 */
	submitVote: function (rating) {
		var self = this,
			settings = self.inherited().settings,
			id =  settings.id,
			url = settings.url,
			titleAfterVote = settings.label.titleAfterVote,
			method = 'POST',
			server = window.location.href,
			isConradTestServer = server.indexOf('conrad-caine') === -1 ? false : true,
			isLocalTestServer = server.indexOf('localhost') === -1 ? false : true,
			data = {articleId: id, rating: rating};


		if(isLocalTestServer || isConradTestServer) {
			method = 'GET';
		}

		if(isLocalTestServer) {
			url = '/server/article-rate.json';
		}

		if(isConradTestServer) {
			var conradPath = window.location.href.split('public')[0];
			url = conradPath + 'public/server/article-rate.json';
		}

		if(this.oldVote) {
			data.oldrating = this.oldVote;
		}

		jQuery.ajax({url: url, method: method, dataType: 'json', data: data})
			.then(function(data) {
				self.$message.html(data.message);
				if(Number(data.status) === 1) {
					self.$title.html(titleAfterVote);

					self.settings.parent.trigger('rate.voted', data);
					self.lastStarTouched = false;
					self.SET_DEFAULT_STAR_FLAG = false;
					self.oldVote = rating;
					return;
				}

			});
	},

	displayHint: function(rating) {
		var tooltips = this.settings.label.tooltip;
		var message = '';

		if (rating !== null && rating > 0) {
			message = tooltips[Math.round(Number(rating) -1)];
		}

		this.$hint.html(message);
	}
});
