/**
 * Switch Brightcove videos in a playlist
 *
 * @class VideoSwitch
 * @constructor
 * @namespace component
 */
CDQ.component.VideoSwitch = Class.extend({
	/**
	 * VideoSwitch constructor
	 * @method init
	 * @param {jQueryObject} container Playlist wrapper
	 */
	init: function(settings) {
		var _this = this;

		this.settings = this.setup(settings);

		this.videoContainer = jQuery(this.settings.container);
		this.videoPlayerWrapper = this.videoContainer.find('.video-player-wrapper');
		this.dynamicPlayer = this.videoPlayerWrapper.find('.dynamic-player-non-automatic');

		CDQ.i.brightcovePlayers.createPlayer(this.dynamicPlayer, function(id) {
			_this.playerID = id;
			_this.bind();
			_this.findInfo();
		});
	},


	/**
	 * Extend the settings received in the constructor and overwrite if it's the case
	 *
	 * @param  {Object} settings
	 * @method  setup
	 */
	setup: function(settings) {
		return jQuery.extend({
			container: '.gfi-videos',
			autoplay: true
		}, settings);
	},


	/**
	 * Bind switch items
	 * @method bind
	 */
	bind: function() {
		var switchButton = this.videoContainer.find('[data-video-id]'),
			_this = this;

		switchButton.on('click', function() {
			var link = jQuery(this),
				videoID = link.data('video-id');

			switchButton.removeClass('is-active');
			link.addClass('is-active');

			_this.switchVideo(videoID, function() {
				//_this.removeInfo();
				//_this.addInfo();
			});
		});
	},

	/**
	 * Call to switch video
	 * @method switchVideo
	 * @param {String} videoID Video id to get info from API
	 * @param {Function} callback
	 */
	switchVideo: function(videoID, callback) {
		var cb = callback || function() {};

		if (this.currentVideo !== videoID) {
			CDQ.i.brightcovePlayers.switchVideo(this.playerID, videoID, this.settings.autoplay);
			this.currentVideo = videoID;

			cb();
		}
	},

	/**
	 * Check information in the link to add to the video information layer
	 *
	 * @method findInfo
	 */
	findInfo: function() {
		var videoID = this.dynamicPlayer.data('video-id'),
			switcher = this.videoContainer.find('[data-video-id="' + videoID + '"]');

		this.addInfo(switcher);
	},

	/**
	 * Get the information received in the link to add a layer in the video
	 *
	 * @method addInfo
	 * @param {jQueryElement} link with the data information
	 */
	addInfo: function() {
		/*
		var title = switcher.data('title'),
			desc = switcher.data('more'),
			descLink = switcher.data('link'),
			html = [];

		html.push('	<div class="content-video__body">');
		if (title) {
			html.push('		<h3>' + title + '</h3>');
		}
		if (descLink && desc) {
			html.push('		<a href="' + descLink + '"><span>' + desc + '</span> <span class="internal-link"></span></a>');
		}
		html.push('	</div>');

		this.videoInfo = jQuery(html.join(''));
		this.videoPlayerWrapper.append(this.videoInfo);
		*/
	},

	/**
	 * Remove information layer to add the other one
	 *
	 * @method removeInfo
	 */
	removeInfo: function() {
		//this.videoInfo.remove();
	},

	/**
	 * Hide the video information layer
	 *
	 * @method hideInfo
	 */
	hideInfo: function() {
		//this.videoInfo.fadeOut();
	},

	/**
	 * Show the video information layer
	 *
	 * @method showInfo
	 */
	showInfo: function() {
		//this.videoInfo.fadeIn();
	}
});
