/*
 * ProgressBar Class calculates the percentage of
 * the scroll in relation of a content
 * similar: http://codepen.io/pankajparashar/pen/towxF
 *
 * @example
 *	new CDQ.component.ProgressBar();
 *
 * @class ProgressBar
 * @constructor
 * @namespace component
 */
CDQ.component.ProgressBar = Class.extend({
	init: function (settings) {
		var suportHTML5ProgressBar = 'max' in document.createElement('progress'),
			Behavior = '';

		this.defaultOptions = {
			selector: '[progress-bar]',
			relativeTo: '[progress-bar="context"]'
		}

		this.stickyHeaderHeight = 148;


		this.settings = this.setup(settings);

		this.$progressBar = jQuery(this.settings.selector);

		if(suportHTML5ProgressBar) {
			Behavior = new CDQ.component.ProgressBarModernBrowserMixin();
		}
		else {
			Behavior = new CDQ.component.ProgressBarOldBrowserMixin();
		}

		jQuery.extend(true, this, Behavior);

		this.getInitialState();
		this.bind();
	},

	setup: function(settings) {
		return jQuery.extend(this.defaultOptions, settings);
	},

	getMax: function(){
		var context = jQuery(this.settings.relativeTo),
			contextHeight = context.outerHeight() - this.stickyHeaderHeight;

		return contextHeight;
	},

	getValue: function(){
		var offsetTop = jQuery(this.settings.relativeTo).offset().top,
			scroll = jQuery(window).scrollTop(),
			result = scroll - offsetTop,
			extra = 0;

		if(this.getMax() - result <= 600) {
			extra = 350;
		}

		return scroll < offsetTop - this.stickyHeaderHeight ? 0 : result + extra + this.stickyHeaderHeight;
	},

	bind: function () {
        jQuery(document).on('scroll resize', this.scroll.bind(this));
	}
});

/**
 * ProgressBarModernBrowserMixin handle api for html5 `progress` component
 */
CDQ.component.ProgressBarModernBrowserMixin = Class.extend({
	init: function () {
	},

	getInitialState: function () {
		var element = this.$progressBar,
			maxWidth = this.getMax();

		element.attr({ max: maxWidth });

		return this;
	},

	scroll: function () {
		var val = this.getValue();

		this.$progressBar.attr({value: val});
	},

	resize: function () {
		var max = this.getMax(),
			val = this.getValue();

		this.$progressBar.attr({max: max, value: val});
	}
});

/**
 * ProgressBarOldBrowserMixin fallback for old browsers
 */
CDQ.component.ProgressBarOldBrowserMixin = Class.extend({
	init: function () {
	},

	getWidth: function() {
		var value = this.getValue(),
			max = this.getMax(),
			width = (value/max) * 100;

		return width + '%';
	},

	getInitialState: function () {
		this.$progressBar = this.$progressBar.find('span');
		return this;
	},

	setWidth: function(){
		this.$progressBar.css({width: this.getWidth()});
	},

	resize: function() {
		this.setWidth();
	},

	scroll: function () {
		this.setWidth();
	}
});
