/**
 * Setup responsive tables
 *
 * 	new CDQ.component.ResponsiveTables();
 *
 * @class  ResponsiveTables
 * @constructor
 * @namespace component
 *
 *
 */

CDQ.component.ResponsiveTables = Class.extend({

	/**
	 * Responsive tables class constructor
	 *
	 * @method init
	 */
	init: function() {
		this.switched = false;
		this.tables = jQuery('table.responsive');
		this.bind();
		this.hideTip();
	},

	/**
	 * Bind events
	 *
	 * @method bind
	 */
	bind: function() {
		var _this = this;

		jQuery(window).on({
			load: function() {
				_this.updateTables();
			},
			redraw: function() {
				_this.switched = false;
				_this.updateTables();
				_this.fixHeight();
			},
			resize: function() {
				_this.updateTables();
			},
			'ResponsiveTable.adjust': function() {
				_this.heightCheck();
			},
			'ResponsiveTable.update': function() {
				_this.tables = jQuery('table.responsive');
				_this.updateTables();
			},
			scroll: function() {
				_this.hideTip();
			}
		});

		window.pageLoad = function() {
			jQuery(window).trigger('ResponsiveTable.update');
		}
	},

	/**
	 * Update tables depending on window width
	 *
	 * @method updateTables
	 */
	updateTables: function() {
		var _this = this,
			winW = window.innerWidth,
			breakpoints = CDQ.global.util.getBreakpoints(),
			lg = breakpoints.large,
			md = breakpoints.medium;

		if ((winW <= lg && winW > md)) {
			_this.tables.each(function(i, element) {
				_this.splitTable(jQuery(element), 2);
			});

			return true;
		}

		if ((winW <= md)) {
			_this.tables.each(function(i, element) {
				_this.splitTable(jQuery(element), 1);
			});

			return true;
		}

		if (winW > lg) {
			_this.tables.each(function(i, element) {
				_this.unsplitTable(jQuery(element));

			});

			return true;
		}

		return this.fixHeight();
	},

	/**
	 * Split, clone and add scroll to tables
	 *
	 * @method splitTable
	 * @param  {HTMLElement} original Original table element
	 * @param  {Integer|String} cols Number of columns to show pinned
	 */
	splitTable: function(original, cols) {
		var copy;
		this.unsplitTable(original);

		original.wrap("<div class='table-wrapper' />");

		copy = original.clone();
		jQuery.data(original, 'copy', copy);
		copy.find('td:not(:nth-child(-n+' + cols + ')), th:not(:nth-child(-n+' + cols + '))').css("display", "none");
		copy.removeClass("responsive");

		original.closest(".table-wrapper").append(copy);
		copy.wrap("<div class='pinned' />");
		original.wrap("<div class='scrollable' />");

		this.setCellHeights(copy, original);
	},

	/**
	 * Unsplit table to return to its default state
	 *
	 * @method  unsplitTable
	 * @param  {HTMLElement} original Original table element
	 */
	unsplitTable: function(original) {
		if (original.parent().is('.scrollable')) {
			original.closest(".table-wrapper").find(".pinned").remove();
			original.unwrap();
		}

		if (original.parent().is('div.table-wrapper')) {
			original.unwrap();
		}

		this.cleanStyles(original);
	},

	/**
	 * Standardize cell heights from both tables to avoid visual alignment issues
	 *
	 * @method setCellHeights
	 * @param {HTMLElement} original Original table element
	 * @param {HTMLElement} copy Copied table element
	 */
	setCellHeights: function(original, copy) {
		var tr = original.find('tr'),
			tr_copy = copy.find('tr'),
			heights = [],
			heights_copy = [],
			new_heights = [];

		tr.each(function(index) {
			var self = jQuery(this),
				tx = self.find('>th:not(.table-gmb th), >td');

			tx.each(function() {
				var self = jQuery(this),
					height = self.outerHeight(true);

				heights[index] = heights[index] || 0;
				if (self.is(':visible') && height > heights[index]) {
					heights[index] = height;
				}
			});
		});

		tr_copy.each(function(index) {
			var self = jQuery(this),
				tx = self.find('>th:not(.table-gmb th), >td');

			tx.each(function() {
				var height = jQuery(this).outerHeight(true);
				heights_copy[index] = heights_copy[index] || 0;

				if (self.is(':visible') && height > heights_copy[index]) {
					heights_copy[index] = height;
				}
			});
		});

		tr.each(function(index) {
			if (heights[index] > heights_copy[index]) {
				new_heights.push(heights[index]);
			} else {
				new_heights.push(heights_copy[index]);
			}
		});

		tr.each(function(index) {
			jQuery(this).height(new_heights[index]);
			jQuery(tr_copy[index]).height(new_heights[index]);
		});
	},
	/**
	 * Check if it's mobile and fix the height of the tables
	 *
	 * @method heightCheck
	 */
	heightCheck: function() {
		var windowWidth = window.innerWidth,
			breakpoints = CDQ.global.util.getBreakpoints();

		if (windowWidth <= breakpoints.large) {
			this.fixHeight();
		}
	},

	/**
	 * Adjust table cells heights
	 *
	 * @method fixHeight
	 */
	fixHeight: function() {
		var _this = this;

		this.tables.each(function(i, element) {
			var original = jQuery(element),
				parent = original.parent(),
				copy = parent.next().find('table');

			_this.setCellHeights(original, copy);
		});
	},

	/**
	 * Hide initial tip
	 *
	 * @method hideTip
	 */
	hideTip: function() {
		var el = jQuery('.drag-tip');
		if (el.length > 0 && CDQ.global.util.isOnScreen(el)) {
			jQuery('.drag-tip').addClass('js-show-and-hide');
		}
	},

	/**
	 * Clean table styles to reset state
	 *
	 * @method cleanStyles
	 * @param  {HTMLElement} original Original table element
	 */
	cleanStyles: function(original) {
		var tr = original.find('tr');

		tr.each(function() {
			tr.attr('style', '');
		});
	}

});
