/**
 * Class that is used to instantiate all brightcove players in pages
 *
 *	// Settings object isn't required. The example bellow shows the default values
 * 	new CDQ.core.BrightcovePlayers({
 * 		videojsWrapperSelector: '.video-js',
 *		dynamicWrappersSelector: '.dynamic-player',
 *		dynamicPlayerIdPrefix: 'dynamic-player-'
 * 	});
 *
 * @class BrightcovePlayers
 * @constructor
 * @namespace core
 */

CDQ.core.BrightcovePlayers = Class.extend({
	/**
	 * Class constructor
	 *
	 * @method init
	 * @param {Object} [settings] receive an object that contains settings to be used in the method.
	 *                            @param  {String} [settings.videojsWrapperSelector='.video-js'] The default brightcove selector.
	 *                            @param  {String} [settings.dynamicWrappersSelector='.dynamic-player'] When it would be created dynamically, there's an special selector for that
	 *                            @param  {String} [settings.dynamicPlayerIdPrefix='dynamic-player-'] The prefix that would be added to every new Id
	 *                            @param  {Boolean} [settings.preload=false] Avoid/Enable preload on video player
	 *                            @param  {Boolean} [settings.forceHTML=true] Always load HTML first
	 */
	init: function(settings) {
		this.settings = this.getSettings(settings);
		this.players = this.getPlayers();
		this.cache = {};
		this.id = 0;

		this.setup();
	},

	/**
	 * Extend default settings and settings from constructor
	 *
	 * @method getSettings
	 * @param {Object} [settings] receive an object that contains settings to replace default ones.
	 * @return {Object} Extended Settings
	 */
	getSettings: function(settings) {
		var defaultSettings = {
			videojsWrapperSelector: '.video-js',
			dynamicWrappersSelector: '.dynamic-player',
			dynamicPlayerIdPrefix: 'dynamic-player-',
			preload: false,
			forceHTML: true
		}

		return jQuery.extend(defaultSettings, settings);
	},

	/**
	 * Get all players with brightcove selector
	 *
	 * @method getPlayers
	 * @return {jQueryObject} A collection of player elements
	 */
	getPlayers: function() {
		return jQuery('video' + this.settings.videojsWrapperSelector + ', ' + this.settings.dynamicWrappersSelector);
	},

	/**
	 * Setup player elements to start brightcove player for each of them
	 *
	 * @method setup
	 * @return {[type]} [description]
	 */
	setup: function() {
		var _this = this;

		this.players.each(function() {
			var player = jQuery(this);

			_this.createPlayer(player);
		});

		this.addScripts();
	},

	/**
	 * Add scripts for all different players and rebuilt then.
	 *
	 * @method addScripts
	 */
	addScripts: function() {
		for (var id in this.cache) {
			if (this.cache.hasOwnProperty(id)) {
				this.addScript(id);
			}
		}
	},

	/**
	 * Build the source path and creates the script tag
	 *
	 * @method addScript
	 * @param {String} id Player element id
	 * @param {Function} callback Execute callback on script load
	 */
	addScript: function(id, callback) {
		var _this = this,
			plObj = this.cache[id],
			src = '//players.brightcove.net/' + plObj.account + '/' + plObj.player + '_default/index.min.js';

		jQuery.getScript(src, function() {
			_this.playerEvents(id);

			if (callback) {
				callback(id);
			}
		});
	},

	playerEvents: function(id) {
		var _this = this,
			api = this.getById(id);

		if (api) {
			api.on('play', function() {
				_this.playing = true;
			});

			api.on('pause', function() {
				_this.playing = false;
			});
		}
	},

	/**
	 * Get player by id, it allows you to use the brightcove player API
	 *
	 * @method getById
	 * @param  {String} id Player element id
	 * @return {BrightcoveAPI|Boolean} Returns the current player api (if available)
	 */
	getById: function(id) {
		if (typeof(videojs) != 'undefined') {
			return videojs(id);
		}

		return false;
	},

	/**
	 * For every player element, it gets the necessary data to build up the scripts and start the player
	 *
	 * @method getVideoData
	 * @param {jQueryElement} player The player element
	 * @param  {Function} callback A callback to be executed
	 */
	getVideoData: function(player, callback) {
		var id = player.attr('id'),
			playerId = player.attr('data-player'),
			account = player.attr('data-account'),
			hasAllData = id && player && account ? true : false;

		if (!this.cache.hasOwnProperty(id) && hasAllData) {
			this.cache[id] = {
				player: playerId,
				account: account
			};

			if (callback) {
				callback(id);
			}
		}
	},

	/**
	 * For some reason, brightcove requires that players are destroyed before hide them, so this method allows us to destroy brightcove players in a context.
	 *
	 * @method destroyPlayers
	 *
	 * @param  {HTMLElement|String} container A jQuery selector to select the context to find players
	 * @param  {Function} callback Callback to execute after all
	 *
	 * @return {Function} Return the callback in order to break the method execution
	 */
	destroyPlayers: function(container, callback) {
		var _this = this,
			players = jQuery(container).find(this.settings.videojsWrapperSelector),
			totalPlayers = players.length;

		if (callback && totalPlayers === 0) {
			callback();
			return false;
		}

		players.each(function(index) {
			var player = jQuery(this),
				id = player.attr('id'),
				api = _this.getById(id);

			if (api) {
				api.pause();
				setTimeout(function() {
					api.dispose();
				}, 0);
				delete _this.cache[id];
			}

			if (callback && index === (totalPlayers - 1)) {
				return callback();
			}
		});
	},

	/**
	 * Create a player when dynamic player is enabled. This is an adaptation to create a player after dispose it
	 * @param  {HTMLElement} element The dynamic element reference
	 * @param  {Function} callback Execute when player is ready
	 * @return {String} Player id that has been created
	 */
	createPlayer: function(element, callback) {
		var _this = this,
			reference = jQuery(element),
			account = reference.attr('data-account'),
			player = reference.attr('data-player'),
			autoplay = reference.attr('data-autoplay'),
			videoId = reference.attr('data-video-id'),
			video = jQuery('<video controls></video>'),
			id = this.getNextId(),
			screenlocker = this.createScreenlocker(reference);

		video.attr('data-account', account)
			.attr('data-player', player)
			.attr('data-embed', 'default')
			.attr('data-video-id', videoId)
			.attr('autoplay', autoplay)
			.addClass(this.settings.videojsWrapperSelector.replace('.', ''))
			.attr('id', id);

		if (!this.settings.preload) {
			video.attr('preload', 'none');
		}

		if (this.settings.forceHTML && !CDQ.global.util.isIOS() && !CDQ.global.util.isIE()) {
			video.attr('data-setup', '{"techOrder": ["html5", "flash"]}');
		}
		cl(CDQ.global.util.isIE())

		if (CDQ.global.util.isIE()) {
			video.attr('data-setup', '{"techOrder": ["flash"]}');
		}

		video.insertAfter(reference);

		this.getVideoData(video, function(id) {
			_this.addScript(id, function() {
				if (callback) {
					callback(id);
				}

				videojs(id).ready(function() {
					_this.hideScreenlocker(screenlocker);
				});
			});
		});

		return id;
	},

	/**
	 * Get next id with prefix. Util to setup dynamic players
	 *
	 * @method getNextId
	 * @return {String} Id prefix + next id number
	 */
	getNextId: function() {
		this.id = this.id + 1;

		return this.settings.dynamicPlayerIdPrefix + this.id;
	},

	/**
	 * If you want to switch to a new video in a video player, use this method.
	 *
	 * @method switchVideo
	 * @param {String} playerId Player id
	 * @param  {String|Integer} videoId Video id to play in the selected player
	 */
	switchVideo: function(playerId, videoId, play) {
		var player = this.getById(playerId);

		player.catalog.getVideo(videoId, function(err, video) {
			player.catalog.load(video);

			player.ready(function() {
				if (play) {
					player.play();
				}
			});
		});
	},

	/**
	 * Search for all players and call the pause method, it will pause only that video that is playing
	 *
	 * @method pauseAll
	 */
	pauseAll: function() {
		for (var id in this.cache) {
			if (this.cache.hasOwnProperty(id)) {
				this.pause(id);
			}
		}
	},

	/**
	 * Pause video by player id
	 *
	 * @method pause
	 * @param {String} id Player id in DOM
	 */
	pause: function(id) {
		var api = this.getById(id);

		if (api && !api.paused()) {
			api.pause();
		}
	},

	createScreenlocker: function(reference) {
		var label = jQuery('<span class="hide">Loading...</span>'),
			loader = jQuery('<div class="loader"></div>'),
			screenlocker = jQuery('<div class="video-player-screenlocker"></div>');

		loader.append(label);
		screenlocker.append(loader);

		if (jQuery.browser.msie && parseInt(jQuery.browser.version, 10) <= 9) {
			screenlocker.find('span').removeClass('hide');
		}

		screenlocker.insertAfter(reference);

		return screenlocker;
	},

	hideScreenlocker: function(screenlocker) {
		screenlocker.fadeOut(function() {
			screenlocker.remove();
		});
	}
});
