/**
 * Setup search field visual functionalities
 *
 * 	// Settings object isn't required. The example bellow shows the default values
 *  	new CDQ.component.Search({
 *  		urlPattern: '?search={searchTerm}',
 *    		onSubmit: function () {}
 *    	});
 *
 * @class Search
 * @constructor
 * @namespace component
 */
CDQ.component.Search = Class.extend({
	/**
	 * Search class constructor
	 *
	 * @method init
	 * @param {Object} [settings] receive an object that contains settings to be used in the method. The object parameter allows the following properties:
	 *                            @param  {String} [settings.urlPattern='?search={searchTerm}'] The url pattern to replace in filter search form.
	 *                            @param  {Function} [settings.onSubmit=null] The callback function to be executed before the search redirect.
	 */
	init: function(settings) {
		/**
		 * Settings property
		 *
		 * @property settings
		 * @type Object
		 * @default {
		 	urlPattern: '?search={searchTerm},
		 	onSubmit: null
		 }
		 */
		this.settings = this.getSettings(settings);

		this.btn = jQuery('.btn-search');
		this.input = jQuery('.search-text');
		this.form = this.input.parent();
		this.wrapper = this.form.parent();
		this.sugestor = this.form.parent().find('.autocomplete-search-result');
		this.inputAnimating = false;
		this.activationBtn = jQuery('.form-search-activate');
		this.productSearchWrapper = jQuery('.product-finder-search');

		this.bind();
	},

	/**
	 * Extend default settings and settings from constructor
	 *
	 * @method getSettings
	 * @param {Object} [settings] receive an object that contains settings to replace default ones.
	 * @return {Object} Extended Settings
	 */
	getSettings: function(settings) {
		var defaultSettings = {
			urlPattern: '?search={searchTerm}',
			onSubmit: null
		};

		return jQuery.extend(true, defaultSettings, settings);
	},

	/**
	 * Bind events
	 *
	 * @method bind
	 */
	bind: function() {
		var _this = this,
			productSearch = this.productSearchWrapper.find('input'),
			productSearchBtn = this.productSearchWrapper.find('a.icons-filled-search');

		if (!CDQ.global.util.checkIE8()) {
			this.input.on('focus', function() {
				_this.expandSearchForm();
			});

			this.input.on('blur', function() {
				_this.contractSearchForm();
			});
		}

		this.activationBtn.on(CDQ.global.activateAction, function() {
			_this.toggleSearch();
		});

		if (!CDQ.global.util.isTouch()) {
			jQuery(window).on('resize', function() {
				_this.resetSearchForm();
			});
		}

		productSearch.on('keydown', function(ev) {
			if (ev.keyCode === jQuery.ui.keyCode.ENTER) {
				jQuery(window).trigger('Search.doSearch', [productSearch]);
				return false;
			}
		});

		productSearchBtn.on(CDQ.global.activateAction, function() {
			jQuery(window).trigger('Search.doSearch', [productSearch]);
		});

		jQuery(window).on('Search.resetInput', function() {
			_this.resetSearchForm();
		});


		jQuery(window).on('Search.contractInput', function() {
			_this.contractSearchForm();
		});

		jQuery(window).on('Search.doSearch', function(ev, input) {
			_this.doSearch(input);
		});
	},

	/**
	 * Expand search field when active
	 *
	 * @method expandSearchForm
	 */
	expandSearchForm: function() {
		if (this.wrapper.hasClass('opened')) {
			return false;
		}

		this.form.addClass('is-active')
	},

	/**
	 * Contract search field when inactive
	 *
	 * @method contractSearchForm
	 */
	contractSearchForm: function() {
		if (this.wrapper.hasClass('opened') || this.sugestor.is(':visible')) {
			return false;
		}

		this.form.removeClass('is-active');
	},

	/**
	 * Reset search form when browser resizes or a custom event is fired
	 *
	 * @method resetSearchForm
	 */
	resetSearchForm: function() {
		this.wrapper.removeData('originalWidth');
		this.form.attr('style', '').removeClass('is-active');
		this.input.blur();

		this.toggleSearch(true);
	},

	/**
	 * Show/Hide search fild when it is hidden for lower breakpoints
	 *
	 * @method toggleSearch
	 * @param {Boolean} reset A boolean that indicates if field should be reseted or not
	 * @return {Bollean} Return true to stop execution when necessary
	 */
	toggleSearch: function(reset) {
		if (!reset && !this.activationBtn.hasClass('active')) {
			this.activationBtn.addClass('active');
			this.wrapper.addClass('opened');

			jQuery(window).trigger('MainMenu.reset');

			if (!CDQ.global.util.isTouch()) {
				this.input.get(0).focus();
			}

			return true;
		}

		this.input.blur();

		this.input.val('');
		this.activationBtn.removeClass('active');
		this.wrapper.removeClass('opened');

		return true;
	},

	/**
	 * Method fired on search button activation
	 *
	 * @method doSearch
	 * @param {jQueryElement} input Search input element object
	 */
	doSearch: function(input) {
		var searchTerm = input.val();

		if (this.settings.onSubmit && typeof(this.settings.onSubmit) === 'function') {
			this.settings.onSubmit(searchTerm);
		}

		window.location = this.settings.urlPattern.replace('{searchTerm}', encodeURIComponent(input.val()));
	}
});
