CDQ.component.NewsletterTable = Class.extend({
	/**
	 * NewsletterTable constructor
	 *
	 * @method init
	 */
	init: function(settings) {
		this.settings = this.getSettings(settings);
		this.options = this.getOptions();
		this.checkboxes = this.settings.wrapper.find('input:checkbox').not(this.settings.checkAll);
		this.bind();
	},

	getSettings: function(settings) {
		return jQuery.extend({
			wrapper: jQuery('.newsletter-table-wrapper'),
			checkAll: jQuery('#subscribe-all')
		}, settings);
	},

	getOptions: function() {
		return {
			unsubscribeText: this.settings.checkAll.data('unsubscribe') || 'Unsubscribe all',
			subscribeText: this.settings.checkAll.data('subscribe') || 'Subscribe all'
		};
	},

	bind: function() {
		var _this = this;

		this.settings.checkAll.on('click', function() {
			var label = jQuery(this).siblings('label');

			if (this.checked) {
				_this.set(true);
				label.text(_this.options.unsubscribeText);
			} else {
				_this.set(false);
				label.text(_this.options.subscribeText);
			}
		});

		this.checkboxes.on('click', function() {
			if (_this.settings.checkAll.get(0).checked) {
				_this.control(_this.settings.checkAll);
			}
		});
	},

	control: function(checkAll) {
		var checked = true;

		this.checkboxes.each(function() {
			if (!this.checked) {
				checked = false;
			}
		});

		if (!checked) {
			checkAll.get(0).checked = false;
		}
	},

	set: function(option) {
		this.checkboxes.each(function() {
			this.checked = option;
		});
	}
});
