/**
 * Geographic exposures map
 *
 *	// Settings object isn't required. The example bellow shows the default values
 *	new CDQ.component.GeographicExposuresMap({
 *		wrapper: '.geographic-exposures',
 *		map: '.geographic-exposures-map',
 *		labels: '.geographic-exposures-data',
 *		classColors: [
 *			'bg-orange',
 *			'bg-mid-orange',
 *			'bg-light-orange',
 *			'bg-blue',
 *			'bg-mid-blue',
 *			'bg-light-blue',
 *			'bg-gray',
 *			'bg-mid-gray',
 *			'bg-light-gray'
 *		],
 *		data: null
 *	});
 *
 * @class GeographicExposuresMap
 * @constructor
 * @namespace component
 */
CDQ.component.GeographicExposuresMap = Class.extend({
	/**
	 * Geographic exposures map constructor
	 *
	 * @method init
	 * @param {Object} settings Constructor settings
	 */
	init: function(settings) {
		this.settings = this.getSettings(settings);
		this.wrapper = jQuery(this.settings.wrapper);
		this.map = this.wrapper.find(this.settings.map);
		this.img = this.map.find('img');
		this.labels = this.wrapper.find(this.settings.labels);

		if (this.settings.data) {
			this.preload();
		}

	},

	/**
	 * Merge default settings with constructor settings
	 *
	 * @method getSettings
	 * @param {Object} settings Constructor settings
	 * @return {Object} Default settings merged with constructor settings
	 */
	getSettings: function(settings) {
		var defaultSettings = {
			wrapper: '.geographic-exposures',
			map: '.geographic-exposures-map',
			labels: '.geographic-exposures-data',
			classColors: [
				'bg-orange',
				'bg-mid-orange',
				'bg-light-orange',
				'bg-blue',
				'bg-mid-blue',
				'bg-light-blue',
				'bg-gray',
				'bg-mid-gray',
				'bg-light-gray'
			],
			data: null
		};

		return jQuery.extend(defaultSettings, settings);
	},

	/**
	 * Preload map image
	 *
	 * @method preload
	 */
	preload: function() {
		var _this = this;

		this.img.on('load', function() {
			_this.resize();
			jQuery(window).trigger('Sticky.goTo');
		});

		jQuery(window).on('resize', function() {
			_this.resize();
		});

		if (CDQ.global.util.checkIE8()) {
			jQuery(window).trigger('Sticky.goTo');
		}

		this.resize();

		this.populate();
	},

	/**
	 * Resize map element
	 *
	 * @method resize
	 */
	resize: function() {
		var scale = this.getScale(),
			transformString = 'scale(' + scale + ')';

		this.labels.css({
			transform: transformString,
			MozTransform: transformString,
			WebkitTransform: transformString,
			msTransform: transformString
		});
	},

	/**
	 * Get map scale to resize
	 *
	 * @method getScale
	 * @return {Integer} Map scale
	 */
	getScale: function() {
		if (!CDQ.global.util.checkIE8()) {
			return this.img.get(0).width / this.img.get(0).naturalWidth
		} else {
			return this.img.get(0).width / this.getNaturalWidth();
		}
	},

	/**
	 * Get natural width of image
	 *
	 * @method getNaturalWidth
	 * @return {Integer} Natural width of map image
	 */
	getNaturalWidth: function() {
		var img = new Image();
		img.src = this.img.attr('src');

		return img.width;
	},

	/**
	 * Populate map values
	 *
	 * @method populate
	 */
	populate: function() {
		var i,
			value,
			element,
			colorIndex = 0,
			totalColors = this.settings.classColors.length;
		for (i = 0; i < this.settings.data.Regions.length; i++) {
			value = this.settings.data.Regions[i].Region + ' ' + (this.settings.data.Regions[i].Exposure.toFixed(1) + '%');
			element = jQuery('.' + CDQ.global.util.slugify(this.settings.data.Regions[i].Region));
			element.addClass(this.settings.classColors[colorIndex]);
			element.html(value);

			if (this.settings.data.Regions[i].Exposure > 0) {
				element.css('display', 'block');
			}

			colorIndex++;

			if (colorIndex == (totalColors - 1)) {
				colorIndex = 0;
			}
		}
	}
});
