/**
 * Pie chart widget
 *
 *	new CDQ.component.PieChart(Object);
 *
 * @class PieChart
 * @constructor
 * @namespace component
 * @extends core.Charts
 */
CDQ.component.PieChart = CDQ.core.Charts.extend({
	getChartData: function(chartData) {
		var defaultChartData = {
			chart: {
				backgroundColor: null,
				spacing: 0,
				plotBackgroundColor: null,
				plotBorderWidth: null,
				plotShadow: false
			},
			title: {
				align: 'left',
				style: {
					color: '#3d4855',
					fontFamily: 'UniversDeutscheBank, Helvetica, Arial, sans-serif',
					fontSize: '18px'
				},
				text: 'Asset Allocation <small>As of: 15 Jul 2014 04:00 PM</small>',
				useHTML: true
			},
			tooltip: {
				pointFormat: '<b>{point.percentage:.1f}%</b>'
			},
			plotOptions: {
				pie: {
					allowPointSelect: false,
					borderWidth: 0,
					colors: ['#cbcdcf', '#3d4855', '#98a2ad'],
					cursor: 'pointer',
					dataLabels: {
						enabled: false
					},
					point: {
						events: {
							legendItemClick: function() {
								return false;
							}
						}
					},
					showInLegend: true,
					states: {
						hover: {
							enabled: true // if false, disables hover animation
						}
					}
				}
			},
			series: null,
			legend: this.getLegendData(),
			credits: {
				enabled: false
			}
		};

		return jQuery.extend(true, defaultChartData, chartData);
	},

	setupChart: function(obj) {
		var seriesData = [],
			i;

		for (i = 0; i < obj.data.length; i++) {
			seriesData.push([
				obj.data[i].AllocationName,
				obj.data[i].Weighting
			]);
		}

		this.chartData.series[0].data = seriesData;

		this.createChart();
		this.bind();
	},

	/**
	 * Bind event
	 * @method bind
	 */
	bind: function() {
		var _this = this;

		jQuery(window).on('resize', function() {
			if (CDQ.global.util.getViewportSize().width <= CDQ.global.util.getBreakpoints('small')) {
				_this.chart.legend.options.align = 'center';
				_this.chart.legend.options.verticalAlign = 'bottom';
			} else {
				_this.chart.legend.options.align = 'right';
				_this.chart.legend.options.verticalAlign = 'middle';
			}
		});
	},

	/**
	 * Return legend data according to viewport
	 *
	 * @method getLegendData
	 * @return {Object} Legend data
	 */
	getLegendData: function() {
		var isSmall = CDQ.global.util.getViewportSize().width <= CDQ.global.util.getBreakpoints('small');

		return {
			align: !isSmall ? 'right' : 'center',
			layout: 'vertical',
			verticalAlign: !isSmall ? 'middle' : 'bottom',
			itemMarginBottom: 10,
			labelFormatter: function() {
				return this.name + ' <strong>' + CDQ.global.util.numberFormat(this.y, 2) + '%</strong>';
			},
			itemStyle: {
				fontWeight: 'normal'
			},
			useHTML: true
		}
	}
});
