CDQ.component.TagsToggler = Class.extend({
	init: function(settings) {
		this.settings = this.getSettings(settings);

		this.setup();
	},

	getSettings: function(settings) {
		var defaultSettings = {
			wrapper: '.tags-wrapper',
			tags: '.toggle-tags',
			btn: '.toggle-tags-btn'
		};

		return jQuery.extend(defaultSettings, settings);
	},

	setup: function() {
		var _this = this,
			wrappers = jQuery(this.settings.wrapper);

		wrappers.each(function() {
			var wrapper = jQuery(this);

			_this.load(wrapper);
		});
	},

	load: function(wrapper) {
		var _this = this;

		if (wrapper.is(':visible')) {
			_this.check(wrapper);
		}

		jQuery(window).resize(function() {
			setTimeout(function() {
				_this.check(wrapper);
			}, 100);
		});
	},

	check: function(wrapper) {
		if (wrapper.is(':visible')) {
			this.applyTags(wrapper);
			wrapper.data('active', true);
		}
	},

	applyTags: function(wrapper) {
		var tags = wrapper.find(this.settings.tags),
			btn = wrapper.find(this.settings.btn),
			firstTag = tags.find('li:first'),
			h = firstTag.outerHeight(true),
			internalHeight = tags.get(0).scrollHeight;

		if (internalHeight > h) {
			this.contract(tags);
			this.bind(btn, tags);
			this.setBtnText(btn);
			btn.removeClass('hide');
		} else if (internalHeight === h) {
			this.expand(tags);
			btn.addClass('hide');
		}
	},

	bind: function(btn, tags) {
		var _this = this;

		if (!this.resizeBind) {
			_this.resizeBind = true;
			jQuery(window).on('resize', function() {
				jQuery(window).trigger('TagsToggler.resize');
			});
		}

		if (!this.tagsTogglerEvent) {
			_this.tagsTogglerEvent = true;
			jQuery(window).on('TagsToggler.resize', function() {
				if (tags.hasClass('tags-contracted')) {
					_this.contract(tags);
				}
			});
		}

		btn.each(function() {
			var b = jQuery(this);
			b.off();
			b.on('click', function() {
				if (tags.hasClass('tags-contracted')) {
					_this.expand(tags);
					_this.setBtnText(btn, true);
				} else {
					_this.contract(tags)
					_this.setBtnText(btn);
				}

				return false;
			});
		});
	},

	contract: function(tags) {
		var firstTag = tags.find('li:first'),
			h = firstTag.outerHeight(true);

		tags.css({
			maxHeight: h
		}).addClass('tags-contracted');
	},

	expand: function(tags) {
		tags.css({
			maxHeight: 800
		}).removeClass('tags-contracted');
	},

	hideBtn: function(btn) {
		btn.hide();
	},

	setBtnText: function(btn, expand) {
		var text = expand === true ? 'less' : 'more';
		btn.html(btn.data(text));
	}
});
