/**
 * Stock chart class
 *
 *	// Settings object isn't required. The example bellow shows the default values
 *	new CDQ.component.StockChart({
 *	 options: {
 *			labels: {
 *				percent: {
 *					x: '#{missing label: percent.x}',
 *					y: '#{missing label: percent.y}'
 *				},
 *				value: {
 *					x: '#{missing label: value.x}',
 *					y: '#{missing label: value.y}'
 *				}
 *			}
 *		}
 *	});
 *
 * @class StockChart
 * @constructor
 * @namespace component
 * @extends core.Charts
 */
CDQ.component.StockChart = CDQ.core.Charts.extend({
	/**
	 * Get settings specifically to StockChart
	 *
	 * @method getSettings
	 * @param {Object} [settings] receive an object that contains settings to be used in the method.
	 *                            @param {String} [settings.options.labels={}] An objext containing the labels of chart
	 *                            @param {String} [settings.options.lang={}] An objext containing the internationalization of popup

	 * @return {Object} Extended settings
	 */
	getSettings: function(settings) {
		var defaultSettings = {
				options: {
					labels: {
						percent: {
							x: '#{missing label: percent.x}',
							y: '#{missing label: percent.y}'
						},
						value: {
							x: '#{missing label: value.x}',
							y: '#{missing label: value.y}'
						}
					}
				},
				trackingCallback: null
			},
			preSettings = jQuery.extend(true, defaultSettings, settings);

		return this.inherited().getSettings(preSettings);
	},

	createChart: function() {
		this.bindSet = false;
		var _this = this,
			orgHighchartsRangeSelectorPrototypeRender = Highcharts.RangeSelector.prototype.render;

		if (!this.settings.ignoredDates) {
			this.settings.ignoredDates = [];
		}

		Highcharts.RangeSelector.prototype.render = function(min, max) {
			orgHighchartsRangeSelectorPrototypeRender.apply(this, [min, max]);

			var leftPosition = 0,
				topPosition = 0,
				space = 10;

			this.zoomText.attr({
				x: leftPosition,
				y: topPosition + 15
			});

			leftPosition += this.zoomText.getBBox().width;

			for (var i = 0; i < this.buttons.length; i++) {
				this.buttons[i].attr({
					x: leftPosition,
					y: topPosition
				});
				leftPosition += this.buttons[i].width + space;
			}
		};

		jQuery(this.settings.container).highcharts('StockChart', this.chartData, function(chart) {
			if (_this.settings.callback) {
				return _this.settings.callback(chart);
			}
			return _this.callback(chart);
		});
	},

	callback: function(chart) {
		var _this = this,
			minDate = chart.options.series[0].data.length > 0 ? chart.options.series[0].data[0].x : null,
			maxDate = chart.options.series[0].data.length > 0 ? chart.options.series[0].data[chart.options.series[0].data.length - 1].x : null,
			tabsLinks = jQuery('.stock-chart-options a');

		this.chart = chart;

		this.renderBackground();

		// setup the date pickers
		setTimeout(function() {
			var widget;

			_this.minDateField = jQuery('input.highcharts-range-selector[name="min"]');

			jQuery.datepicker.setDefaults({
				beforeShowDay: function(current) {
					var day = current.getDate() > 9 ? current.getDate() : '0' + current.getDate(),
						fixedMonth = current.getMonth() + 1,
						month = fixedMonth > 9 ? fixedMonth : '0' + fixedMonth,
						currentDate = current.getFullYear() + '-' + month + '-' + day,
						isWeekday = jQuery.datepicker.noWeekends(current);

					if (isWeekday[0] === true) {
						if (jQuery.inArray(currentDate, _this.settings.ignoredDates) > -1) {
							return [false, ''];
						}
						return [true, ''];
					} else {
						return isWeekday;
					}


				},
				dateFormat: 'yy-mm-dd',
				onSelect: function() {
					this.onchange();
					this.onblur();
				},
				minDate: moment(minDate).utc().format('YYYY-MM-DD'),
				maxDate: moment(maxDate).utc().format('YYYY-MM-DD')
			});

			jQuery(window).on('resize', function() {
				_this.setupRangeSelector();
			});

			_this.setupRangeSelector();

			jQuery('input.highcharts-range-selector', _this.chart.options.chart.renderTo)
				.datepicker({
					beforeShow: function(i, obj) {
						widget = obj.dpDiv;
						var uiDatepickerDiv = widget;
						if (widget.data("top")) {
							setTimeout(function() {
								uiDatepickerDiv.css("top", uiDatepickerDiv.data("top"));
							}, 50);
						}
					},
					onClose: function(i, obj) {
						widget = obj.dpDiv;
						widget.data("top", widget.position().top);
						this.blur();
						_this.changeDownloadLink();
					}
				});


			if (!this.bindSet) {
				if (CDQ.global.util.isTouch()) {
					CDQ.global.util.tappable(tabsLinks, function(ev) {
						_this.changeTab(ev);
					});
				} else {
					tabsLinks.off('click');
					tabsLinks.on('click', function(ev) {
						_this.changeTab(ev);
						return false;
					});
				}
				_this.bind();
				this.bindSet = true;
			}


			_this.changeDownloadLink(moment(_this.chart.xAxis[0].min).utc().format('YYYY-MM-DD'), moment(_this.chart.xAxis[0].max).utc().format('YYYY-MM-DD'));

			jQuery(window).trigger('Sticky.goTo');
		}, 0);
	},

	getChartData: function(chartData) {
		var _this = this;

		var defaultChartData = {
			colors: ['#3d4855', '#98a2ad'],
			chart: {
				backgroundColor: '#f4f6f7',
				plotBackgroundColor: '#e6e9eb',
				spacingTop: 50,
				height: 500,
				events: {
					redraw: function() {
						_this.changeDownloadLink(moment(this.xAxis[0].min).utc().format('YYYY-MM-DD'), moment(this.xAxis[0].max).utc().format('YYYY-MM-DD'));
						_this.resizeBackground();
					}
				}
			},

			yAxis: {
				opposite: false,
				labels: {
					align: 'right',
					x: -10
				},
				gridLineColor: 'white',
				gridLineWidth: 2,
				title: {
					text: this.settings.options.labels.percent.y
				}
			},
			xAxis: {
				lineWidth: 0,
				tickWidth: 0,
				type: 'datetime',
				dateTimeLabelFormats: {
					hour: ' ' // remove hour from axis
				},
				events: {
					setExtremes: function() {
						_this.changeDownloadLink(moment(this.min).utc().format('YYYY-MM-DD'), moment(this.max).utc().format('YYYY-MM-DD'));
					}
				},
				title: {
					text: this.settings.options.labels.percent.x
				},
				labels: {
					formatter: function() {
						var t = new Date(this.value - 1);
						var u = t.getUTCDate();
						var s = t.getUTCMonth();
						var v = t.getUTCFullYear();
						return Highcharts.dateFormat("%e-%b-%Y", Date.UTC(v, s, u))
					},
					staggerLines: 2
				}
			},
			plotOptions: {
				series: {
					colors: ['#3d4855', '#98a2ad'],
					compare: 'percent',
					padding: 20,
					turboThreshold: 0,
					dataGrouping: {
						enabled: false
					}
				}
			},

			tooltip: {
				backgroundColor: 'white',
				borderWidth: 0,
				shadow: false,
				crosshairs: {
					color: '#e29317',
					dashStyle: 'solid'
				},
				useHTML: true,
				formatter: function() {
					return _this.t(_this.getTooltip(this));
				}
			},
			rangeSelector: {
				selected: 3,
				inputPosition: {
					y: -44,
					x: 9
				},
				inputBoxBorderColor: '#91979d',
				inputDateFormat: '%Y-%m-%d',
				inputEditDateFormat: '%Y-%m-%d',
				inputStyle: {
					color: '#3d4855'
				},
				buttonTheme: {
					fill: '#677381',
					height: 18,
					padding: 8,
					style: {
						color: 'white',
						fontWeight: 'normal',
						fontFamily: 'Arial, sans-serif'
					},
					states: {
						hover: {
							fill: '#3d4855'
						},
						select: {
							fill: '#e29317',
							style: {
								color: 'white'
							}
						}
					}
				},
				buttons: [{
					type: 'month',
					count: 1,
					text: '1M'
				}, {
					type: 'month',
					count: 3,
					text: '3M'
				}, {
					type: 'month',
					count: 6,
					text: '6M'
				}, {
					type: 'year',
					count: 1,
					text: '1Y'
				}, {
					type: 'year',
					count: 3,
					text: '3Y'
				}, {
					type: 'year',
					count: 5,
					text: '5Y'
				}, {
					type: 'ytd',
					text: 'YTD'
				}]
			},
			navigator: {
				enabled: false,
				margin: 40,
				height: 50,
				handles: {
					backgroundColor: '#3d4855',
					borderColor: 'white'
				},
				maskFill: 'rgba(0, 0, 0, 0.2)',
				outlineColor: 'transparent',
				outlineWidth: 0,
				series: {
					type: 'areaspline',
					color: '#e6e9eb',
					fillOpacity: 1,
					// dataGrouping: {
					// 	smoothed: true
					// },
					lineWidth: 0,
					marker: {
						enabled: true
					}
				}
			},
			credits: {
				enabled: false
			},
			scrollbar: {
				enabled: false
			},
			legend: {
				align: 'right',
				verticalAlign: 'top',
				floating: true,
				enabled: true,
				symbolHeight: 16,
				y: 7
			}
		};

		return jQuery.extend(true, defaultChartData, chartData)
	},

	/**
	 * Get tooltip HTML to show on chart mouseover
	 *
	 * @method getTooltip
	 * @param {Object} serie Current serie object
	 * @return {String} HTML markup to show
	 */
	getTooltip: function(serie) {
		var html = [],
			extremes = serie.points[0].series.xAxis.getExtremes(),
			minDate = moment(extremes.min).utc().format('DD MMM YYYY'),
			maxDate = moment(serie.x).utc().format('DD MMM YYYY'),
			endDateLabel = this.currentCompare != 'history' ? 'endDate' : 'date',
			dateArray = this.getValuesByDate(serie.x),
			minDateArray = this.getValuesByDate(extremes.min),
			nav = serie.points[0].point.hasOwnProperty('change') ? serie.points[0].point.change.toFixed(2) : 0,
			navValue = (dateArray && dateArray[1] != null) ? dateArray[1].toFixed(2) : (0).toFixed(2),
			negArrow = ' icons-arrow-down-red-big',
			posArrow = ' icons-arrow-up-green-big',
			navIcon = nav < 0 ? negArrow : nav > 0 ? posArrow : '',
			indexI = 1,
			index,
			indexIcon;

		// Search manually for the next available date
		if (!minDateArray) {
			minDateArray = this.getMinDate(extremes.min);
			if (minDateArray) {
				minDate = moment(minDateArray[0]).utc().format('DD MMM YYYY');
			}
		}

		if ((serie.points.length > 1 && serie.points[1]) || (serie.points.length == 1 && serie.points[0].series.name == 'Index')) {
			indexI = serie.points[0].series.name == 'Index' ? 0 : 1;
			index = serie.points[indexI].point.change.toFixed(2);
			indexIcon = index < 0 ? negArrow : index > 0 ? posArrow : '';
		}

		html.push('<table class="stock-chart-tooltip">');
		html.push('	<thead>');
		html.push('		<tr>');
		if (this.currentCompare != 'history') {
			html.push('			<th>#{startDate}: ' + minDate + '</th>');
		}
		html.push('			<th>#{' + endDateLabel + '}: ' + maxDate + '</th>');
		html.push('		</tr>');
		html.push('	</thead>');
		html.push('	<tbody>');

		if (indexI == 1) {
			html.push('		<tr>');
			if (this.currentCompare != 'history') {
				html.push('			<th>#{navPercentage}</th>');
			}
			html.push('			<th>#{nav}</th>');
			html.push('		</tr>');

			html.push('		<tr>');
			if (this.currentCompare != 'history') {
				html.push('			<td><i class="icon-inline icon-middle' + navIcon + '"></i> ' + nav + ' %</td>');
			}
			html.push('			<td>' + navValue + '</td>');
			html.push('		</tr>');
		}

		if (index) {
			html.push('		<tr>');
			html.push('			<th>#{indexLevel}</th>');
			html.push('		</tr>');
			html.push('		<tr>');
			html.push('			<td><i class="icon-inline icon-middle' + indexIcon + '"></i> ' + index + '%</td>');
			html.push('		</tr>');
			html.push('	</tbody>');
		}

		html.push('</table>');

		return html.join('');
	},

	/**
	 * Create a background to adjust layout according to design
	 *
	 * @method renderBackground
	 */
	renderBackground: function() {
		// this.chartBg = this.chart.renderer.rect(0, this.chart.plotBox.y - 30, this.chart.containerWidth, this.chart.plotBox.height + this.chart.xAxis[1].height + 50, 0);
		this.chartBg = this.chart.renderer.rect(0, this.chart.plotBox.y - 30, this.chart.containerWidth, this.chart.plotBox.height + 100, 0);

		this.chartBg.attr({
			'stroke-width': 0,
			stroke: '#e6e9eb',
			fill: '#e6e9eb',
			zIndex: 0
		}).add();
	},

	/**
	 * Resize background when chart is resized
	 *
	 * @method resizeBackground
	 */
	resizeBackground: function() {
		var height = this.chart.plotBox.height + this.chart.xAxis[0].height;

		this.chartBg.attr({
			y: this.chart.plotBox.y - 30,
			width: this.chart.containerWidth,
			height: height + 30
		});
	},

	setupChart: function(obj, i) {
		var jsonData = obj || this.data,
		series = [],
		index = i || 6;

		series.push({
			name: jsonData.ticker,
			data: jQuery.map(jsonData.values, function(d) {
				return {
					x: d[0],
					y: d[index], // the 6th index is the adjusted NAV value, it will be used only to render the chart, the tooltip must show index 1. (PUSD-746)
					all: d[2]
				};
			})
		});

		series.push({
			name: "Index",
			data: jQuery.map(jsonData.values, function(d) {
				return {
					x: d[0],
					y: d[2],
					all: d[2]
				};
			})
		});

		if (!this.data) {
			this.data = obj;
		}
		this.chartData.series = series;

		this.createChart();
	},

	/**
	 * Get values by date from series
	 *
	 * @method getValuesByDate
	 * @param  {Date} date Date to select
	 * @return {Array} Current date values
	 */
	getValuesByDate: function(date) {
		var selectedDate;

		jQuery.each(this.data.values, function() {
			if (date == this[0]) {
				selectedDate = this;
			}
		});

		return selectedDate;
	},

	/**
	 * Change download links based on date range
	 *
	 * @example
	 *	<a href="javascript:;" class="download-distribution-schedule" data-type="csv">Download CSV</a>
	 *	<a href="javascript:;" class="download-distribution-schedule" data-type="xls">Download XLS</a>
	 *
	 * The url contain a data-type attribute that will get information from downloadUrl settings
	 *
	 * @method changeDownloadLink
	 */
	changeDownloadLink: function(min, max) {
		var _this = this,
			links = jQuery('.download-distribution-schedule'),
			dates,
			toReplace,
			urls = this.settings.downloadUrl;

		min = min || jQuery('input.highcharts-range-selector[name="min"]', this.chart.options.chart.renderTo).val(),
		max = max || jQuery('input.highcharts-range-selector[name="max"]', this.chart.options.chart.renderTo).val(),
		dates = [min, max];

		if (urls) {
			links.each(function() {
				var link = jQuery(this),
					type = link.data('type'),
					url = _this.settings.downloadUrl[type];

				toReplace = url.match(/\{[a-zA-Z]+\}+/ig);

				for (var i = 0; i < toReplace.length; i++) {
					url = url.replace(toReplace[i], dates[i]);
				}

				link.attr('href', url);
			});
		}
	},

	/**
	 * Change chart type to percentage
	 *
	 * @method renderPercentage
	 */
	renderPercentage: function() {
		this.setupChart();
		this.currentCompare = 'percent';
		this.chart.yAxis[0].setCompare('percent', false);
		this.chart.yAxis[0].setTitle({
			text: this.settings.options.labels.percent.y
		}, false);

		this.chart.xAxis[0].setTitle({
			text: this.settings.options.labels.percent.x
		}, false);

		this.chart.redraw();
	},

	/**
	 * Change chart type to default
	 *
	 * @method renderValue
	 */
	renderValue: function() {
		this.setupChart(undefined, 1);
		this.currentCompare = 'history';
		this.chart.yAxis[0].setCompare(null, false);
		this.chart.yAxis[0].setTitle({
			text: this.settings.options.labels.value.y
		}, false);

		this.chart.xAxis[0].setTitle({
			text: this.settings.options.labels.value.x
		}, false);

		this.chart.redraw();
	},

	/**
	 * Change tab
	 *
	 * @method changeTab
	 * @param  {Object} ev Event object
	 */
	changeTab: function(ev) {
		var link = jQuery(ev.target),
			tabElement = jQuery('.stock-chart-options'),
			tabs = tabElement.find('li');

		tabs.removeClass('active');

		link.parent().addClass('active');

		if (link.attr('href') == '#percent') {
			this.renderPercentage();
			this.addSerie('Index');
		} else {
			this.renderValue();
			this.removeSerie('Index');
		}

		return false;
	},

	/**
	 * Format date to desired output: YYY-MM-DD
	 *
	 * @method formatDate
	 * @param {Date} timestamp Date in timestamp format
	 */
	formatDate: function(timestamp) {
		return moment(timestamp).utc().format('YYYY-MM-DD');
	},

	/**
	 * Show/hide range selector buttons according to breakpoint.
	 *
	 * @method setupRangeSelector
	 */
	setupRangeSelector: function() {
		var buttons = jQuery('.highcharts-button');

		if (CDQ.global.util.getViewportSize().width <= CDQ.global.util.getBreakpoints('largeIntermediate')) {
			return buttons.hide();
		}

		return buttons.show();
	},

	/**
	 * Remove an specific serie from chart.
	 *
	 * @method removeSerie
	 * @param {String} name Serie name defined in this.chartData.series
	 */
	removeSerie: function(name) {
		for (var i = this.chart.series.length - 1; i >= 0; i--) {
			if (this.chart.series[i].hasOwnProperty('name') && this.chart.series[i].name == name && this.chart.series[i].visible) {
				this.chart.series[i].hide();
				jQuery('.highcharts-legend-item:last').hide();
			}
		}
	},

	/**
	 * Add an specific, existent, serie back from chart.
	 *
	 * @method addSerie
	 * @param {String} name Serie name
	 */
	addSerie: function(name) {
		for (var i = this.chart.series.length - 1; i >= 0; i--) {
			if (this.chart.series[i].hasOwnProperty('name') && this.chart.series[i].name == name && !this.chart.series[i].visible) {
				this.chart.series[i].show();
				jQuery('.highcharts-legend-item:last').show();
			}
		}
	},

	getMinDate: function(timestamp) {
		var adjustedTimestamp = parseInt(timestamp, 10),
			dateArray = this.getValuesByDate(adjustedTimestamp),
			dayms = 86400000,
			attempts = 0;

		if (dateArray) {
			return dateArray;
		}

		var nextDay = adjustedTimestamp + dayms,
			dateArr = this.getValuesByDate(nextDay);

		while (dateArr === undefined && attempts < 10) {
			attempts++;
			nextDay = nextDay + dayms;
			dateArr = this.getValuesByDate(nextDay);
		}

		return dateArr;
	},

	/**
	 * Bind method
	 *
	 * @method bind
	 */
	bind: function() {
		var _this = this,
			link = jQuery('.download-distribution-schedule');
		link.on(CDQ.global.activateAction, function() {
			_this.trackDownload();
		});
	},

	trackDownload: function() {
		var start = jQuery('input.highcharts-range-selector[name="min"]').val(),
			end = jQuery('input.highcharts-range-selector[name="max"]').val();
		if (this.settings.trackingCallback) {
			this.settings.trackingCallback(start, end);
 		}
 	}
});
