/**
 * It can be called everywhere
 *
 * @class GatewayInfographic
 * @constructor
 * @namespace component
 * @extend core.CanvasStage
 */
CDQ.component.GatewayInfographic = CDQ.core.CanvasStage.extend({
	/**
	 * Constructor
	 * @method init
	 * @param {Object} settings Settings to call parent task
	 */
	init: function(settings) {
		this.settings = settings;
		this.inherited().init(settings);
		this.configure();
		this.bind();
		this.start();
	},

	configure: function () {
		this.steps = this.getSteps();
		this.stepsOrder = ['title', 'buttons', 'circles', 'arrows', 'endSteps'];

		this.currentStep = 0;

		CDQ.global.infograficColorsSet = {
			bg1: this.paper.set(),
			bg2: this.paper.set()
		};
	},

	/**
	 * Start from the first step
	 *
	 * @method start
	 */
	start: function() {
		this.steps[this.stepsOrder[this.currentStep]].call();
	},

	/**
	 * Destroy the GatewayInfographic, but keeps it container
	 * @method destroy
	 */
	destroy: function() {
		jQuery('#' + this.settings.id + ' svg > *').unbind().remove();
	},

	/**
	 * Destroy GatewayInfographic and creates a new based on a new configuration
	 * @param  {Object}
	 */
	reinit: function (settings) {
		if(settings) {
			this.settings = settings;
		}
		this.destroy();
		this.configure();
		this.bind();
		this.start();
	},

	/**
	 * Add event to go to next step
	 *
	 * @method bind
	 */
	bind: function() {
		var _this = this;

		jQuery(document).off('Gateway_Next');
		jQuery(document).on('Gateway_Next', function() {
			_this.currentStep++;

			var nextStep = _this.stepsOrder[_this.currentStep];

			if (_this.steps[nextStep]) {
				_this.steps[nextStep].call();
			}
		});

		jQuery(document).on('GatewayInfographic.reinit', function(event, data) {
			_this.reinit(data);
		});
	},

	/**
	 * Return steps to be executed
	 *
	 * @methods getSteps
	 * @return {Object} steps All steps functions to be executed
	 */
	getSteps: function() {
		var _this = this,
			steps = {
				title: function() {
					_this.title = new CDQ.component.GatewayTitle(_this.settings, _this);
				},
				buttons: function() {
					_this.buttons = new CDQ.component.GatewayButtons(_this.settings, _this);
				},
				circles: function() {
					_this.circles = new CDQ.component.GatewayCircles(_this.settings, _this);
				},
				arrows: function() {
					_this.arrows = new CDQ.component.GatewayArrows(_this.settings, _this);
				},
				endSteps: function() {}
			};

		return steps;
	}
});
