/**
 * Basic Charts class
 *
 *	// Settings object isn't required. The example bellow shows the default values
 *	new CDQ.core.Charts({
 *		script: null,
 *		container: '.chart-container',
 *		chartData: null,
 *		url: null,
 *		options: null,
 *		callback: null
 *	});
 *
 * @class Charts
 * @constructor
 * @namespace core
 */
CDQ.core.Charts = Class.extend({
	/**
	 * Charts constructor
	 *
	 * @method init
	 * @param {Object} settings Constructor settings
	 */
	init: function(settings) {
		this.settings = this.getSettings(settings);
		this.chartData = this.getChartData(this.settings.chartData);

		//create a new reference to the object
		this.initialData = jQuery.extend({}, this.chartData);
		return this.start();
	},

	/**
	 * Merge default settings with constructor settings
	 *
	 * @method getSettings
	 * @param {Object} settings Constructor settings
	 * @return {Object} Default settings merged with constructor settings
	 */
	getSettings: function(settings) {
		var defaultSettings = {
			script: null,
			container: '.chart-container',
			chartData: null,
			url: null,
			options: null,
			callback: null
		};

		return jQuery.extend(true, defaultSettings, settings);
	},

	/**
	 * Start to build chart
	 *
	 * @method start
	 */
	start: function() {
		this.setOptions();

		if (this.settings.url) {
			return this.loadData();
		}

		return this.createChart();
	},

	/**
	 * Create chart after data loaded
	 *
	 * @method createChart
	 */
	createChart: function() {
		var _this = this;

		jQuery(this.settings.container).highcharts(this.chartData, function(chart) {
			if (_this.settings.callback) {
				return _this.settings.callback(chart);
			}

			return _this.callback(chart);
		});
	},

	/**
	 * Load json data
	 *
	 * @method loadData
	 */
	loadData: function() {
		var _this = this;

		if (this.settings.url) {
			return jQuery.ajax({
				url: this.settings.url,
				dataType: 'JSON',
				success: function(data) {
					_this.setupChart(data);
				},
				error: function() {
					return ce('## CDQ.core.Charts Error: Ajax error on load json data.');
				}
			});
		}

		return ce('## CDQ.core.Charts Error: The data to be used in chart wasn\'t defined');
	},

	/**
	 * Setup chart data before create chart
	 *
	 * @method setupChart
	 * @param {Object} obj Chart data
	 */
	setupChart: function(obj) {
		this.chartData.series = obj;
		this.createChart();
	},

	/**
	 * Set chart options
	 *
	 * @method setOptions
	 */
	setOptions: function() {
		Highcharts.setOptions(this.settings.options);
	},

	/**
	 * Chart callback
	 *
	 * @method callback
	 * @param {Object} chart Highcharts chart object
	 */
	callback: function(chart) {
		this.chart = chart;
		jQuery(window).trigger('Sticky.goTo');
	},

	/**
	 * Get chart data
	 *
	 * @method getChartData
	 */
	getChartData: function() {
		return null;
	},

	t: function(string) {
		var match = string.match(/#\{(.)*?\}/g),
			prop,
			t;

		if (!match) {
			return string;
		}

		for (var i = match.length - 1; i >= 0; i--) {
			prop = match[i] ? match[i].replace('#{', '').replace('}', '') : undefined;

			if (prop) {
				t = this.settings.options.lang[prop] ? this.settings.options.lang[prop] : '#{missing translation: ' + prop + '}';

				string = string.replace(match[i], t);
			}
		}

		return string;
	},

	getDefaultColors: function() {
		var defaultColor = [
			'#e39318',
			'#eaad52',
			'#efc98b',
			'#001645',
			'#6e7794',
			'#a4a7ba',
			'#768998',
			'#a9b0b6',
			'#d9e3e5'
		];

		// @note @theme
		var dbbankingtheme = [
			'#1AA2DF',
			'#33ADE2',
			'#4DB7E6',
			'#66C1E9',
			'#6e7794',
			'#a4a7ba',
			'#768998',
			'#a9b0b6',
			'#d9e3e5'
		];

		var theme = defaultColor;
		var isDBbanking= jQuery('body').hasClass('theme-db-banking');

		if(isDBbanking) {
			theme = dbbankingtheme;
		}

		return theme;
	}
});
