/**
 * Premium/Discount widget
 *
 *	// Settings object isn't required. The example bellow shows the default values
 *	new CDQ.component.PremiumDiscountChart({
 *		wrapper: '.pricing-details-chart',
 *		select: '.premium-discount-select',
 *		urlPattern: '?start={startDate}&end={endDate}',
 *		startDate: null,
 *		endDate: null,
 *		script: null,
 *		chartData: null,
 *		container: '.pricing-chart-wrapper',
 *		options: null,
 *		callback: null,
 *		lang: {
 *			discount: '#{missing lang.discount}',
 *			premium: '#{missing lang.premium}'
 *		}
 *	});
 *
 * @class PremiumDiscountChart
 * @constructor
 * @namespace component
 */
CDQ.component.PremiumDiscountChart = CDQ.core.Charts.extend({
	/**
	 * Pricing details widget constructor
	 *
	 * @method init
	 * @param {Objects} settings Constructor settings
	 */
	init: function(settings) {
		this.settings = this.getSettings(settings);
		this.wrapper = jQuery(this.settings.wrapper);
		this.valuesTable = this.wrapper.find('.pricing-details-values');
		this.valuesTbody = this.valuesTable.find('tbody');
		this.bars = this.wrapper.find(this.settings.bars);
		this.chartData = this.getChartData(this.settings.chartData);

		this.bind();
		this.load();
	},

	/**
	 * Bind events
	 *
	 * @method bind
	 */
	bind: function() {
		var _this = this;

		jQuery(this.settings.select).on('change', function() {
			var dates = this.value.split('-'),
				start = dates[0],
				end = dates[1];

			_this.load(start, end);
		});
	},

	/**
	 * Merge default settings with constructor settings
	 *
	 * @method getSettings
	 * @param {Object} settings Constructor settings
	 * @return {Object} Default settings merged with constructor settings
	 */
	getSettings: function(settings) {
		var defaultSettings = {
			wrapper: '.pricing-details-chart',
			select: '.premium-discount-select',
			urlPattern: '?start={startDate}&end={endDate}',
			startDate: null,
			endDate: null,
			script: null,
			chartData: null,
			container: '.pricing-chart-wrapper',
			options: null,
			callback: null,
			lang: {
				discount: '#{missing lang.discount}',
				premium: '#{missing lang.premium}'
			}
		};

		return jQuery.extend(true, defaultSettings, settings);
	},

	getChartData: function(chartData) {
		var _this = this,
			defaultChartData = {
				colors: ['#98a2ad'],
				chart: {
					type: 'column',
					backgroundColor: '#f4f6f7',
					plotBackgroundColor: "#e6e9eb",
					spacingRight: 0,
					spacingTop: 20,
					marginLeft: 1,
					events: {
						redraw: function() {
							_this.createChart();
						}
					}
				},
				title: {
					text: ''
				},
				xAxis: {
					endOnTick: true,
					startOnTick: true,
					showFirstLabel: true,
					showLastLabel: false,
					gridLineWidth: 1,
					gridLineColor: '#FFF',
					tickLength: 0,
					tickInterval: 0.5,
					lineColor: '#4f5356',
					plotLines: [{
						color: '#4f5356',
						width: 1,
						value: 0,
						dashStyle: 'dot',
						zIndex: 5,
						label: {
							text: 'NAV',
							rotation: 0,
							y: 20,
							style: {
								color: '#4f5356',
								fontFamily: 'Arial, sans-serif',
								fontSize: '12px'
							}
						}
					}, {
						color: 'transparent',
						width: 1,
						value: 0,
						zIndex: 7,
						label: {
							text: 'Discount | Premium',
							y: 260,
							x: -56,
							rotation: 0,
							style: {
								color: '#4f5356',
								fontWeight: 'bold',
								fontFamily: 'Arial, sans-serif',
								fontSize: 12
							}
						}
					}],
					title: {
						text: '-', // reserve bottom space
						style: {
							color: 'transparent'
						},
						textAlign: 'right',
						align: 'high'
					},
					labels: {
						enabled: true,
						formatter: function() {
							if (this.value > 4.5 ||
								this.value < -4.0 ||
								this.value == this.axis.min) {
								return '';
							}

							return this.value.toFixed(1);
						}
					}
				},
				yAxis: {
					lineWidth: 1,
					lineColor: '#4f5356',
					gridLineWidth: 0,
					labels: {
						enabled: false
					},
					title: {
						text: 'Number of Days',
						style: {
							color: '#4f5356',
							fontWeight: 'bold',
							fontFamily: 'Arial, sans-serif',
							fontSize: 12
						},
						y: -10,
						x: 10,
						textAlign: 'left',
						align: 'high',
						rotation: 0
					},
					startOnTick: true
				},
				legend: {
					enabled: false
				},
				credits: {
					enabled: false
				},
				tooltip: {
					enabled: false
				},
				plotOptions: {
					column: {
						stacking: 'normal',
						pointPadding: 0,
						groupPadding: 0,
						pointPlacement: -0.5,
						dataLabels: {
							enabled: true,
							verticalAlign: 'bottom',
							style: {
								color: '#4f5356',
								fontWeight: 'bold',
								fontFamily: 'Arial, sans-serif',
								fontSize: 12
							}
						},
						states: {
							hover: {
								color: '#e29317'
							}
						}
					}
				},
				series: [{}]
			};

		return jQuery.extend(true, defaultChartData, chartData);
	},


	/**
	 * Load json data
	 *
	 * @method load
	 * @param {Date} [start] Start date
	 * @param {Date} [end] End date
	 */
	load: function(start, end) {
		var _this = this,
			startDate = start ? start : this.settings.startDate,
			endDate = end ? end : this.settings.endDate,
			url = this.settings.urlPattern.replace('{startDate}', startDate)
			.replace('{endDate}', endDate);

		jQuery.ajax({
			url: url,
			dataType: "JSON",
			success: function(data) {
				if (_this.chart) {
					_this.chart.destroy();
				}
				_this.populate(data);

				if (!_this.firstTime) {
					_this.firstTime = true;
					jQuery(window).trigger('Sticky.goTo');
				}
			}
		});

	},

	/**
	 * Populate widget
	 *
	 * @method populate
	 */
	populate: function(data) {
		this.setupChart(data);
		this.populateValues(data);
	},

	setupChart: function(data) {
		this.chartData.series[0].data = data.data;

		// define min and max
		this.chartData.xAxis.min = this.getMin(data.data);
		this.chartData.xAxis.max = this.getMax(data.data);

		this.createChart();
	},

	/**
	 * Populate Premium/Discount values
	 *
	 * @method populateValues
	 */
	populateValues: function(data) {
		this.valuesTbody.find('td').each(function() {
			var cell = jQuery(this);

			cell.html(data[cell.attr('data-name')])
		});

		this.bindGroups();
	},

	/**
	 * Bind mouse events in table to hover columns
	 *
	 * @method bindGroups
	 */
	bindGroups: function() {
		var _this = this,
			groups = this.valuesTable.find('[data-name]'),
			labels = this.valuesTable.find('thead th');

		labels.on('mouseenter', function() {
			groups.eq(jQuery(this).index()).trigger('mouseenter');
		}).on('mouseleave', function() {
			groups.eq(jQuery(this).index()).trigger('mouseleave');
		});

		groups.each(function() {
			var group = jQuery(this),
				cols = _this.valuesTable.find('colgroup col');

			group.on('mouseenter', function() {
				if (_this.mouseenter(group.data('name'))) {
					cols.eq(jQuery(this).index()).addClass('active');
				}
			}).on('mouseleave', function() {
				_this.mouseleave();
				cols.eq(jQuery(this).index()).removeClass('active');
			})
		});
	},

	callback: function(chart) {
		this.chart = chart;
		this.draw();
	},

	/**
	 * Method that writes texts in the chart and forces first/last columns to
	 * be resized
	 *
	 * @method draw
	 */
	draw: function() {
		// this.drawText(this.settings.lang.premium, 'right');
		// this.drawText(this.settings.lang.discount);

		this.setupBarWidth();
	},

	/**
	 * Draw text label into chart
	 *
	 * @method drawText
	 * @param  {String} text Text to be drawn
	 * @param  {String} align Text alignment
	 */
	drawText: function(text, align) {
		var drawText = this.chart.renderer.text(text).add().toFront(),
			textBBox = drawText.getBBox(),
			x = this.chart.plotLeft + 10,
			y = 40;

		if (align == 'right') {
			x = this.chart.plotLeft + this.chart.plotWidth - (textBBox.width + 10);
		}

		drawText.attr({
			x: x,
			y: y
		}).css({
			color: '#4f5356',
			fontFamily: 'Arial, sans-serif',
			fontSize: '12px'
		});
	},

	/**
	 * Method to be called on mouse enter of a data group
	 *
	 * @method mouseenter
	 * @param {String} group An identifier to define the group to highlight
	 * @return {Boolean}
	 */
	mouseenter: function(group) {
		switch (group) {
			case 'DaysAtPremium':
				jQuery(this.chart.series[0].data).each(function() {
					if (this.x > 0) {
						this.setState('hover');
					}
				});
				return true;

			case 'DaysAtDiscount':
				jQuery(this.chart.series[0].data).each(function() {
					if (this.x <= 0) {
						this.setState('hover');
					}
				});
				return true;

			case 'DaysBetween':
				jQuery(this.chart.series[0].data).each(function() {
					if (this.x < 1 && this.x > -0.5) {
						this.setState('hover');
					}
				})
				return true;

			default:
				return false;
		}
	},

	/**
	 * Remove highlights on mouse leave
	 *
	 * @method mouseleave
	 */
	mouseleave: function() {
		jQuery(this.chart.series[0].data).each(function() {
			this.setState();
		});
	},

	/**
	 * Get the biggest number from data series
	 *
	 * @method getMax
	 * @param {Array} series Data series
	 * @return {Integer} The biggest number from series
	 */
	getMax: function(series) {
		var max = 1,
			len = series && series.length ? series.length - 1 : -1;

		for (var i = len; i >= 0; i--) {
			if (max < series[i][0]) {
				max = series[i][0];
			}
		}

		return max;
	},

	/**
	 * Get the smallest number from data series
	 *
	 * @method getMin
	 * @param {Array} series Data series
	 * @return {Integer} The smallest number from series
	 */
	getMin: function(series) {
		var min = -1,
			len = series && series.length ? series.length - 1 : -1;

		for (var i = len; i >= 0; i--) {
			if (min > series[i][0]) {
				min = series[i][0];
			}
		}

		return min - 0.5;
	},

	/**
	 * Workaround to make first/last columns wider
	 *
	 * @method setupBarWidth
	 */
	setupBarWidth: function() {
		// GMB: this is the only way it works
		var data = this.chart.series[0].data;

		jQuery(data).each(function(index) {
			if (this.graphic && (index === 0 || index === data.length - 1)) {
				var increaseW = 0.5,
					col = this.graphic,
					bbox = col.getBBox(),
					width = (bbox.width * (1 + increaseW)),
					x = index === 0 ? bbox.x - (bbox.width * increaseW) : bbox.x,
					label = this.dataLabel ? this.dataLabel : null,
					labelBbox = label ? label.getBBox() : null;

				col.attr({
					width: width,
					x: x
				});

				if (label) {
					label.attr({
						x: index === 0 ?
							(width / 2) - (labelBbox.width / 2) : bbox.x + (width / 2) - (labelBbox.width / 2)
					});
				}
			}
		});
	}
});
