/**
 * Setup document viewer methods
 *
 *	new CDQ.component.DocumentViewer();
 *
 * @class DocumentViewer
 * @constructor
 * @namespace component
 */

CDQ.component.DocumentViewer = Class.extend({
	/**
	 * Method constructor that defines the frame height
	 *
	 * @method init
	 */
	 init: function(settings) {
	 	this.settings = this.getSettings(settings);
	 	this.bind();
	 },

	 getSettings: function(settings) {
	 	return jQuery.extend({
	 		tabs: jQuery('.tabs'),
	 		docView: jQuery('.document-viewer-frame'),
	 		downloadWrapper: jQuery('.document-footer'),
	 		downloadItem: jQuery('.document-footer-wrap')
	 	}, settings);
	 },

	 bind: function() {
	 	var _this = this;
	 	var tabLinks = this.settings.tabs.find('a');
	 	var downloadLinks = this.settings.downloadItem.find('a');

	 	tabLinks.on(CDQ.global.activateAction, function(ev) {
	 		var el = jQuery(this);

			ev.preventDefault();
	 		_this.activateTab(el);
	 		_this.setIframeSrc(el);
	 		_this.activateDownload(el, tabLinks);
	 	});

	 	downloadLinks.on(CDQ.global.activateAction, function(ev) {
	 		var el = jQuery(this);

	 		if (_this.isPDF(el)) {
	 			ev.preventDefault();
	 			window.open(el.attr('href'), "", "width=800,height=600,resizable=yes");
	 		}
	 	});

	 	jQuery(window).resize(function() {
	 		_this.setIframeHeight();
	 	});

	 	jQuery(window).load(function() {
	 		_this.setIframeHeight();
	 		_this.settings.docView.attr('src', _this.settings.docView.attr('src'));
	 	});
	 },

	 activateTab: function(el) {
	 	var parent = el.parent();

	 	parent.siblings().removeClass('active mobile-active');
	 	parent.addClass('active mobile-active');
	 },

	 activateDownload: function(el, tabLinks) {
	 	var index = tabLinks.index(el);

	 	this.settings.downloadItem.addClass('hide');
	 	this.settings.downloadItem.eq(index).removeClass('hide');
	 	this.setIframeHeight();
	 },

	 setIframeSrc: function(el) {
	 	this.settings.docView.attr('src', el.attr('href'));
	 },

	 setIframeHeight: function() {
	 	var framePosition = this.settings.docView.offset().top;
	 	var downloadHeight = this.settings.downloadWrapper.outerHeight(true);
	 	var windowHeight = jQuery(window).height();
	 	var spaceBottom = 20;
	 	var minimum = 400;

	 	var frameHeight = windowHeight - framePosition - downloadHeight  - spaceBottom;

	 	this.settings.docView.css('height', frameHeight > minimum ? frameHeight : minimum);
	 },

	 isPDF: function(el) {
	 	var pattern = /^(.*?)\.pdf$/;

	 	return el.attr('href').match(pattern);
	 }

});
