CDQ.component.ImageToggler = Class.extend({
	init: function() {
		this.images = jQuery('img[data-srcset]');
		this.references = [];
		this.parseImages();
		this.setSource();
		this.bind();
	},

	parseImages: function() {
		var _this = this;

		this.images.each(function() {
			var currentImage = jQuery(this);

			_this.references.push({
				image: currentImage,
				sources: _this.getSources(currentImage.data('srcset'))
			});
		});
	},

	getSources: function(data) {
		var breakpoints = data.split(','),
			info = [];

		for (var i = 0; i < breakpoints.length; i++) {
			var pair = jQuery.trim(breakpoints[i]).split(' ');

			info.push({
				path: pair[0],
				width: parseInt(pair[1], 10)
			});
		}

		return info.sort(function(a, b) {
			return a.width - b.width;
		});
	},

	bind: function() {
		var _this = this;

		jQuery(window).resize(function() {
			clearTimeout(_this.resizeTimer);
			_this.resizeTimer = setTimeout(function() {
				_this.setSource();
			}, 50);
		});
	},

	setSource: function() {
		var currentImage,
			url;

		for (var i = 0, len = this.references.length; i < len; i++) {
			currentImage = this.references[i];
			url = this.getUrl(currentImage.sources);

			if (currentImage.image.attr('src') != url) {
				currentImage.image.attr('src', url);
			}
		}
	},

	getUrl: function(sources) {
		var screenWidth = jQuery(window).width();

		for (var i = 0, len = sources.length; i < len; i++) {
			if (sources[i].width > screenWidth) {
				return sources[i].path;
			}
		}

		return sources[sources.length - 1].path;
	}
});
