/**
 * Description
 *
 * @class Infographic
 * @constructor
 * @namespace component
 */

CDQ.component.Infographic = Class.extend({
	/**
	 * Description
	 *
	 * @method  init
	 */
	init: function() {
		this.element = jQuery(".infographic");

		if (this.element.length === 0) {
			return false;
		}

		this.container = this.element.find(".graphics");
		this.wrapper = this.element.find(".graphics-wrapper");
		this.graphics = this.element.find(".graphic-type-1, .graphic-type-2, .graphic-type-3");
		this.aside = this.element.find(".aside");

		this.opening = false;

		this.start();
	},

	/**
	 * Description
	 *
	 * @method  start
	 */
	start: function() {
		var _this = this;

		this.adjustItems();
		CDQ.global.util.linearFadeIn(this.graphics.eq(0), "slow", "linear", function() {
			_this.initializeScroll();

			if (CDQ.global.util.isMobile()) {
				_this.bindMobileHandlers();
				_this.fixDetails();
			} else {
				_this.bindDesktopHandlers();
			}

			_this.bind();
		});
	},

	/**
	 * Description
	 *
	 * @method  initializeScroll
	 */
	initializeScroll: function() {
		if (jQuery(window).width() > 768) {
	 		this.scrollPane = this.aside.jScrollPane({
				autoReinitialise: true,
				hideFocus: true
			});
			this.isScrollActive = true;
		}


		this.aside.prepend("<div class='close-button'><span class='close'>Close</span></div>");
		this.closeButton = this.aside.find(".close-button");
	},

	fixDetails: function() {
		this.aside.find("h2").css("line-height", "30px");
	},

	/**
	 * Description
	 *
	 * @method  bindDesktopHandlers
	 */
	bindDesktopHandlers: function() {
		var _this = this;

		this.graphics.on({
			mouseover: function(event) {
				var index = _this.index(jQuery(event.currentTarget));

				if (!_this.isOpen()) {

					if (_this.blurring) {
						clearTimeout(_this.blurring);
						_this.blurring = null;
					}

					_this.blur();
					_this.focus(index);
				}
			},
			mouseout: function() {
				if (!_this.isOpen()) {
					_this.blurring = setTimeout(function() {
						_this.blur();
					}, 200);
				}
			},
			click: function(event) {
				var index = _this.index(jQuery(event.currentTarget));

				if (!_this.isOpen()) {
					event.stopPropagation();
					_this.open(index);
				}
			}
		});

		jQuery("html").on({
			click: function(event) {
				var contentWrapper = _this.targeted(event.target, ".aside"),
					closeButton = _this.targeted(event.target, ".aside .close-button"),
					closeArea = closeButton || !contentWrapper;

				if (_this.isOpen() && closeArea) {
					_this.close();
				}
			}
		});
	},

	/**
	 * Description
	 *
	 * @method  bindMobileHandlers
	 */
	bindMobileHandlers: function() {
		var _this = this,
			buttonBehaviour,
			graphicBehaviour;

		buttonBehaviour = function() {
			if (_this.isOpen()) {
				_this.close();
			}
		}

		graphicBehaviour = function(event) {
			var index = _this.index(jQuery(event.currentTarget)),
				focus = _this.index(_this.graphics.filter('.hover'));

			if (!_this.isOpen() && !_this.isFocus()) {
				_this.focus(index);
			} else if (!_this.isOpen() && index !== focus) {
				_this.blur();
				_this.focus(index);
			} else if (!_this.isOpen()) {
				_this.open(index);
			} else {
				_this.close();
			}
		};

		this.closeButton.swipe({
			tap: buttonBehaviour
		});
		this.closeButton.on({
			click: buttonBehaviour
		});

		this.graphics.swipe({
			tap: graphicBehaviour
		});
		this.graphics.on({
			click: graphicBehaviour
		});
	},

	bind: function() {
		var _this = this;

		jQuery(window).resize(function() {
			setTimeout(function() {
				if (jQuery(this).width() <= 768 && _this.isScrollActive) {
					_this.aside.attr('style', '');
					_this.isScrollActive = false;
					_this.scrollPane.destroy();
				} else if (jQuery(this).width() > 768 && !_this.isScrollActive) {
					_this.scrollPane = _this.aside.jScrollPane({
						autoReinitialise: true,
						hideFocus: true
					});
					_this.isScrollActive = true;
				}
			}, 500);
		});
	},

	/**
	 * Description
	 *
	 * @method  isOpen
	 */
	isOpen: function() {
		return this.graphics.filter(".active").length > 0 ? true : false;
	},

	/**
	 * Description
	 *
	 * @method  isOpen
	 */
	isFocus: function() {
		return this.graphics.filter(".hover").length > 0 ? true : false;
	},

	/**
	 * Description
	 *
	 * @method  index
	 */
	index: function(element) {
		if (element.length) {
			return Number(element.attr("class").replace(/[a-z]+-type-/gi, "").replace(/[a-z]/gi, ""));
		}
		return null;
	},

	/**
	 * Description
	 *
	 * @method  targeted
	 * @param  {Objec} target Description
	 * @param  {String} selector Description
	 */
	targeted: function(target, selector) {
		return jQuery(target).parents().andSelf().filter(selector)[0] ? true : false;
	},

	/**
	 * Description
	 *
	 * @method  focus
	 * @param  {Number} index Description
	 */
	focus: function(index) {
		var graphic = ".graphic-type-" + index;
		this.wrapper.children().not(graphic).addClass("inactive");
		this.wrapper.children(graphic).addClass("hover");
	},

	/**
	 * Description
	 *
	 * @method  blur
	 */
	blur: function() {
		this.wrapper.children().removeClass("inactive").removeClass("hover");
	},

	/**
	 * Description
	 *
	 * @method  open
	 * @param  {Number} index Description
	 */
	open: function(index) {
		var _this = this,
			graphic = jQuery(".graphic-type-" + index),
			content = jQuery(".content-type-" + index),
			selected = "selected-type-" + index;

		// this.opening = true;

		this.wrapper.children().not(graphic).addClass("inactive");
		graphic.removeClass("hover");
		graphic.addClass("active");
		content.addClass("active");

		this.container.addClass(selected, {
			duration: "slow",
			easing: "linear",
			complete: function() {
				_this.aside.fadeIn("slow", "linear", function() {
					_this.aside.addClass("active");
					_this.opening = false;
				});
			}
		});
	},

	/**
	 * Description
	 *
	 * @method  close
	 */
	close: function() {
		var _this = this,
			content = jQuery("[class*='content']"),
			selected = "selected-type-1 selected-type-2 selected-type-3";

		this.aside.fadeOut("slow", "linear", function() {
			content.removeClass("active");
			_this.aside.removeClass("active")

			_this.container.removeClass(selected, "slow", "linear", function() {
				_this.wrapper.children().removeClass("inactive").removeClass("active");
			});
		});
	},


	adjustItems: function() {
		var topLabel = this.wrapper.find('.client-value'),
			topLabelW = topLabel.outerWidth(true),
			wrapperCenter = this.wrapper.width() / 2,
			tLabelX = wrapperCenter - topLabelW / 2;

		topLabel.css('margin-left', tLabelX);
	}
});
