/**
 * Scrollable will get a list of elements and render it in one line.
 * When there's more content to show, it will create arrows to navigate in a
 * single line.
 *
 * 	// Settings object isn't required. The example bellow shows the default values
 * 	new CDQ.component.Scrollable({
 * 		element: null,
 *		wrapper: '.scrollable-tabs',
 *		scrollable: 'ul',
 *		prevBtn: '.scroll-link.icons-arrow-prev',
 *		nextBtn: '.scroll-link.icons-arrow-next',
 *		hideBtnClass: 'scroll-link--hidden',
 *		withArrowClass: '.scrollable-tabs--with-arrows',
 *		move: 20
 * 	});
 *
 * @class Scrollable
 * @constructor
 * @namespace component
 */
CDQ.component.Scrollable = Class.extend({
	/**
	 * Scrollable constructor
	 *
	 * @method init
	 */
	init: function(settings) {
		var _this = this;

		this.moveInterval;
		this.hasTransform = jQuery('html').hasClass('csstransforms');
		this.nl = 0;
		/**
		* Settings property
		*
		* @property settings
		* @type Object
		* @default {
			wrapper: '.scrollable',
			scrollable: '.scrollable ul',
			prevBtn: '.scroll-link.icons-arrow-prev',
			nextBtn: '.scroll-link.icons-arrow-next',
			hideBtnClass: 'scroll-link--hidden',
			withArrowClass: '.scrollable-tabs--with-arrows',
			move: 20
		}
		*/
		this.settings = this.getSettings(settings);

		/**
		 * All scrollable elements available
		 *
		 * @property elements
		 * @type jQueryObject
		 */
		this.elements = jQuery(this.settings.element);

		this.elements.each(function() {
			var isAutoScrollBehavior = jQuery(this).data('autoScroll') === "";

			if(isAutoScrollBehavior) {
				new CDQ.component.AutoScrollable(jQuery(this), _this);
			}

			_this.setup(jQuery(this));
		});
	},

	/**
	 * Setup the element and show buttons only when content width is bigger than the current row
	 *
	 * @method setup
	 * @param {jQueryObject} element The current scrollable element
	 */
	setup: function(element) {
		this.setSizes(element);

		this.bind(element);
		element.find(this.settings.prevBtn).addClass(this.settings.hideBtnClass);
	},

	/**
	 * Get default settings
	 *
	 * @method getSettings
	 * @return {Object} Default Settings
	 */
	getSettings: function(settings) {
		var defaultSettings = {
			element: '.scrollable-tabs:not("[data-sticky]")',
			wrapper: '.tabs-wrapper',
			scrollable: 'ul',
			prevBtn: '.scroll-link--prev',
			nextBtn: '.scroll-link--next',
			hideBtnClass: 'scroll-link--hidden',
			withArrowClass: 'scrollable-tabs--with-arrows',
			move: 50
		};

		return jQuery.extend(defaultSettings, settings);
	},

	/**
	 * Set size of current scrollable element wrapper
	 *
	 * @method setSizes
	 * @param {jQueryObject} element The current scrollable element
	 */
	setSizes: function(element) {
		var _this = this,
			wrapper = element.find(this.settings.wrapper);

		setTimeout(function() {
			var width = 2,
				scrollable = element.find(_this.settings.scrollable);

			scrollable.children().each(function() {
				width += jQuery(this).outerWidth(true);
			});

			scrollable.width(width);
			_this.resetPosition(element);

			if ((wrapper.width()) >= width) {
				_this.hideButtons(element);
				return false;
			}

			_this.setButtons(element, 0);
		}, 500);
	},

	/**
	 * Bind buttons and add swipe event
	 *
	 * @method bind
	 * @param {jQueryObject} element The current scrollable element
	 */
	bind: function(element) {
		var _this = this,
			prevBtn = element.find(this.settings.prevBtn),
			nextBtn = element.find(this.settings.nextBtn),
			inAction = CDQ.global.util.hasMouse() ? 'mouseenter' : 'touchstart',
			outAction = CDQ.global.util.hasMouse() ? 'mouseleave' : 'touchend';

		prevBtn.on(inAction, function(ev) {
			ev.preventDefault();
			_this.move(element, -1);
		});

		prevBtn.on(outAction, function(ev) {
			ev.preventDefault();
			_this.stop(element);
		});

		nextBtn.on(inAction, function(ev) {
			ev.preventDefault();
			_this.move(element, 1);
		});

		nextBtn.on(outAction, function(ev) {
			ev.preventDefault();
			_this.stop(element);
		});

		if (CDQ.global.util.isTouch()) {
			element.find(this.settings.scrollable).swipe({
				swipe: function(event, direction, distance) {
					switch (direction) {
						case 'left':
							_this.swipeScroll(element, direction, distance);
							break;
						case 'right':
							_this.swipeScroll(element, direction, distance);
							break;
						default:
					}
				},
				threshold: 10,
				excludedElements: ''
			});
		}

		jQuery(window).on('resize orientationchange', function() {
			_this.setSizes(element);
		});

		jQuery(window).on('Scrollable.SetSizes', function() {
			_this.setSizes(element);
		});
	},


	/**
	 * Move content to the right and to the left
	 *
	 * @method move
	 * @param {jQueryObject} element The current scrollable element
	 * @param {Integer} direction If -1 move to the left, if 1, move to the right
	 */
	move: function(element, direction) {
		var _this = this,
			wrapper = element.find(this.settings.wrapper),
			scrollable = element.find(_this.settings.scrollable);

		clearInterval(this.moveInterval);
		_this.moveInterval = setInterval(function() {
			var limit = scrollable.outerWidth(true) - wrapper.outerWidth(true),
				oldNl = _this.nl,
				nl = _this.getPosition(direction, limit);

			if ((direction == 1 && oldNl * -1 < limit) || (direction == -1 && oldNl < 0)) {
				if (_this.hasTransform) {
					_this.translate(scrollable, nl);
					_this.setButtons(element, nl, limit);
				}
				else {
					scrollable.animate({
						left: nl
					}, 100, function() {
						_this.setButtons(element, nl, limit);
					});
				}
			}

		}, 100);
	},

	/**
	 * Stop movement when mouse leaves the buttons
	 *
	 * @method stop
	 * @param {jQueryObject} element The current scrollable element
	 */
	stop: function(element) {
		clearInterval(this.moveInterval);
		element.find(this.settings.scrollable).stop(true, true);
	},

	/**
	 * Get the next position where the content must be moved
	 *
	 * @method getPosition
	 * @param {Integer} direction The movement direction
	 * @param {Integer} limit The limit area to move
	 * @param {Integer} [distance] If swap event is fired, add the swipe distance instead of settings.move integer
	 * @return {Integer} The new position to move to
	 */
	getPosition: function(direction, limit, distance) {
		var moveTo,
			dist = distance ? distance : this.settings.move;

		if (direction == 1) {
			moveTo = this.nl - dist;
		} else {
			moveTo = this.nl + dist;
		}


		if (moveTo > 0) {
			moveTo = 0;
		} else if (moveTo < (limit * -1)) {
			moveTo = limit * -1;
		}

		this.nl = moveTo;

		return moveTo;
	},

	/**
	 * Setup buttons opacity based on scrolling element position
	 *
	 * @method setButtons
	 * @param {jQueryObject} element The current scrollable element
	 * @param {Integer} position The scrolling element position
	 * @param {Integer} limit Limit of last element visibility
	 */
	setButtons: function(element, position, limit) {
		var prevBtn = element.find(this.settings.prevBtn),
			nextBtn = element.find(this.settings.nextBtn);

		element.addClass(this.settings.withArrowClass);

		if (position === 0) {
			prevBtn.addClass(this.settings.hideBtnClass);
			nextBtn.removeClass(this.settings.hideBtnClass);
		} else if (position <= limit * -1) {
			prevBtn.removeClass(this.settings.hideBtnClass);
			nextBtn.addClass(this.settings.hideBtnClass);
		} else {
			nextBtn.removeClass(this.settings.hideBtnClass);
			prevBtn.removeClass(this.settings.hideBtnClass);
		}
	},

	/**
	 * Move scrolling element on swipe gesture
	 *
	 * @method swipeScroll
	 * @param {jQueryObject} element The current scrollable element
	 * @param {String} direction The direction of swipe gesture. Usually "right" or "left"
	 * @param {Integer} distance The distance of swipe gesture
	 */
	swipeScroll: function(element, direction, distance) {
		var wrapper = element.find(this.settings.wrapper),
			scrollable = element.find(this.settings.scrollable),
			direct = direction == 'right' ? -1 : 1,
			limit = scrollable.width() - wrapper.width(),
			oldNl = this.nl,
			nl = this.getPosition(direct, limit, distance * 2);

		if ((direction == 'left' && oldNl * -1 < limit) || (direction == 'right' && oldNl < 0)) {
			this.translate(scrollable, nl);
			this.setButtons(element, nl, limit);
		}

	},

	/**
	 * Hide buttons
	 *
	 * @method hideButtons
	 * @param {jQueryObject} element The current scrollable element
	 */
	hideButtons: function(element) {
		element.removeClass(this.settings.withArrowClass);
		element.find(this.settings.prevBtn).addClass(this.settings.hideBtnClass);
		element.find(this.settings.nextBtn).addClass(this.settings.hideBtnClass);
	},

	/**
	 * Reset current scrollable element to it's initial position
	 *
	 * @method resetPosition
	 * @param {jQueryObject} element The current scrollable element
	 */
	resetPosition: function(element) {
		var scrollable = element.find(this.settings.scrollable);

		this.nl = 0;

		if (!this.hasTransform) {
			return scrollable.css('left', 0);
		}

		return this.translate(scrollable, 0);
	},

	/**
	 * Apply CSS3 translate rules to scrollable elements
	 *
	 * @method translate
	 * @param  {jQueryObject} element The element to apply the css property
	 * @param  {Integer} x The x position
	 */
	translate: function (element, x) {
		// translateZ(0) will avoid enable GPU aceleration on mobile/safari
		element.css({
			'-webkit-transform': 'translate(' + x + 'px, 0)',
			'-moz-transform': 'translate(' + x + 'px, 0)',
			'-ms-transform': 'translate(' + x + 'px, 0)',
			'transform': 'translate(' + x + 'px, 0)'
		});
	}
});
