/**
 * Build gateway infographic content
 *
 * @class GatewayContent
 * @constructor
 * @namespace component
 */
CDQ.component.GatewayContent = Class.extend({
	/**
	 * Class constructor
	 *
	 * @method init
	 * @param {Class} core CanvasStage core
	 */
	init: function(core) {
		this.core = core;
		this.settings = this.core.settings;
		this.element = jQuery(this.gc());
		this.wrapper = this.element.find(this.gc('wrapper'));
		this.infographic = this.element.find(this.gc('infographic'));
		this.content = this.element.find(this.gc('content'));
		this.target = jQuery(this.wrapper).find('#' + this.settings.id);
		this.animationContainer = this.target.children(':first');

		this.makeUnselectable();

		this.opened = false;
		this.scroll;

		this.setup();
	},

	/**
	 * Setup scroll, add close button and call bind method
	 *
	 * @method setup
	 */
	setup: function() {
		this.scroll = this.content.jScrollPane({
			autoReinitialise: true,
			hideFocus: true
		});

		this.content.prepend("<a class='close-button'><span class='close'>Close</span></a>");
		this.closeButton = this.content.find(".close-button");

		this.addEvent();
		this.bind();
	},

	/**
	 * Bind buttons
	 *
	 * @method bind
	 */
	bind: function() {
		var _this = this;

		this.closeButton.on('click', function() {
			_this.close();
		});
	},

	/**
	 * Add event to close content on click/touch outside that
	 *
	 * @method addEvent
	 */
	addEvent: function() {
		var _this = this,
			container = this.content.find('.jspContainer'),
			action = Modernizr.touch ? 'touchstart' : 'click';

		jQuery(document).on(action, function(ev) {
			if (_this.closeButton.is(ev.target) || _this.closeButton.find(ev.target).length > 0) {
				return false;
			}

			if (_this.opened && !container.is(ev.target) && container.find(ev.target).length === 0) {
				_this.close();
			}
		});
	},

	/**
	 * Close infographic contents
	 *
	 * @method close
	 */
	close: function() {
		this.hide();
		jQuery(window).trigger('Infographic_closeContent');
	},

	/**
	 * Show content based on its id
	 *
	 * @method show
	 * @param {String} identifier Content id
	 */
	show: function(identifier) {
		CDQ.global.screenLockEl.show();
		this.showContent(identifier);
		this.toggleContent();
	},

	/**
	 * Hide content
	 *
	 * @method init
	 * @param {Class} core CanvasStage core
	 */
	hide: function() {
		this.toggleContent();
	},

	/**
	 * Toggle content
	 *
	 * @method toggleContent
	 */
	toggleContent: function() {
		var _this = this,
			duration = 800;

		if (!this.opened) {
			_this.content.css('margin-left', -135);
			this.wrapper.animate({
				'margin-left': -218
			}, {
				duration: duration,
				queue: false,
				complete: function() {
					_this.content.animate({
						opacity: 1
					}, {
						duration: duration,
						queue: false,
						complete: function() {
							_this.opened = true;
						}
					});
				}
			});
		} else {
			this.content.animate({
				opacity: 0
			}, {
				duration: duration,
				queue: false,
				complete: function() {
					_this.content.css('margin-left', 0);

					_this.wrapper.animate({
						'margin-left': 0
					}, {
						duration: duration,
						queue: false,
						complete: function() {
							_this.opened = false;
							_this.hideContent();
							CDQ.global.screenLockEl.hide();
						}
					});
				}
			});
		}
	},

	/**
	 * Show content by id
	 *
	 * @method showContent
	 * @param {String} identifier content id
	 */
	showContent: function(identifier) {
		this.visibleContent = this.content.find('.scroll-content[data-identifier="' + identifier + '"]');
		this.visibleContent.show();
	},

	/**
	 * Hide visible content
	 *
	 * @method hideContent
	 */
	hideContent: function() {
		this.visibleContent.hide();
	},

	/**
	 * Disable text selection on older browsers
	 *
	 * @method makeUnselectable
	 */
	makeUnselectable: function() {
		if (jQuery.browser.msie && parseInt(jQuery.browser.version, 10) <= 9) {
			this.content.children().each(function() {
				jQuery(this).on({
					mousedown: function(ev) {
						if (ev.preventDefault) {
							return ev.preventDefault();
						}

						return ev.returnValue === false;
					},
					selectstart: function() {
						return false;
					}
				});
			});
		}
	},

	/**
	 * Alias to add a prefix in classes
	 *
	 * @method gc
	 * @param {String} name Class name to add a prefix
	 * @return class name with prefix
	 */
	gc: function(name) {
		var prefix = '.box-gateway';
		if (name) {
			return prefix + '-' + name;
		}

		return prefix;
	}
});
