/**
 * Util class
 *
 *	new CDQ.core.Util();
 *
 * @class Util
 * @constructor
 * @namespace core
 */
CDQ.core.Util = Class.extend({
	/**
	 * Util constructor
	 *
	 * @method init
	 */
	init: function() {
		this.checkOperationalSystem();
		this.checkFlexWrap();
		this.checkTouch();
	},

	/**
	 * Check if browser is safari and add a class to html to prevent flexWrap errors
	 *
	 * @method checkFlexWrap
	 */
	checkFlexWrap: function() {
		jQuery('.limit-text').dotdotdot({ watch:true });
	},

	checkTouch: function() {
		var _this = this;

		Modernizr.addTest('touch', function() {
			return _this.isTouch();
		});
	},

	/**
	 * Execute some modernizer tests to add some classes related to operational systems in order to apply specific CSS dor it.
	 *
	 * @method checkOperationalSystem
	 */
	checkOperationalSystem: function() {
		Modernizr.addTest('macintosh', function() {
			return !!navigator.userAgent.match(/Macintosh/i);
		});

		Modernizr.addTest('windows', function() {
			return !!navigator.userAgent.match(/windows/i);
		});

		Modernizr.addTest('linux', function() {
			return !!navigator.userAgent.match(/linux/i);
		});

	},

	/**
	 * Check if browser is IE less than 9.
	 *
	 * @method checkIE8
	 * @return {Boolean} True for ie8
	 */
	checkIE8: function() {
		return jQuery('html').hasClass('lt-ie9') ? true : false;
	},

	/**
	 * check if browser is IE less than 10
	 * @return {[type]} [description]
	 */
	checkIE9: function() {
		return jQuery('html').hasClass('lt-ie10') ? true : false;
	},

	/**
	 * Check if device is touch
	 *
	 * @method isTouch
	 * @return {Boolean} True for touch devices
	 */
	isTouch: function() {
		return !!('ontouchstart' in window);
	},

	/**
	 * Check if device has mouse
	 *
	 * @method hasMouse
	 * @return {Boolean} True for mouse devices
	 */
	hasMouse: function() {
		return !!('onmousedown' in window) && !('orientation' in window);
	},

	/**
	 * Check if device is mobile
	 *
	 * @method isMobile
	 * @return {Boolean} True for mobile device
	 */
	isMobile: function() {
		if (window.navigator.userAgent.match('Mobile')) {
			return true;
		}
		return false;
	},

	/**
	 * Check if device OS is iOS
	 *
	 * @method isIOS
	 * @param {String|Integer} version Version number to detect iOS version
	 * @return {Boolean} True if iOS
	 */
	isIOS: function(version) {
		var mainPattern = '(iPad|iPhone|iPod)',
			pattern = version ? mainPattern + ' (OS ' + version + ')' : mainPattern,
			regex = new RegExp(pattern, 'g');

		return regex.test(navigator.userAgent);
	},

	/**
	 * Check if device OS is IE and return the version
	 *
	 * @method isIE
	 * @return {Boolean} True if is IE
	 */
	isIE: function() {
		var ua = window.navigator.userAgent;

	    var msie = ua.indexOf('MSIE ');
	    if (msie > 0) {
	        // IE 10 or older => return version number
	        return parseInt(ua.substring(msie + 5, ua.indexOf('.', msie)), 10);
	    }

	    var trident = ua.indexOf('Trident/');
	    if (trident > 0) {
	        // IE 11 => return version number
	        var rv = ua.indexOf('rv:');
	        return parseInt(ua.substring(rv + 3, ua.indexOf('.', rv)), 10);
	    }

	    var edge = ua.indexOf('Edge/');
	    if (edge > 0) {
	       // Edge (IE 12+) => return version number
	       return parseInt(ua.substring(edge + 5, ua.indexOf('.', edge)), 10);
	    }

	    return false;
	},

	/**
	 * Check by feature detection if it's safari
	 *
	 * @method isSafari
	 * @return {Boolean} is safari or not
	 */
	isSafari: function() {
		return Object.prototype.toString.call(window.HTMLElement).indexOf('Constructor') > 0;
	},

	/**
	 * Get view port size
	 *
	 * @method getViewportSize
	 * @param  {Object} w Window object
	 * @return {Object} View port width and height
	 */
	getViewportSize: function(w) {
		w = w || window;

		var d = w.document;

		if (w.innerWidth != null) return {
			width: w.innerWidth,
			height: w.innerHeight
		};

		if (document.compatMode == "CSS1Compat") {
			return {
				width: d.documentElement.clientWidth,
				height: d.documentElement.clientHeight
			};
		}

		return {
			width: d.body.clientWidth,
			height: d.body.clientHeight
		};
	},

	/**
	 * Get breakpoint widths
	 *
	 * @method getBreakpoints
	 * @param  {String} breakpoint Breakpoint name
	 * @return {String|Object} If breakpoint is defined, return just breakpoint
	 * width, otherwise returns all breakpoints as an object
	 */
	getBreakpoints: function(breakpoint) {
		var breakpoints = {
			small: 480,
			medium: 600,
			largeIntermediate: 680,
			large: 768
		};

		if (breakpoint) {
			return breakpoints[breakpoint];
		}

		return breakpoints;
	},

	/**
	 * Execute a ordered fade in from a set of jQuery elements
	 *
	 * @method  linearFadeIn
	 * @param  {Object} element Description
	 * @param  {String} speed Description
	 * @param  {String} easing Description
	 * @param  {Function} callback Description
	 */
	linearFadeIn: function(element, speed, easing, callback) {
		var _this = this;

		if (element.next().length) {
			element.fadeIn(speed, easing, function() {
				_this.linearFadeIn(element.next(), speed, easing, callback);
			});
		} else {
			element.fadeIn(speed, easing, callback);
		}
	},

	/**
	 * Execute a ordered fade out from a set of jQuery elements
	 *
	 * @method  linearFadeOut
	 * @param  {Object} element Description
	 * @param  {String} speed Description
	 * @param  {String} easing Description
	 * @param  {Function} callback Description
	 */
	linearFadeOut: function(element, speed, easing, callback) {
		var _this = this;

		if (element.next().length) {
			element.fadeOut(speed, easing, function() {
				_this.linearFadeOut(element.next(), speed, easing, callback);
			});
		} else {
			element.fadeOut(speed, easing, callback);
		}
	},

	/**
	 * Set tappable for touch devices
	 *
	 * @method tappable
	 * @param {HTMLElement} element Element to apply event
	 * @param {Function} callback Callback to fire on tap
	 * @param  {Integer} delay Delay to define it is a tap
	 */
	tappable: function(element, callback) {
		/*var touchDelay = delay || 150;
		return element.tappable({
			callback: callback,
			touchDelay: touchDelay
		});*/

		return element.tap(callback);
	},

	/**
	 * Format a number to add decimals
	 *
	 * @method numberFormat
	 * @param {Integer} n Number to format
	 * @param {Integer} d Decimals to add
	 * @return {String} Formatted number
	 */
	numberFormat: function(n, d) {
		var decimals = d || 0;
		return n.toFixed(decimals);
	},

	/**
	 * Check if the current url is valid or not
	 *
	 * @method isValidUrl
	 * @param  {String} url Url to validate
	 * @return {Boolean} True for valid url
	 */
	isValidUrl: function(url) {
		var pattern = /(http|https):\/\/(\w+:{0,1}\w*@)?(\S+)(:[0-9]+)?(\/|\/([\w#!:.?+=&%@!\-\/]))?/;

		if (url && url.match(pattern)) {
			return true;
		}

		ce('## CDQ.core.Util.isValidUrl => Error: Invalid url');

		return false;
	},

	/**
	 * Slugify a String
	 *
	 * @method slugify
	 * @param  {String} string String to slugify
	 * @return {String} Slugified string
	 */
	slugify: function(string) {
		return string.replace(' ', '-').toLowerCase();
	},

	/**
	 * Check if an element is visible on viewport
	 *
	 * @method isOnScreen
	 * @param  {HTMLElement} element Element to check visibility
	 * @return {Boolean} True for element visible
	 */
	isOnScreen: function(el) {
		var element = el.get(0);
		var bounds = element.getBoundingClientRect();
		return bounds.top < window.innerHeight && bounds.bottom > 0;
	},

	/**
	 * Util method to feature toggle, depending on the release and project.
	 *
	 * @method checkRelease
	 * @param  {String} name Project name
	 * @param  {String or Float} [version] Release version
	 * @return {Boolean}
	 */
	checkRelease: function(name, version) {
		if (CDQ.global.hasOwnProperty('releaseData')) {
			if ((name == CDQ.global.releaseData.project) && (version == CDQ.global.releaseData.version || !version)) {
				return true;
			}
		}
		return false;
	},

	/**
	 * Util method to emulate checkbox on teaser icons in newsletter template
	 *
	 * @method teaserCheckbox
	 * @param  {String} el_class Class of the element
	 */
	 teaserCheckbox: function(el) {
	 	CDQ.global.util.tappable(jQuery(el), function(){
	 		if (jQuery(this).hasClass('active')) {
	 			jQuery(this).removeClass('active');
	 		} else {
	 			jQuery(this).addClass('active');
	 		}
	 	});
	 },

	 linkedProperty: function(settings) {
	 	var $element = jQuery(settings.selector),
	 		$target = jQuery(settings.target),
	 		prop = settings.prop,
	 		cache = '';

	 	function updateLink() {
	 		var val = $element.css(prop),
	 			hasChanged = cache !== val;

	 		if(hasChanged) {
				cache = val;
	 		}

			$target.css(prop, val);
	 	}

	 	jQuery(window).on('resize orientationchange', updateLink);
	 	updateLink();
	 }
});
