/**
 * Video lightbox
 *
 *	// Settings object isn't required. The example bellow shows the default values
 * 	new CDQ.component.VideoLightbox({
 * 		account: '1430472120001',
 * 		player: 'ab41f7b8-c0a7-42ce-a5d6-5d8f73ae7512',
 * 		width: 480,
 * 		height: 270,
 * 		backgroundColor: '#FFFFFF',
 * 		players: [],
 *		missingTitle: '#Error: missing title'
 * 	});
 *
 * The following HTML is necessary inside `<body>`:
 *
 *	<div class="disclaimertext--lb hide row internal-concept js-video-lightbox">
 *		<header class="row page-hl">
 *			<h5 class="h1 col-xl-8 text-highlighted"></h5>
 *		</header>
 *		<div class="content--lb">
 *			<div class="video-player-wrapper"></div>
 *		</div>
 *	</div>
 *
 * @class VideoLightbox
 * @constructor
 * @namespace component
 */
CDQ.component.VideoLightbox = Class.extend({

	/**
	 * Video lightbox constructor
	 *
	 * @method init
	 * @param {Object} settings Constructor settings
	 */
	init: function(settings) {
		/**
		* Settings property
		*
		* @property settings
		* @type Object
		* @default {
			account: '1430472120001',
			player: 'ab41f7b8-c0a7-42ce-a5d6-5d8f73ae7512',
			width: 480,
			height: 270,
			backgroundColor: '#FFFFFF',
			players: [],
			missingTitle: '#Error: missing title'
		}
		*/
		this.settings = this.getSettings(settings);
		this.playerAdded = false;

		this.setup();
	},

	/**
	 * Extend default settings and settings from constructor
	 *
	 * @method getSettings
	 * @param {Object} [settings] receive an object that contains settings to replace default ones.
	 * @return {Object} Extended Settings
	 */
	getSettings: function(settings) {
		var defaultSettings = {
			account: '1430472120001',
			player: 'ab41f7b8-c0a7-42ce-a5d6-5d8f73ae7512',
			width: 480,
			height: 270,
			backgroundColor: '#FFFFFF',
			players: [],
			missingTitle: '#Error: missing title'
		};

		return jQuery.extend(defaultSettings, settings);
	},

	/**
	 * Get video links and bind links to open dialog
	 *
	 * @method setup
	 */
	setup: function() {
		var _this = this;

		jQuery(document).on('click', 'a[data-video]', function() {
			var a = jQuery(this),
				videoID = a.data('video'),
				player = a.data('player'),
				title = a.attr('title');

			_this.addVideo(videoID, title, player);
		});

		jQuery(window).on('Video.destroy', function (ev, dialog) {
			CDQ.i.brightcovePlayers.destroyPlayers(dialog);
			_this.removeVideo(dialog);
		});
	},

	/**
	 * Add the video into dialog source
	 *
	 * @method addVideo
	 * @param  {Integer} videoID Video id key
	 * @param  {String} title Video title
	 * @param  {String} player Player key
	 */
	addVideo: function(videoID, title, player) {
		if (!this.playerAdded) {
			var dialog = jQuery('#video-dialog'),
				playerElement,
				playerInfo = this.getPlayerInfo(player);

			dialog.find('h2').html(title || this.settings.missingTitle);
			dialog.find('.video-player-wrapper').html(this.getTemplate(videoID, playerInfo, title));

			CDQ.i.popbox.open('#video-dialog')

			playerElement = dialog.find('.dynamic-player');
			this.createPlayer(playerElement);
		}
	},

	/**
	 * Get video basic HTML markup
	 *
	 * @method  getTemplate
	 * @param  {Integer} videoID Video id key
	 *
	 * @return {String} HTML markup as string
	 */
	getTemplate: function(videoID, playerInfo, title) {
		var html = [];

		html.push('<div class="print-only dynamic-player" data-video-id="' + videoID + '" data-account="' + playerInfo.account + '" data-player="' + playerInfo.player + '">')
		html.push(title)
		html.push('</div>');

		return html.join('');
	},

	/**
	 * Callback executed when dialog is closed
	 *
	 * @method removeVideo
	 */
	removeVideo: function() {
		if (this.playerAdded === true) {
			this.playerAdded = false;
		}
	},

	/**
	 * Get the player info based on the link, if it's not set, gets the default video settings
	 *
	 * @method getPlayerInfo
	 * @param  {Integer} playerID Id of the player  to the link
	 * @return {Object} Player information to the template
	 */
	getPlayerInfo: function(player) {
		for (var i in this.settings.players) {
			if (this.settings.players[i].player == player) {
				return this.settings.players[i];
			}
		}

		return {
			account: this.settings.account,
			player: this.settings.player
		};
	},

	createPlayer: function(playerElement) {
		window.videojs = undefined; // workaround for brightcove bug
		CDQ.i.brightcovePlayers.createPlayer(playerElement);
	}
});
