/**
 * Setup sticy footer
 *
 *	new CDQ.component.StickyFooter({
 *		breakPoint: 600
 *	});
 *
 * @class StickyFooter
 * @constructor
 * @namespace component
 */
CDQ.component.StickyFooter = Class.extend({
	/**
	 * Sticky Footer constructor
	 *
	 * @method init
	 * @param {Object} settings Settings that will come from constructor
	 */
	init: function(settings) {
		this.settings = settings;
		if (jQuery(window).width() <= this.settings.breakPoint) {
			this.injectFooter()
		}

	},
	/**
	 * Creates the element and appends it to the body
	 *
	 * @method injectFooter
	 */
	injectFooter: function() {
		var stickyH,
			stickyFooter = jQuery('<div class="sticky-footer-wrap"></div>');
		stickyFooter.addClass(this.settings.footerClass);
		stickyFooter.append(this.settings.footerInner);

		for (var i = 0; i < this.settings.footerEls.length; i++) {
			this.settings.footerInner.append(jQuery('.' + this.settings.footerEls[i]))
		}
		jQuery('.grid12').append(stickyFooter);
		stickyH = stickyFooter.height();
		jQuery('#main').append('<div class="sticky-spacer" style="height:' + stickyH + 'px"></div>')
	}
});
