/**
 * Donut chart widget
 *
 * @class DonutChart
 * @constructor
 * @namespace component
 * @extends core.Charts
 */
CDQ.component.DonutChart = CDQ.core.Charts.extend({
	createChart: function() {
		var _this = this;

		jQuery(this.settings.container).highcharts(this.chartData, function(chart) {
			if (!_this.settings.url && !_this.chartObj) {
				_this.setupChart(chart);
			}

			if (_this.settings.callback) {
				return _this.settings.callback(chart);
			}

			return _this.callback(chart);
		});
	},

	getChartData: function(chartData) {
		var defaultChartData = {
			colors: this.getDefaultColors(),
			credits: false,
			chart: {
				plotBackgroundColor: null,
				plotBorderWidth: null,
				plotShadow: false,
				backgroundColor: '#f4f6f7'
			},
			title: {
				text: '#{missing title}',
				align: 'left',
				useHTML: true,
				x: -10,
				style: {
					left: 0
				}
			},
			tooltip: {
				animation: false,
				pointFormat: '<b>{point.y}%</b>'
			},
			plotOptions: {
				pie: {
					allowPointSelect: false,
					cursor: 'pointer',
					dataLabels: {
						enabled: true,
						style: {
							color: '#787d82',
							fontSize: '12px',
							fontFamily: 'Arial, sans-serif'
						},
						useHTML: true,
						formatter: function() {
							return '<div class="legend-text">' + this.point.name + '<div class="legend-percentage">' + this.y + '%</div>';
						}
					},
					borderWidth: 0
				}
			},
			series: [{
				animation: false,
				type: 'pie',
				name: null,
				size: '80%',
				innerSize: '56%',
				data: []
			}]
		};

		return jQuery.extend(true, defaultChartData, chartData);
	},

	setupChart: function(obj) {
		var _this = this;
		this.chartObj = obj;
		if (obj.data) {
			this.chartData.series[0].data = obj.data;

			this.settings.callback = function(chart) {
				_this.callback(chart);
				_this.textPositioning(chart);
				_this.showTooltip(chart);
			}
			this.createChart();
		}
		else {
			this.settings.callback = function(obj) {
				_this.textPositioning(obj);
				_this.showTooltip(obj);
			}
		}

		this.bind();
	},

	/**
	 * A callback that is executed after chart is done. It centralizes the text (when it exists)
	 *
	 * @method textPositioning
	 * @param {Object} chart Built in highchart reference
	 */
	textPositioning: function(chart) {
		var container = jQuery(chart.container),
			span = container.parent().parent().find('.pie-chart-text-wrapper'),
			textX = chart.plotLeft + (chart.plotWidth * 0.5),
			textY = chart.plotTop + (chart.plotHeight * 0.5);

		span.css('left', textX);
		span.css('top', textY + (span.height() * -0.5));
	},

	/**
	 * Display tooltip when hovering legend item
	 *
	 * @method  showTooltip
	 * @param  {Object} chart Built in highchart reference
	 */
	showTooltip: function(chart) {
		jQuery(this.settings.container).find('.chart-data-legend').each(function(index, element) {
			jQuery(element).hover(function() {
				chart.tooltip.refresh(chart.series[0].data[index]);
			}, function() {
				chart.tooltip.hide();
			})
		});
	},

	/**
	 * Bind event
	 * @method bind
	 */
	bind: function() {
		var _this = this,
			screenWidth = jQuery(window).width();

		this.resizeLegend();

		jQuery(window).on('resize', function() {
			if (jQuery(window).width() != screenWidth) {
				clearTimeout(_this.resizeTimer);
				_this.resizeTimer = setTimeout(function() {
					_this.settings.callback(_this.chart || _this.chartObj);
					_this.resizeLegend();
					screenWidth = jQuery(window).width();
				}, 200);
			}
		});
	},

	/**
	 * Event fired when window resizes.
	 *
	 * @method resizeLegend
	 */
	resizeLegend: function() {
		this.changeLegend();
	},

	/**
	 * Change legend positioning according to window size. Currently it runs in from large breakpoint.
	 * Read the CSS documentation to check the breakpoint width.
	 * This method destroys and recreate the chart (only when necessary)
	 *
	 * @method changeLegend
	 */
	changeLegend: function() {
		var screenWidth = jQuery(window).width(),
			options;

		if (screenWidth <= CDQ.global.util.getBreakpoints('large')) {
			options = {
				legend: {
					enabled: true,
					layout: 'horizontal',
					align: 'center',
					verticalAlign: 'bottom',
					itemMarginBottom: 2,
					y: 17,
					useHTML: true,
					labelFormat: '<span class="chart-data-legend">{name}</span>'
				},
				plotOptions: {
					pie: {
						dataLabels: {
							enabled: false
						},
						showInLegend: true,
						point: {
							events: {
								legendItemClick: function() {
									return false;
								}
							}
						}
					}
				}
			}
		} else {
			options = this.initialData;
		}


		this.chartData = jQuery.extend(this.chartData, options);

		if (this.chart) {
			this.chart.destroy();
		}

		this.createChart();
	},

	getTextWrapper: function() {
		var container = jQuery(this.chart.container),
			wrapper = container.parent().parent().find('.pie-chart-text-wrapper');

		return wrapper;
	}
});
