/**
 * Tooltip
 *
 *	new CDQ.component.TableRowToggler();
 *
 * @class Tooltip
 * @constructor
 * @namespace component
 * @example
 * 		new CDQ.component.TableRowToggler(config).run;
 */

CDQ.component.Tooltip = Class.extend({
	init: function (settings) {
		this.settings = this.setup(settings);

		this.$body = jQuery('body');
		this.$tooltips = jQuery(this.settings.selector);
	},

	setup: function(settings) {
		var defaultSettings = {
			tooltipClass: 'tooltip',
			arrowClass: 'tooltip__arrow',
			closeBtnClass: 'tooltip__close',
			selector: '[data-tooltip]'
		};

		return jQuery.extend(defaultSettings, settings);
	},

	run: function () {
		this.$tooltips.tooltip(this.configuration());
		this.bind();
	},

	bind: function() {
		if(CDQ.global.util.isTouch()) {
			jQuery(document).on('click', this.settings.closeBtnClass, function () {
				jQuery(this).closest('.ui-tooltip').tooltip('close');
			});
		}
	},

	configuration: function () {
		var _this = this;
		return {
			tooltipClass: _this.settings.tooltipClass,
			position: {
				my: 'center top',
				at: 'center bottom+10',
				using: function(position, feedback) {
					var left,
						top = -10,
						ARROW_WIDTH = 22,
						$arrow = jQuery('<div>');

					if(feedback.element.left === 0) {
						left = feedback.target.left + (feedback.target.width / 2);
					}
					else if(feedback.element.left + feedback.element.width === jQuery(window).width()) {
						left = feedback.target.left + (feedback.target.width / 2) - feedback.element.left
					}
					else {
						left = feedback.element.width /2;
					}

					// arrow
					left -= ARROW_WIDTH / 2;

					// Position top;
					if (feedback.vertical == 'bottom') {
						top = feedback.element.height;
						$arrow.addClass('top');
					}

					jQuery(this).css(position);


					// Arrow
					$arrow
						.addClass(_this.settings.arrowClass)
						.css({left: left})
						.css({top: top})
						.appendTo(this);

					// Close Btn
					if(CDQ.global.util.isTouch()) {
						jQuery('<a href="javascript:;" class="' + _this.settings.closeBtnClass + '"></a>')
							.append(jQuery('<i class="icons-close"></i>'))
							.appendTo(this);
					}
				}
			}
		};
	}
});
