/**
 * Canvas stage main class
 *
 *	// Settings object isn't required. The example bellow shows the default values
 *	new CDQ.core.CanvasStage({
 *		id: null,
 *		target: null,
 *		width: 0,
 *		height: 0,
 *		colors: {
 *			text1: '#787d82',
 *			text2: '#ffffff',
 *			bg1: '#6e7490',
 *			bg2: '#6e7490',
 *			stage: '#f4f6f7'
 *		},
 *		data: null
 *	})
 *
 * @class CanvasStage
 * @constructor
 * @namespace core
 */
CDQ.core.CanvasStage = Class.extend({
	/**
	 * CanvasStage constructor
	 * @method init
	 * @param {Object} settings settings object
	 */
	init: function(settings) {
		/**
		 * Settings property
		 *
		 * @type {Object}
		 */
		this.settings = this.getSettings(settings);
		this.setup();
	},

	/**
	 * Setup canvas stage
	 * @method setup
	 */
	setup: function() {
		if (!CDQ.global.stage) {
			this.stage = this.getStageProperties();

			CDQ.global.stage = this.stage; // avoid duplicate

			this.target = jQuery(this.settings.target);
			this.element = this.target.append(jQuery('<div>', {
				id: this.settings.id
			}));

			this.paper = new Raphael(this.settings.id, this.settings.width, this.settings.height);
			CDQ.global.paper = this.paper;

			this.makeUnselectable(this.paper.canvas);

			this.content = new CDQ.component.GatewayContent(this);
			this.drawScreenLock();
		} else {
			this.stage = CDQ.global.stage;
			this.paper = CDQ.global.paper;
		}
	},

	/**
	 * Draw a screenlock to mask when animating
	 * @method drawScreenLock
	 */
	drawScreenLock: function() {
		var suffix = '-mask';

		this.screenLockEl = jQuery('<div>', {
			id: this.settings.id + suffix
		});
		CDQ.global.screenLockEl = this.screenLockEl;
		this.target.append(this.screenLockEl);

		this.screenLock = new Raphael(this.settings.id + suffix, this.settings.width, this.settings.height);

		this.screenLock.rect(0, 0, this.settings.width, this.settings.height).attr({
			opacity: 0,
			fill: this.settings.colors.bg1,
			stroke: 'none'
		});
	},

	/**
	 * Return settings merged
	 *
	 * @method getSettings
	 * @param  {Object} customSettings Settings from constructor
	 * @return {Object} Merged settings
	 */
	getSettings: function(customSettings) {
		var settings = {
			id: null,
			target: null,
			width: 0,
			height: 0,
			colors: {
				text1: '#787d82',
				text2: '#ffffff',
				bg1: '#6e7490',
				bg2: '#6e7490',
				stage: '#f4f6f7'
			},
			data: null
		};

		return jQuery.extend(settings, customSettings);
	},

	/**
	 * Get stage middle coordinates
	 * @method getStageProperties
	 */
	getStageProperties: function() {
		var stage = {
			xCenter: this.settings.width / 2,
			yCenter: this.settings.height / 2
		};

		return stage;
	},

	/**
	 * Draw line (freehand style)
	 *
	 * @method drawLine
	 * @param {Integer} strokeSize Stroke size in pixels
	 * @param {String} strokeColor Stroke color property to get from settings.color
	 * @param {Object} directions an object containing n arrays with directions
	 */
	drawLine: function(directions, attr) {
		var i,
			directionsLength = directions.length,
			lines = [];

		for (i = 0; i < directionsLength; i++) {
			lines.push(this.paper.path(this.getPathString(directions[i])).attr(attr));
		}

		return lines;
	},

	/**
	 * Generate a custom pathString to use in Raphael's Paper.path() method. The basic functionality is move to, line to.
	 * http://raphaeljs.com/reference.html#Paper.path
	 *
	 * @method getPathString
	 * @param {Array} values An array containing the following format: [x1, y1, x2, y2].
	 * @return {String} Generated pathString
	 */
	getPathString: function(values) {
		var string = "m";
		string += values[0] + "," + values[1];
		string += "L";
		string += values[2] + "," + values[3];

		return string;
	},

	/**
	 * Write text using Raphael.js API
	 *
	 * @method drawText
	 * @param  {String} dataProp A property from this.settings.data to get translated data.
	 * @param  {Integer} x The x position
	 * @param  {Integer} y The y position
	 * @param  {Object} attr Raphael's Paper.text() attributes
	 * @return {Object} Raphael's object reference
	 */
	drawText: function(dataProp, x, y, attr) {
		var string = this.settings.data[dataProp] ? this.settings.data[dataProp] : dataProp,
			text;

		attr.cursor = 'default';

		text = this.paper.text(x, y, string).attr(attr);

		this.makeUnselectable(text);

		return text;
	},

	/**
	 * Draw Rectangle
	 *
	 * @method drawRectangle
	 * @param  {Integer} x The x position
	 * @param  {Integer} y The y position
	 * @param  {Integer} width The element width
	 * @param  {Integer} height The element height
	 * @param  {Object} attr Raphael's Paper.rect() attributes
	 * @return {Object} Raphael's object reference
	 */
	drawRectangle: function(x, y, width, height, attr) {
		var rect = this.paper.rect(x, y, width, height).attr(attr);

		return rect;
	},

	/**
	 * Draw a path
	 * @method drawPath
	 * @param  {Object} attr Raphaeljs path attributes
	 * @return {Object} Raphaeljs object
	 */
	drawPath: function(attr) {
		var path = this.paper.path().attr(attr);

		return path;
	},

	/**
	 * Add unselectable attribute to older browsers.
	 *
	 * @method makeUnselectable
	 * @param {jQueryObject} text All text elements inside infographic
	 */
	makeUnselectable: function(text) {
		text = text.paper ? text[0] : text;

		jQuery(text).attr('unselectable', 'on').children().each(function() {
			jQuery(this).attr('unselectable', 'on');
		});
	},

	/**
	 * To be executed every time the animation of a class finishes. It calls the next animation step.
	 *
	 * @method done
	 */
	done: function() {
		jQuery(document).trigger('Gateway_Next');
	}
});
