/**
 * Tile Class
 *
 *	new CDQ.component.Tiles();
 *
 * @class Tiles
 * @constructor
 * @namespace component
 */
CDQ.component.Tiles = Class.extend({
	/**
	 * Tile Constructor
	 *
	 * @method init
	 */
	init: function(settings) {
		this.settings = this.getSettings(settings);
		this.wrapper = jQuery(this.settings.wrapper);

		this.setup();
	},

	/**
	 * Define the settings extending from the parameters received in the class
	 *
	 * @method getSettings
	 * @param  {Object} settings documented in the method init
	 * @return {Object} the extended settings that it's gonna be used
	 */
	getSettings: function(settings) {
		var defaultSettings = {
			wrapper: '.tiles',
			tiles: '.tile'
		};

		return jQuery.extend(defaultSettings, settings);
	},

	/**
	 * Check whether it's necessary to add wrappers or not for older browsers
	 *
	 * @method setup
	 */
	setup: function() {
		if (CDQ.global.util.checkIE9()) {
			this.addHelpers();
		}
	},

	/**
	 * Add wrapper to each 3 elements and remove wrapper classes to fix bugs in ie9-
	 *
	 * @method addHelpers
	 */
	addHelpers: function() {
		var tiles = this.wrapper.find(this.settings.tiles),
			classes = this.wrapper.attr('class'),
			len = tiles.length,
			wrapElement = jQuery(document.createElement('div'));

		wrapElement.addClass(classes);
		this.wrapper.removeClass(classes);

		for (var i = 0; i < len; i += 3) {
			tiles.slice(i, i + 3).wrapAll(wrapElement);
		}
	}
});
