/**
 * All filters functionalities
 *
 * 	new CDQ.component.Filters();
 *
 * @class Filters
 * @constructor
 * @namespace component
 */
CDQ.component.Filters = Class.extend({

	/**
	 * Main method constructor
	 * @param  {object} settings Custom settings sent in the constructor
	 * @method init
	 */
	init: function(settings) {
		this.settings = this.getSettings(settings);
		this.wrapper = jQuery(this.settings.wrapper);

		this.bind();
	},

	/**
	 * Receives the constructor options and overwrite if needed
	 *
	 * @param  {object} settings Custom settings
	 * @return {object} settings Default settings
	 * @method getSettings
	 */
	getSettings: function(settings) {
		return jQuery.extend({
			wrapper: '.filter',
			dropdownsWrapper: '.f-dropdown__wrapper',
			dropdown: '.f-dropdown',
			dropdownOption: '.f-dropdown__option',
			filterContainer: '.filter__container',
			filterOverlay: '.f-overlay',
			filterCategory: '.filter__category',
			filterClose: '.f-overlay__close',
			categoryAction: '.filter__cat-action',
			filterList: '.f-overlay__list',
			filterItemClass: 'f-overlay__item',
			animateDuration: 300
		}, settings);
	},

	/**
	 * Method that bind the events in the elements
	 *
	 * @method bind
	 */
	bind: function() {
		var _this = this,
			dropdownBtns = this.wrapper.find(this.settings.dropdown),
			filterClose = this.wrapper.find(this.settings.filterClose),
			filterCategory = this.wrapper.find(this.settings.filterCategory),
			dropdownOptions = this.wrapper.find(this.settings.dropdownOption),
			breakpoint = 600,
			winWidth = jQuery(window).width();

		if (CDQ.global.util.isTouch()) {
			CDQ.global.util.tappable(filterClose, function() {
				_this.toggleOverlay();
			});

			CDQ.global.util.tappable(filterCategory, function() {
				if (winWidth < breakpoint) {
					_this.getFilterInfo(this);
				}
			});

			CDQ.global.util.tappable(dropdownOptions, function() {
				jQuery(this).toggleClass('is-active');
			});

			CDQ.global.util.tappable(dropdownBtns, function() {
				if (winWidth < breakpoint) {
					return _this.activeOverlay(this);
				}
				return _this.activeDropdown(this);
			});
		}
		if(CDQ.global.util.hasMouse()) {
			filterClose.on('click', function () {
				_this.toggleOverlay();
			});

			filterCategory.on('click', function () {
				if (winWidth < breakpoint) {
					_this.getFilterInfo(this);
				}
			});

			dropdownOptions.on('click', function () {
				jQuery(this).toggleClass('is-active');
			});

			dropdownBtns.on('click', function () {
				if (winWidth < breakpoint) {
					return _this.activeOverlay(this);
				}
				return _this.activeDropdown(this);
			});
		}


		jQuery(document).on('click', '.' + this.settings.filterItemClass, function() {
			var el = jQuery(this);
			el.toggleClass('is-active');
		});

		jQuery(window).on('resize', function() {
			winWidth = jQuery(window).width();

			if (winWidth < breakpoint) {
				_this.closeActive();
			}
		});
	},

	/**
	 * Method that activate the corresponding dropdown
	 *
	 * @param  {DOM element} el The link that it was triggered
	 * @return void void It returns just to break method flow
	 * @method activeDropdown
	 */
	activeDropdown: function(el) {
		var _this = this,
			triggerLink = jQuery(el),
			target = triggerLink.data('target'),
			dropdown = jQuery('[data-dropdown="' + target + '"]'),
			height = this.contentHeight(dropdown);

		if (target && !this.animating) {
			this.animating = true;

			if (dropdown.hasClass('is-opened')) {
				return dropdown.animate({
					height: 0
				}, {
					duration: this.settings.animateDuration,
					complete: function() {
						triggerLink.removeClass('is-active');
						dropdown.removeClass('is-opened');
						_this.animating = false;
					}
				});
			}

			return this.closeActive(function() {
				dropdown.animate({
					height: height
				}, {
					duration: _this.settings.animateDuration,
					start: function() {
						triggerLink.addClass('is-active');
						dropdown.addClass('is-opened');
					},
					complete: function() {
						_this.animating = false;
					}
				});
			});
		}
	},

	/**
	 * Check whether has a active dropdown and closes it
	 *
	 * @param  {Function} cb Callback
	 * @method closeActive
	 */
	closeActive: function(cb) {
		var active = this.wrapper.find(this.settings.dropdown + '.is-active'),
			target = active.data('target'),
			dropdown = jQuery('[data-dropdown="' + target + '"]'),
			callback = cb || function() {};

		if (target) {
			dropdown.animate({
				height: 0
			}, {
				duration: this.settings.animateDuration,
				complete: function() {
					active.removeClass('is-active');
					dropdown.removeClass('is-opened');
					callback();
				}
			});
		} else {
			callback();
		}
	},

	/**
	 * Check and returns the height of the elements inside the wrapper
	 *
	 * @param  {jQuery element} Wrapper of the elements to be calculated
	 * @return {Integer} height Height of the elements
	 * @method contentHeight
	 */
	contentHeight: function(wrapper) {
		var children = wrapper.children(),
			height = 0;

		children.each(function() {
			height += jQuery(this).outerHeight(true);
		});

		return height;
	},

	/**
	 * Method that it's called for mobiles devices or smaller screens
	 *
	 * @param  {DOM element} el The link that it was triggered
	 * @method activeOverlay
	 */
	activeOverlay: function(el) {
		var _this = this,
			triggerLink = jQuery(el),
			target = triggerLink.data('target'),
			dropdown = jQuery('[data-dropdown="' + target + '"]'),
			overlay = jQuery(this.settings.filterOverlay);

		if (target) {
			this.getDropdownInfo(dropdown, overlay, function() {
				_this.toggleOverlay(overlay);
				// _this.goTop();
			});
		}
	},

	/**
	 * Toggle the visibility of the overlay
	 *
	 * @param  {DOM Element} el filterOverlay wrapper
	 * @return void void It returns just to break method flow
	 * @method toggleOverlay
	 */
	toggleOverlay: function(el) {
		var overlay = el || jQuery(this.settings.filterOverlay),
			filterList = overlay.find(this.settings.filterList);

		this.fixOverlayHeight(filterList);

		if (overlay.hasClass('opened')) {
			jQuery('body').removeClass('overlay');
			return overlay.animate({
				top: '100%'
			}).removeClass('opened');
		}

		jQuery('body').addClass('overlay');
		return overlay.animate({
			top: 0
		}).addClass('opened').css('opacity', 1);
	},

	/**
	 * Gets the info from normal filters and send to the overlay builder
	 *
	 * @param  {jQuery Element} el Triggered link
	 * @method getFilterInfo
	 */
	getFilterInfo: function(el) {
		var _this = this,
			target = jQuery(el).data('target'),
			itemsContainer = jQuery('[data-section="' + target + '"]'),
			items = itemsContainer.find('a').clone(),
			overlay = jQuery(this.settings.filterOverlay).find(this.settings.filterList);

		this.buildOverlay(items, overlay, function() {
			// _this.goTop();
			_this.toggleOverlay();
		});
	},

	/**
	 * Gets the info from the corresponding
	 * dropdown and send to the overlay builder
	 *
	 * @param  {jQuery Element} dropdown The corresponding dropdown
	 * @param  {jQuery Element} overlay The overlay so it can receive the filter options
	 * @param  {Function} cb callback
	 * @method getDropdownInfo
	 */
	getDropdownInfo: function(dropdown, overlay, cb) {
		var links = dropdown.find(this.settings.dropdownOption).clone(),
			wrapper = overlay.find(this.settings.filterList);

		this.buildOverlay(links, wrapper, cb);
	},

	/**
	 * Method responsible for getting the links from the received wrapper
	 * and build the overlay for mobile
	 *
	 * @param  {jQuery Element} links The links that needs to be cloned
	 * @param  {jQuery Element} wrapper The wrapper that is going to receive the links
	 * @param  {Function} cb callback
	 * @return void void It returns just to break method flow
	 * @method buildOverlay
	 */
	buildOverlay: function(links, wrapper, cb) {
		var _this = this,
			item = jQuery(document.createElement('li')),
			callback = cb || function() {},
			overlay;

		overlay = links.map(function() {
			return _this.buildItem(this, item);
		});

		wrapper.html(overlay.toArray().join(' '));
		callback();
	},

	/**
	 * Scroll the window to the very top of the document
	 *
	 * @method goTop
	 */
	/*goTop: function() {
		// both
		jQuery(window).scrollTop(0);
	},*/

	/**
	 * Grabs the info from the overlay builder and return
	 * the treated link item
	 *
	 * @param  {DOM Element} el A link
	 * @param  {[type} item The element that it will be transformed and returned to the list
	 * @return {String} A bunch of HTML corresponding the builded <li> item
	 */
	buildItem: function(el, item) {
		var link = jQuery(el),
			classes = link.hasClass('is-active') ? this.settings.filterItemClass + ' is-active' : this.settings.filterItemClass;

		if (link.attr('id')) {
			link.attr('id', link.attr('id') + '_filter');
		}

		link.attr('class', classes);

		return item.html(link).get(0).outerHTML;
	},

	/**
	 * Get the height of the screen and set to the
	 * filter list wrapperso the overlay can be scrollable
	 *
	 * @param  {jQuery Element} filterList The fulfilled
	 * @method fixOverlayHeight
	 */
	fixOverlayHeight: function(filterList) {
		var siblings = filterList.siblings(),
			windowHeight = jQuery(window).height();

		filterList.height(windowHeight - siblings.height());
	}
});
