CDQ.component.AutoScrollable = Class.extend({
	init: function(settings) {
		var _this = this;
		this.settings = this.getSettings(settings);
		this.hasTransform = jQuery('html').hasClass('csstransforms');
		this.index;
		this.nl = 0;
		this.translated = 0;
		this.arrowSize = 33;
		this.sizes = [];

		this.moveInterval;
		this.setSizesTimer;
		this.scrollTimer;

		jQuery(function() {
			_this.setup();
		});
	},

	getSettings: function(settings) {
		var defaultSettings = {
			element: '.scrollable-tabs-autoscroll',
			wrapper: '.tabs-wrapper',
			scrollable: 'ul',
			prevBtn: '.scroll-link--prev',
			nextBtn: '.scroll-link--next',
			hideBtnClass: 'scroll-link--hidden',
			withArrowClass: 'scrollable-tabs--with-arrows',
			automove: true,
			anchor: '[data-anchor]',
			move: 50,
			stickyHeaderWrapper: '.sticky-header-wrapper'
		};

		return jQuery.extend(defaultSettings, settings);
	},

	/**
	 * @method setup
	 * @param {jQueryObject} element The current scrollable element
	 */
	setup: function() {
		this.$element = jQuery(this.settings.element);
		this.$prevBtn = this.$element.find(this.settings.prevBtn);
		this.$nextBtn = this.$element.find(this.settings.nextBtn);
		this.$wrapper = this.$element.find(this.settings.wrapper);
		this.$scrollable = this.$element.find(this.settings.scrollable);
		this.$scrollableChildren = this.$scrollable.children();
		this.$scrollableLimit = this.$scrollable.width() - this.$wrapper.width();
		this.$anchors = jQuery(this.settings.anchor);

		this.setSizes();
		this.calcSizes();
		this.index = this.findActive().index();

		if(this.index === -1) {
			this.goTo(0);
		}

		this.bind();


		this.$prevBtn
			.addClass(this.settings.hideBtnClass);
	},

	bind: function() {
		var _this = this;

		CDQ.global.util.tappable(this.$prevBtn, function() {
			_this.moveToPrev();
		});

		CDQ.global.util.tappable(this.$nextBtn, function() {
			_this.moveToNext();
		});

		CDQ.global.util.tappable(this.$scrollableChildren, this.anchorClick.bind(this));
		/*this.$scrollableChildren.on(CDQ.global.activateAction,this.anchorClick.bind(this));*/

		if(this.settings.automove) {
			jQuery(window).scroll(this.trackCurrent.bind(this));
		}

		jQuery(window).on('resize orientationchange', function() {
			_this.setSizes();
		});

		if (CDQ.global.util.isTouch()) {
			this.$element.find(this.$scrollable).swipe({
				swipe: function(event, direction) {
					switch (direction) {
						case 'left':
							_this.moveToNext();
							break;
						case 'right':
							_this.moveToPrev();
							break;
						default:
					}
				},
				threshold: 10,
				excludedElements: ''
			});
		}
	},

	trackCurrent: function() {
		clearTimeout(this.scrollTimer);

		function apply() {
			var scroll = jQuery(window).scrollTop(),
				higherThanOffsetTop = this.anchorsOffsetTop
				.map(function(top, index) {
					if (scroll >= top) {
						return index;
					}
				});

			higherThanOffsetTop = $.grep(higherThanOffsetTop, function(i) {
				return i === 0 || i
			});

			var active = higherThanOffsetTop[higherThanOffsetTop.length - 1];

			if(active === undefined) {
				this.goTo(0);
				return;
			}

			this.setActive(active);
			this.goTo(active);
		}

		this.scrollTimer = setTimeout(apply.bind(this), 50);
	},

	anchorClick: function(event) {
		var targetIndex = jQuery(event.currentTarget).index(),
			offsetTop = this.anchorsOffsetTop[targetIndex];

		if(offsetTop) {
			jQuery('body, html').animate({scrollTop: offsetTop}, 400);
		}
	},

	findActive: function() {
		return this.$scrollableChildren.filter(function() {
			return jQuery(this).hasClass('active');
		})
	},

	setActive: function(index) {
		this.$scrollableChildren
			.removeClass('active')
			.eq(index)
			.addClass('active');
	},

	/**
	 * Set size of current scrollable element wrapper
	 *
	 * @method setSizes
	 * @param {jQueryObject} element The current scrollable element
	 */
	setSizes: function() {
		var INTERVAL = 500;

		function apply() {
			var width = 2,
				wrapperWidth = this.$wrapper.width(),
				scrollable = this.$scrollable.first();

			jQuery(scrollable).children().each(function() {
				width += jQuery(this).outerWidth(true);
			});

			this.$scrollable.width(width);

			this.resetPosition();

			if (wrapperWidth >= width) {
				this.hideButtons();
				return false;
			}

			this.setButtons(0);
		}

		this.setSizesTimer = setTimeout(apply.bind(this), INTERVAL);
	},

	calcSizes: function() {
		var _this = this;
		this.sizes = [];

		this.$scrollableChildren.each(function() {
			var item = jQuery(this),
				index = item.index(),
				width = item.width();

			_this.sizes[index] = width;
		});

		if(this.settings.automove) {
			this.anchorsOffsetTop = this.$anchors
				.map(function() {
					return parseInt(jQuery(this).offset().top - jQuery(_this.settings.stickyHeaderWrapper).outerHeight(true), 10);
				})
				.toArray();
		}
	},


	/**
	 * Hide buttons
	 *
	 * @method hideButtons
	 * @param {jQueryObject} element The current scrollable element
	 */
	hideButtons: function() {
		this.$element.removeClass(this.settings.withArrowClass);
		this.$prevBtn.addClass(this.settings.hideBtnClass);
		this.$nextBtn.addClass(this.settings.hideBtnClass);
	},


	/**
	 * Reset current scrollable element to it's initial position
	 *
	 * @method resetPosition
	 * @param {jQueryObject} element The current scrollable element
	 */
	resetPosition: function() {
		this.nl = 0;

		this.calcSizes();

		return this.goTo(this.index);
	},

	goTo: function(index) {
		var ANIMATION_INTERVAL = 50;

		clearTimeout(this.moveInterval);

		function apply() {
			this.moveFrom(this.index, index);
		}

		this.moveInterval = setTimeout(apply.bind(this), ANIMATION_INTERVAL);
	},

	getDirection: function(fromIndex, toIndex) {
		return fromIndex > toIndex ? 'right' : 'left';
	},

	moveFrom: function (fromIndex, toIndex) {
		var sizes = this.sizes.slice(0, toIndex),
			amount = sizes.reduce(function(a, b) { return a + b }, 0);

		this.index = toIndex;

		var distance = (amount - this.arrowSize) * -1; // var distance = -amount - this.arrowSize;

		if(toIndex === 0) {
			distance = 0;
		}

		var limit = this.getLimit();

		if (distance < limit*-1) {
			distance = limit*-1;
		}

		this.setButtons(toIndex, this.getLimit(), distance);
		this.translate(distance);
	},

	moveToPrev: function() {
		this.moveFrom(this.index, this.index - 1);
	},

	moveToNext: function() {
		this.moveFrom(this.index, this.index + 1);
	},

	/**
	 * Setup buttons opacity based on scrolling element position
	 *
	 * @method setButtons
	 * @param {jQueryObject} element The current scrollable element
	 * @param {Integer} position The scrolling element position
	 * @param {Integer} limit Limit of last element visibility
	 */
	setButtons: function(position, limit, amount) {
		var hideBtnClass = this.settings.hideBtnClass;
		this.$element.addClass(this.settings.withArrowClass);

		if (position === 0) {
			this.$prevBtn.addClass(hideBtnClass);
			this.$nextBtn.removeClass(hideBtnClass);
		} else if (amount <= (limit * -1) || position == (limit * -1)) {
			this.$prevBtn.removeClass(hideBtnClass);
			this.$nextBtn.addClass(hideBtnClass);
		} else {
			this.$nextBtn.removeClass(hideBtnClass);
			this.$prevBtn.removeClass(hideBtnClass);
		}
	},


	/**
	 * Apply CSS3 translate rules to scrollable elements
	 *
	 * @method translate
	 * @param  {jQueryObject} element The element to apply the css property
	 * @param  {Integer} x The x position
	 */
	translate: function (x) {
		this.translated = x;

		if(!this.hasTransform) {
			return this.$scrollable.css('left', x);
		}

		this.$scrollable.css({
			'-webkit-transform': 'translate(' + x + 'px, 0) translateZ(0)',
			'-moz-transform': 'translate(' + x + 'px, 0) translateZ(0)',
			'-ms-transform': 'translate(' + x + 'px, 0)', // ie doesn't support translateZ
			'transform': 'translate(' + x + 'px, 0) translateZ(0)'
		});
	},

	getLimit: function() {
		return this.$scrollable.width() - this.$wrapper.width();
	}
});
