/**
 * Main menu functionalities for older browsers and touch devices
 *
 * 	new CDQ.component.MainMenu();
 *
 * @class MainMenu
 * @constructor
 * @namespace component
 */
CDQ.component.MainMenu = Class.extend({
	/**
	 * Main menu class constructor
	 *
	 * @method init
	 */
	init: function() {
		this.activateBtn = jQuery('.nav-caller');
		this.mainNavigation = jQuery('.main-navigation');
		this.mainNavigationItem = jQuery('.main-navigation-item');
		this.mainNavigationItemActive = jQuery('.main-navigation-item.active');
		this.mainNavigationLinks = this.mainNavigationItem.find('> a');
		this.subMenueLis = jQuery('.sub-menu').find('> li');
		this.subMenueInitActive = jQuery('.sub-menu').find('> li.init-act');
		this.setupTouch();
		this.responsiveLink = jQuery('[data-responsive-link]');
		this.addActiveSubNav();

		this.bind();
	},

	/**
	 * Bind events
	 *
	 * @method bind
	 */
	bind: function() {
		var _this = this,
			win = jQuery(window);

		win.on('resize', function() {
			_this.setupTouch();
			if (!CDQ.global.util.isTouch()) {
				_this.closeMenu();
			}
		});

		this.mainNavigationLinks.on('click', function () {
			var viewPortSize = CDQ.global.util.getViewportSize();
			if (viewPortSize.width <= CDQ.global.util.getBreakpoints('large')) {
				return false;
			}
		});

		this.mainNavigationItem.on('mouseenter', function() {
			_this.checkPosition(jQuery(this));
			jQuery(window).trigger('Search.resetInput');
		});

		jQuery(window).on('MainMenu.reset', function() {
			_this.closeMenu();
		});

		CDQ.global.util.tappable(this.activateBtn, function() {
			var btn = jQuery(this),
				target = jQuery(btn.data('target')),
				viewPortSize = CDQ.global.util.getViewportSize();

			_this.activateBtn.not(btn).removeClass('active');
			_this.mainNavigation.not(target).removeClass('active');

			if (viewPortSize.width <= 768) {
				target.find('[class="active"]').removeClass('active');

				if (!btn.hasClass('active')) {
					target.addClass('active');
					btn.addClass('active');
				} else {
					target.removeClass('active');
					btn.removeClass('active');
				}

				jQuery(window).trigger('Search.resetInput');

				return false;
			}
		});

		CDQ.global.util.tappable(this.mainNavigationLinks, function(ev) {
			var viewPortSize = CDQ.global.util.getViewportSize();
			if (viewPortSize.width <= 768) {
				var el = jQuery(this).parent();

				_this.mainNavigationItem.not(el).removeClass('active');

				if (el.hasClass('active')) {
					el.removeClass('active');
					window.location = this.href;
				} else {
					el.addClass('active');
					ev.preventDefault();
					ev.stopPropagation();
				}
			}
		});

		// DACL-1023
		this.responsiveLink.click(function(event) {
			var viewPortSize = CDQ.global.util.getViewportSize();
			if (viewPortSize.width > 768) {
				return event.preventDefault();
			}
			window.location.href = jQuery(this).data('responsiveLink')
		});
	},

	/**
	 * Setup touch events to provide a touch friendly navigation
	 *
	 * @method addActiveSubNav
	 */
	addActiveSubNav: function() {
		var _this = this;

		this.subMenueLis.on('mouseenter', function() {
			_this.subMenueLis.removeClass('init-act')
		});
		this.mainNavigation.on('mouseleave', function() {
			_this.subMenueInitActive.addClass('init-act')
		});
	},
	/**
	 * Setup touch events to provide a touch friendly navigation
	 *
	 * @method setupTouch
	 */
	setupTouch: function() {
		var viewPortSize = CDQ.global.util.getViewportSize();

		this.mainNavigationItem.removeClass('active');

		if (viewPortSize.width >= 768) {
			this.mainNavigationItemActive.addClass('active');
		}
	},

	/**
	 * Open main navigation menu
	 *
	 * @method openMenu
	 */
	openMenu: function() {
		this.mainNavigation.addClass('active');
		this.activateBtn.addClass('active');
	},

	/**
	 * Close main navigation menu
	 *
	 * @method closeMenu
	 */
	closeMenu: function() {
		this.mainNavigation.removeClass('active');
		this.activateBtn.removeClass('active');
	},


	/**
	 * When hovered, check if the position of the navigation item
	 * doesn't pass the wrapper
	 *
	 * @param  {jQuery Object} navItem Navigation Item
	 * @method checkPosition
	 */
	checkPosition: function(navItem) {
		var navItemX = navItem.offset().left,
			nav = navItem.closest('.main-menu-wrapper'),
			navX = nav.offset().left,
			navSubMenu = navItem.find('.sub-menu-wrapper');

		if (navItemX + navSubMenu.width() > navX + nav.width()) {
			navSubMenu.css('right', 0);
		}
	}
});
