/**
 * Toggle class
 *
 *	new CDQ.component.Toggle();
 *
 * @example
 *	<p class="wrapper-class toggle-animation">
 *		Lorem ipsum dolor sit amet, consectetur adipisicing elit.
 *		Dignissimos sapiente, deserunt sed quae enim eum numquam!
 *		Deleniti accusamus ipsam sequi ipsum ratione nobis ad, blanditiis,
 *		dolorum explicabo dignissimos alias sed.
 *	</p>
 *
 *	<a href="javascript:;" class="toggle" data-target=".wrapper-class" data-shown="Hide message" data-hidden="Show message">Show message</a>
 *
 * or
 *
 * @example
 *	<p class="wrapper-class toggle-animation">
 *		Lorem ipsum dolor sit amet, consectetur adipisicing elit.
 *		Dignissimos sapiente, deserunt sed quae enim eum numquam!
 * 		<span class="marker"></span>
 *		Deleniti accusamus ipsam sequi ipsum ratione nobis ad, blanditiis,
 *		dolorum explicabo dignissimos alias sed.
 *	</p>
 *
 *	<a href="javascript:;" class="toggle" data-target=".wrapper-class" data-marker=".marker" data-shown="Hide message" data-hidden="Show message" data-max-height="2000px">Show message</a>
 *
 * * data-target - Element that holds the text (it will hold the size limit too)
 * * data-shown - Text that should appear when text is visible
 * * data-hidden - Text that should appear when text is hidden
 * * data-marker - Will set max-height depending on the marker, don't use the class to hold the size limit
 * * data-max-height - Will define tha max-height value. Default 9999px
 * * class="toggle" - defined class to every toggle
 *
 * @class Toggle
 * @constructor
 * @namespace component
 */

CDQ.component.Toggle = Class.extend({
	/**
	 * Toggle Constructor
	 *
	 * @method init
	 */
	init: function() {
		var _this = this,
			togglers = jQuery('.toggle');

		togglers.each(function() {
			var settings = _this.config(jQuery(this));

			_this.setup(settings);
			_this.bind(settings);
		});
	},

	/**
	 * Setup method responsible for setting up
	 * the toggle with the text and animation
	 *
	 * @method setup
	 * @param {Object} settings The settings of a single toggle element
	 */
	setup: function(settings) {
		if (settings.marker) {
			this.checkMarker(settings);
		}
		if (settings.hiddenText && settings.shownText) {
			this.setText(settings);
		}

		settings.target.addClass('toggle-animation');
	},

	/**
	 * Check whether the component
	 * initial state is active or not
	 *
	 * @method checkMarker
	 * @param {Object} settings The settings of a single toggle element
	 */
	checkMarker: function(settings) {
		if (!settings.target.hasClass(settings.className)) {
			this.hideToMarker(settings);
		}
	},

	/**
	 * Calculate the height of the
	 * element based on the marker element
	 *
	 * @method hideToMarker
	 * @param {Object} settings The settings of a single toggle element
	 */
	hideToMarker: function(settings) {
		var marker = settings.target.find(settings.marker),
			visibleHeight = marker.offset().top - settings.target.offset().top;

		settings.target.css({
			maxHeight: visibleHeight,
			overflow: 'hidden'
		});

		this.setText(settings);
	},

	/**
	 * Set a longer max height so the CSS
	 * can work
	 *
	 * @method showBeyondMarker
	 * @param {Object} settings The settings of a single toggle element
	 */
	showBeyondMarker: function(settings) {
		settings.target.css({
			maxHeight: settings.maxHeight
		});

		this.setText(settings);
	},

	/**
	 * Set the first state of the element (shown or hidden)
	 *
	 * @method setText
	 * @param {Object} settings The settings of a single toggle element
	 */
	setText: function(settings) {
		if (settings.target.hasClass(settings.className)) {
			settings.toggle.text(settings.shownText);
		} else {
			settings.toggle.text(settings.hiddenText);
		}
	},

	/**
	 * Responsible for define the toggle options defined in the HTML
	 *
	 * @method config
	 * @param {jQuery Element} toggle Single toggle
	 * @return {Object} obj Toggle information grabed from HTML Element
	 */
	config: function(toggle) {
		return {
			toggle: toggle,
			target: jQuery(toggle).siblings(toggle.data('target')),
			className: toggle.data('class') || 'active',
			hiddenText: toggle.data('hidden') || null,
			shownText: toggle.data('shown') || null,
			marker: toggle.data('marker') || null,
			maxHeight: toggle.data('max-height') || '9999px'
		}
	},

	/**
	 * Responsible for binding the events to the elements
	 *
	 * @method bind
	 * @param {jQuery Element} toggle Single toggle
	 */
	bind: function(settings) {
		var _this = this;

		settings.toggle.on(CDQ.global.activateAction, function() {
			_this.toggle(settings);
		});
	},

	/**
	 * Delegate the toggle depending on the settings
	 *
	 * @method toggle
	 * @param {Object} settings The settings of a single toggle element
	 */
	toggle: function(settings) {
		if (settings.marker) {
			return this.toggleMarker(settings);
		}
		return this.toggleClass(settings);
	},

	/**
	 * Toggle state of the element based
	 * on the classes passed as argument in the HTML with marker
	 *
	 * @method toggleMarker
	 * @param {Object} settings The settings of a single toggle element
	 */
	toggleMarker: function(settings) {
		if (!settings.target.hasClass(settings.className)) {
			settings.target.addClass(settings.className);
			this.showBeyondMarker(settings)
		} else {
			settings.target.removeClass(settings.className);
			this.hideToMarker(settings);
		}
	},

	/**
	 * Toggle state of the element based
	 * on the classes passed as argument in the HTML
	 *
	 * @method toggleMarker
	 * @param {Object} settings The settings of a single toggle element
	 */
	toggleClass: function(settings) {
		if (!settings.target.hasClass(settings.className)) {
			settings.target.addClass(settings.className);
			this.changeText(settings, true);
		} else {
			settings.target.removeClass(settings.className);
			this.changeText(settings);
		}
	},

	/**
	 * Change the text of the link and change elements classe to hide/show
	 *
	 * @method changeText
	 * @param {Object} settings The settings of a single toggle element
	 * @param {Boolean} active A boolean to check if it's active or not
	 */
	changeText: function(settings, active) {
		if (settings.hiddenText && settings.shownText) {
			if (active) {
				settings.toggle.text(settings.shownText);
			} else {
				settings.toggle.text(settings.hiddenText);
			}
		}
	}
})
