/**
 * Tabs functionalities for some tabs components
 *
 *	// Settings object isn't required. The example bellow shows the default values
 *	new CDQ.component.Tabs({
 *		container: '.tabs-content'
 *	});
 *
 * @class Tabs
 * @constructor
 * @namespace component
 */

CDQ.component.Tabs = Class.extend({
	/**
	 * Tabs class constructor
	 *
	 * @method init
	 * @param {Object} settings Settings defined on constructor
	 */
	init: function(settings) {
		this.tabs = jQuery('.tabs');
		this.tabsLinks = this.tabs.find('a');

		this.settings = this.getSettings(settings);

		this.bind();
	},

	/**
	 * Merge default settings with current settings
	 *
	 * @method getSettings
	 * @param  {Object} settings Constructor settings
	 * @return {Object} Merge default with constructor settings
	 */
	getSettings: function(settings) {
		var defaultSettings = {
			container: '.tabs-content'
		};

		return jQuery.extend(defaultSettings, settings);
	},

	/**
	 * Bind events
	 *
	 * @method bind
	 */
	bind: function() {
		var _this = this;

		if (CDQ.global.util.isTouch()) {
			CDQ.global.util.tappable(this.tabsLinks, function(ev) {
				_this.show(ev);
			});
		} else {
			this.tabsLinks.on('click', function(ev) {
				_this.show(ev);
				return false;
			});
		}
	},

	/**
	 * Show active tab component
	 *
	 * @method show
	 * @param {Object} ev Current event captured from tab anchor
	 */
	show: function(ev) {
		if (this.settings.show) {
			return this.settings.show(ev);
		}

		var element = jQuery(ev.target),
			target = element.attr('href'),
			tabs = jQuery(this.settings.container).children();

		if (!target.match('javascript')) {
			tabs.addClass('hide');

			jQuery(target).removeClass('hide');

			this.tabsLinks.parent().removeClass('active');

			element.parent().addClass('active');

			jQuery(window).trigger('ResponsiveTable.adjust');
		}


		return false;
	}
});
