/**
 * TargetToggler
 * 		generic component to show elements when clicked
 *
 *	// Settings object isn't required. The example bellow shows the default values
 *	new CDQ.component.TargetToggler({
 *		btn: 'data-target',
 *   	target: 'data-target-id'
 *	});
 *	 *
 * @class TargetToggler
 * @constructor
 * @namespace component
 * @example
 * <a href="#" data-target="1">click me</a>
 * <p data-target-id="1">Lorem !</p>
 */
CDQ.component.TargetToggler = Class.extend({
	init: function(settings) {
		this.settings = this.getSettings(settings || {});
		this.setup();
	},

	getSettings: function(settings) {
		var defaultSettings = {
			btn: 'data-target',
			target: 'data-target-id',
			when: 'data-target-when',
			icons: false,
			iconOpen: '',
			iconClose: ''
		};

		return jQuery.extend(defaultSettings, settings);
	},

	setup: function() {
		this.btns = jQuery('['+ this.settings.btn +']');
		this.targets = jQuery('['+ this.settings.target +']');
		this.whens = jQuery('[' + this.settings.when + ']');
		this.icons = this.settings.icons;
		if(this.icons) {
			this.iconOpen = this.settings.iconOpen;
			this.iconClose = this.settings.iconClose;
		}
	},

	run: function() {
		this.animate('hidde', this.targets);
		this.watchIt();
	},

	animate: function(type, element) {
		var animation = '';
		var options = 'fast';

		if(type === 'show') {
			animation = 'fadeIn'
		}
		if(type === 'hidde') {
			animation = 'fadeOut';
		}
		if(type === 'toggle') {
			animation = 'fadeToggle'
		}

		return element[animation](options)
	},

	handleInteraction: function(event) {
		event.preventDefault();
		var targetName = jQuery(event.currentTarget).data().target;
		var targetId = jQuery('[data-target-id="' + targetName + '"]');
		var target = jQuery('[data-target="' + targetName + '"]');
		this.animate('toggle', targetId);
		if(this.icons){
			this.handleIcons(target);
		}
	},

	watchIt: function() {
		var self = this;
		this.btns.each(function(_, element) {
			var elm = jQuery(element);
			var when = jQuery(element).data().targetWhen || 'click touchstart';
			elm.on(when, self.handleInteraction.bind(self));
		});
	},

	handleIcons: function(td) {
		var link = td.children('.'+this.iconOpen);

		if(link.hasClass(this.iconOpen)){
			link.removeClass(this.iconOpen);
			link.addClass(this.iconClose);
		}else{
			link = td.children('.'+this.iconClose);
			link.removeClass(this.iconClose);
			link.addClass(this.iconOpen);
		}
	}
});
