CDQ.component.Form = Class.extend({
	init: function(settings) {
		this.settings = this.getSettings(settings);
		this.setup();
		this.bind();
	},

	getSettings: function(settings) {
		return jQuery.extend({
			formWrapper: jQuery('.registration-form'),
			formElements: '.form-field',
			submit: jQuery('.submit-form'),
			errorMessage: jQuery('.form__error-message')
		}, settings);
	},

	setup: function() {
		var _this = this;
		var formFields = this.settings.formWrapper.find(this.settings.formElements);

		window.Parsley.addAsyncValidator('unique', function(xhr) {
			return xhr.responseJSON.valid;
		});

		this.form = formFields.parsley({
			namespace: 'data-vl-',
			successClass: 'is-valid',
			errorClass: 'is-invalid',
			classHandler: function(data) {
				// damn jsii
				return _this.classHandler(data);
			}
		});
	},

	bind: function() {
		var _this = this;

		window.CDQ.global.util.tappable(this.settings.submit, function() {
			if (!_this.validate()) {
				_this.settings.errorMessage.addClass('is-visible');
				return false;
			}
			_this.settings.errorMessage.removeClass('is-visible');
		});
	},

	classHandler: function(data) {
		return data.$element.closest('.form__group');
	},

	validate: function() {
		var i;
		// to show errors one by one, just remove valid
		// variable and return false inside the loop
		var valid = true;
		var len = this.form.length;

		for (i = 0; i < len; i++) {
			// strict verify
			if (true !== this.form[i].validate()) {
				valid = false;
			}
		}

		return valid;
	}
});
