/**
 * Sticky header element
 *
 *	new CDQ.component.StikyHeader();
 *
 * @class  StickHeader
 * @constructor
 * @namespace component
 */
CDQ.component.StickyHeader = Class.extend({
	/**
	 * Sticky header constructor
	 *
	 * @method init
	 */
	init: function(settings) {
		this.settings = this.getSettings(settings);
		this.main = jQuery('body .center-block:last-child');
		this.element = jQuery(this.settings.wrapper);
		this.parent = this.element.parent();
		this.elementY = this.element.offset().top;
		this.elementBottom = this.element.offset().top + this.element.outerHeight(true);
		this.topSpacing = 32;
		this.htmlBody = jQuery('html, body');
		this.positions = jQuery('.sticky-position');
		this.minHeight = 400;

		this.lastItem = null;

		this.setup();
	},

	getSettings: function(settings) {
		var defaultSettings = {
			wrapper: '.sticky-header-wrapper',
			tabsSelector: '.sticky-header-wrapper .scrollable-tabs-arrowed',
			callback: function(){}
		};

		return jQuery.extend(defaultSettings, settings);
	},

	/**
	 * Setup sticky header creating a detached copy of sticky header fixed element
	 *
	 * @method setup
	 */
	setup: function() {
		this.detachedElement = this.element.clone();
		this.detachedElement.addClass('detached');

		this.main.append(this.detachedElement);
		this.tabs = jQuery(this.settings.tabsSelector);
		this.bind();
	},

	/**
	 * Bind events
	 *
	 * @method bind
	 */
	bind: function() {
		var _this = this,
			tabsLinks = this.tabs.find('ul li a');

		jQuery(window).on({
			scroll: function(event) {
				_this.sticky(event);
			},
			touchmove: function(event) {
				_this.sticky(event);
			},
			resize: function() {
				_this.detachedElement.css({
					width: _this.parent.width()
				});

				if (!_this.detachedElement.hasClass('sticky-header-visible')) {
					_this.detachedElement.css({
						top: _this.detachedElement.outerHeight(true) * 2 * -1
					});
				}

				_this.sticky();
			}
		});

		if (!CDQ.global.util.isTouch()) {
			tabsLinks.on('click', function() {
				_this.goTo(jQuery(this).attr('href'));
				return false;
			});
		} else {
			CDQ.global.util.tappable(tabsLinks, function() {
				_this.goTo(jQuery(this).attr('href'))
			});
		}

		this.detachedScrollable = new CDQ.component.Scrollable({
			element: this.detachedElement.find('.scrollable-tabs')
		});

		this.detachedElement.css({
			width: this.parent.width(),
			top: this.detachedElement.outerHeight(true) * 2 * -1
		});

		jQuery(window).on('Sticky.goTo', function() {
			if (window.location.hash) {
				_this.goTo(window.location.hash.replace('#!', '#'));
			}
		});

		jQuery(window).on('load', function() {
			jQuery(window).trigger('Sticky.goTo');
		});
	},

	/**
	 * Attach/Detach sticky header on page scroll
	 *
	 * @method sticky
	 */
	sticky: function(event) {
		var scrollTop = jQuery(document).scrollTop(),
			wH = jQuery(window).height();

		this.scrollSpy(scrollTop, event);

		if (wH <= this.minHeight) {
			this.attach();
			return false;
		}

		if (scrollTop >= (this.elementBottom - this.detachedElement.outerHeight(true))) {
			return this.detach();
		}

		return this.attach();
	},

	/**
	 * Dettach sticky header element
	 *
	 * @method detach
	 */
	detach: function() {
		if (this.detachedElement.hasClass('sticky-header-visible')) {
			return false;
		}

		this.detachedElement.addClass('sticky-header-visible')
		this.detachedElement.finish();

		this.detachedElement.animate({
			top: 0
		});

		this.settings.callback();
	},

	/**
	 * Attach sticky header element
	 *
	 * @method attach
	 */
	attach: function() {
		if (!this.detachedElement.hasClass('sticky-header-visible')) {
			return false;
		}

		var _this = this;

		this.detachedElement.finish();
		this.detachedElement.animate({
			top: this.detachedElement.outerHeight(true) * 2 * -1
		}, function() {
			_this.detachedElement.removeClass('sticky-header-visible');
		});
	},

	/**
	 * Go to selected section
	 *
	 * @method goTo
	 * @param {String} element Target name
	 */
	goTo: function(target) {
		var targetElement = jQuery('a[name="' + target.replace('#', '') + '"]'),
			top;

		if (targetElement.length > 0) {
			top = targetElement.offset().top - this.detachedElement.height();
			this.htmlBody.finish();
			this.htmlBody.animate({
				scrollTop: top - this.topSpacing
			}, 1000);
		}

		return false;
	},

	/**
	 * Spy scroll event to activate menu according to current section
	 *
	 * @method scrollSpy
	 * @param {Integer} top current scroll top position
	 */
	scrollSpy: function(top, event) {
		var _this = this,
			fromTop = top + this.detachedElement.height(),
			current = this.positions.map(function() {
				if (jQuery(this).offset().top - (_this.topSpacing + 1) < fromTop) {
					return this;
				}
			}),
			target;

		current = current[current.length - 1];
		target = current ? current.name : "";
		var name = jQuery(current).attr('name');

		if (this.lastItem !== target) {
			this.lastItem = target;

			this.tabs.find('ul li a').parent().removeClass("active");
			this.tabs.find("[href=#" + target + "]").parent().addClass("active");
			jQuery(window).trigger('StickyHeader.currentActive', [name, event]);
		}

	}
});
