/**
 * Build gateway infographic arrows
 *
 * @class GatewayArrows
 * @constructor
 * @namespace component
 * @extend core.CanvasStage
 */
CDQ.component.GatewayArrows = CDQ.core.CanvasStage.extend({
	/**
	 * GatewayArrows constructor
	 * @method init
	 * @param {Object} settings Settings object
	 * @param {Object} parent GatewayInfographic methods and properties
	 */
	init: function(settings, parent) {
		this.inherited().init(settings);
		this.parent = parent;

		this.arrowHeight = 40;
		this.set = this.paper.set();
		this.circles = this.parent.circles.set.circles;
		this.arrowsData = this.getArrowsData();
		this.initialTop;
		this.drawArrow();
	},

	/**
	 * Draw an arrow after circles
	 * @method drawArrow
	 */
	drawArrow: function() {
		var _this = this,
			arrow,
			i,
			data,
			path;

		for (arrow in this.arrowsData) {
			if (this.arrowsData.hasOwnProperty(arrow)) {
				data = this.arrowsData[arrow];

				for (i in data) {
					if (data.hasOwnProperty(i)) {
						path = this.getArrow(data[i], arrow);
					}
				}
			}
		}

		this.set.animate({
			opacity: 1
		}, 500, function() {
			_this.parent.buttons.animateIcons();
			_this.done();
		});
	},

	/**
	 * Build the arrow object/path
	 * @method getArrow
	 * @param {Array} arr Arrow coordinates
	 * @param {String} color Arrow color
	 * @return {RaphaelJSSet} arrow The arrow set
	 */
	getArrow: function(arr, color) {
		var x = arr[0],
			y = arr[1],
			h = y + this.arrowHeight,
			s = 6,
			sW = 2,
			attr = {
				stroke: this.settings.colors[color],
				'stroke-width': sW,
				'stroke-linecap': 'round',
				opacity: 0
			},
			arrow = this.paper.set();

		arrow.push(
			this.paper.path(["M", x, y, "L", x, h]).attr(attr),
			this.paper.path(["M", x + s, h - s, "L", x, h]).attr(attr),
			this.paper.path(["M", x - s, h - s, "L", x, h]).attr(attr)
		);

		this.set.push(arrow);
		CDQ.global.infograficColorsSet[color].push(arrow);
		this.set.toBack();

		/*if (arr[2]) {
			this.rotateArrow(arrow, arr[2]);
		}*/

		return arrow;
	},

	/**
	 * Get arrows data
	 * @method getArrowsData
	 * @return {Object} obj Returns an object containing 2 different colors.
	 */
	getArrowsData: function() {
		var i,
			circle,
			bbox,
			cbb = {},
			obj;

		for (i = 0; i < this.circles.length; i++) {
			circle = this.circles[i];
			bbox = circle.getBBox();

			cbb['c' + (i + 1)] = bbox;
		}

		this.initialTop = (cbb.c1.y + cbb.c1.height + 1);

		obj = {
			bg1: {
				'c2': [this.getMiddle(cbb.c2) + (cbb.c2.width / 4), this.initialTop],
				'c3': [this.getMiddle(cbb.c3), this.initialTop],
				'c4': [this.getMiddle(cbb.c4), this.initialTop]
			},
			bg2: {
				'c1': [this.getMiddle(cbb.c1), this.initialTop],
				'c2': [this.getMiddle(cbb.c2) - (cbb.c2.width / 4), this.initialTop]
			}
		};

		return obj;
	},

	/**
	 * Get the x middle of a canvas path
	 * @method getMiddle
	 * @param {Object} bbox Element BBox
	 * @return {Integer} middle x
	 */
	getMiddle: function(bbox) {
		return bbox.x + (bbox.width / 2);
	},

	/**
	 * Rotates arrow in order to keep the format
	 * @method rotateArrow
	 * @param {RaphaelJSSet} arrow Arrow set
	 * @param {Integer} angle Angle in degrees
	 */
	rotateArrow: function(arrow, angle) {
		var setBBox = arrow.getBBox(),
			l_coord = setBBox.x,
			r_coord = setBBox.x2,
			t_coord = setBBox.y,
			b_coord = setBBox.y2,
			cx = (l_coord + r_coord) / 2,
			cy = (t_coord + b_coord) / 2;

		arrow.rotate(angle, cx, cy);
	}
});
