/**
 * A generic component to a simple sticky element
 *
 * @class Sticky
 * @constructor
 * @namespace component
 */
CDQ.component.Sticky = Class.extend({
	/**
	 * Sticky Footer constructor
	 *
	 * @method init
	 * @param {Object} settings Settings that will come from constructor
	 */
	init: function(settings) {
		this.settings = this.getSettings(settings);
		this.bind();
	},

	getSettings: function(settings) {
		return jQuery.extend({
			element: jQuery('.sticky-actions'),
			className: 'sticky-actions--static',
			rule: 'bottom'
		}, settings);
	},

	bind: function() {
		var _this = this;

		jQuery(document).scroll(function() {
			_this.check();
		})
	},

	getCurrentY: function() {
		if (this.settings.rule === 'top') {
			return jQuery(document).scrollTop();
		}
		return jQuery(document).scrollTop() + window.innerHeight;
	},

	check: function() {
		var elParent = this.settings.element.parent();
		var elParentTop = elParent.offset().top + this.settings.element.outerHeight();
		var currentY = this.getCurrentY();

		if (currentY >= elParentTop) {
			this.settings.element.addClass(this.settings.className);
		} else {
			this.settings.element.removeClass(this.settings.className);
		}
	}
});
