CDQ.component.SuggestorSearch = CDQ.component.AutoCompleteSearch.extend({
	getSettings: function(settings) {
		var defaultSettings = {
			resultSelector: '.autocomplete-search-result',
			url: null,
			sourceData: {},
			labels: {
				results: '#{missing translation results}',
				singleResult: '#{missing translation singleResult}',
				seeAll: '#{missing translation seeAll}'
			},
			searchCenterLink: '/Search/{st}/Something',
			input: '.search-text',
			button: '.btn-search',
			mainMenu: '.main-navigation',
			onHTMLReady: function() {}
		}

		return jQuery.extend(true, defaultSettings, settings);
	},
	/**
	 * Setup the autocomplete widget (requires jquery ui autocomplete)
	 *
	 * @method setup
	 */
	setup: function() {
		var _this = this,
			source = this.settings.sourceData;

		jQuery.widget("custom.catcomplete", jQuery.ui.autocomplete, {
			_create: function() {
				this._super();
				this.widget().menu("option", "items", "> :not(.ui-menu-item-results)");
			},
			_renderMenu: function(ul, items) {
				var _this = this;

				jQuery.each(items, function(index, item) {
					jQuery.each(item.Records, function(n, record) {
						_this._renderItemData(ul, record, item);

						if (items.length - 1 == index && item.Records.length - 1 == n) {
							// _this._renderItemData(ul, record, item, true);
							_this._renderTotalItem(ul, record, item);
						}
					});
				});
			},

			_renderItemData: function(ul, record, item, last) {
				if (last) {
					return this._renderItem(ul, record, item, true);
				}

				return this._renderItem(ul, record, item);
			},

			_renderItem: function(ul, record, item) {
				var li,
					a,
					divl,
					divr;

				a = jQuery('<a></a>')
					.attr('href', record.Link).
				addClass('clearfix');
				divl = jQuery('<div></div>').
				addClass('col-3-4')
					.text(record.Title);
				divr = jQuery('<div></div>')
					.addClass('col-1-4')
					.html(item.Name);

				a.append(divl).append(divr);

				li = jQuery('<li></li>')
					.addClass('ui-search-item')
					.append(a)
					.data("ui-autocomplete-item", item);

				ul.append(li);
			},

			_renderTotalItem: function(ul, item) {
				var coll,
					colr,
					total,
					a,
					li,
					resultsLabel = _this.totalResults === 1 ? 'singleResult' : 'results';

				coll = jQuery(document.createElement('div'))
					.addClass('col-1-2 hide-md');

				colr = jQuery(document.createElement('div'))
					.addClass('col-1-2 text-right pull-right-lg');

				total = jQuery(document.createElement('p'))
					.html(_this.totalResults + ' ' + _this.settings.labels[resultsLabel]);

				coll.append(total);

				a = jQuery(document.createElement('a'))
					.html(_this.settings.labels.seeAll)
					.attr('href', _this.settings.searchCenterLink.replace("{st}", _this.currentTerm))
					.append('<i class="icons-internal-link"></i>');

				colr.append(a);

				li = jQuery(document.createElement('li'))
					.addClass('ui-menu-item-results ui-menu-item clearfix')
					.append(coll)
					.append(colr)
					.data("ui-autocomplete-item", item);

				ul.append(li);
			}
		});


		jQuery(this.settings.input).catcomplete({
			appendTo: this.settings.resultSelector,
			source: function(request, response) {
				jQuery.ajax({
					url: _this.settings.url,
					dataType: "json",
					data: jQuery.extend(source, {
						searchterm: request.term
					}),
					success: function(data) {
						_this.currentTerm = request.term;
						_this.totalResults = data.TotalResults;
						response(data.Results, data.totalResultCount);
						_this.settings.onHTMLReady(data);
					}
				});
			},
			minLength: 3,
			open: function(event) {
				var resultsElement = jQuery(_this.settings.resultSelector),
					mainMenu = jQuery(_this.settings.mainMenu);

				mainMenu.addClass('no-open');
				resultsElement.css('display', 'block');
				resultsElement.find('.ui-autocomplete').css({
					position: 'absolute',
					left: 'auto',
					right: '-5px',
					top: '38px'
				});
				setTimeout(function() {
					_this.highlightTerms(event.target.value);
				}, 0);
			},
			close: function() {
				jQuery(_this.settings.resultSelector).css('display', 'none');
				jQuery(_this.settings.mainMenu).removeClass('no-open');
			},
			focus: function(ev) {
				ev.preventDefault();
			}
		});
	},

	highlightTerms: function(term) {
		var _this = this,
			results = jQuery(this.settings.resultSelector).find('.ui-menu-item:not(.ui-menu-item-results) a div');

		results.each(function() {
			var element = jQuery(this),
				text = jQuery(this).text();

			element.html(_this.highlight(term, text));
		});
	}
});
