CDQ.component.Popbox = Class.extend({
	/**
	 * Popbox constructor
	 *
	 * @method init
	 */
	init: function(settings) {
		this.settings = this.getSettings(settings);
		this.body = jQuery('body');
		this.bind();
	},

	getSettings: function(settings) {
		return jQuery.extend({
			trigger: jQuery('.open-popbox'),
			element: jQuery('.popbox')
		}, settings);
	},

	bind: function() {
		var _this = this;
		var closeButton = this.settings.element.find('.popbox__close, .btn-disagree');
		var acceptButton = this.settings.element.find('.btn-accept');

		CDQ.global.util.tappable(this.settings.element, function(ev) {
			if (ev.target == this) {
				_this.close(jQuery(ev.target));
			}
		});

		this.settings.trigger.on(CDQ.global.activateAction, function() {
			_this.open(jQuery(this).data('target'));
		});

		closeButton.on(CDQ.global.activateAction, function() {
			_this.close(jQuery(this));
		});

		acceptButton.on(CDQ.global.activateAction, function() {
			_this.close(jQuery(this));
		});

		jQuery(window).on('orientationchange', function() {
			_this.setContentHeight(_this.settings.element.closest('.is-open'), true);
		});

		jQuery(document).on('keyup', function(ev) {
			if (ev.keyCode == 27) {
				_this.close();
			}
		})
	},

	open: function(el, href, target) {
		var element = jQuery(el);
		var _this = this;


		if (href) {
			_this.setButtonHref(element, href, target);
		}

		_this.checkContent(element, function() {
			element.addClass('is-open');
		});
	},

	setButtonHref: function(element, href, target) {
		var button = element.find('.btn-accept');

		button.attr('href', href);

		if (target) {
			button.attr('target', target);
		}
	},

	close: function(trigger) {
		var popbox = trigger ? trigger.closest(this.settings.element) : this.settings.element;

		popbox.removeClass('is-open');
		this.body.removeClass('overlay');

		if (this.hasVideo(popbox)) {
			jQuery(window).trigger('Video.destroy', [popbox]);
		}
	},

	checkContent: function(popbox, cb) {
		var hasElement = popbox.find('.newsletter').length > 0 ? true : false;
		var callback = cb || function() {};


		if (hasElement) {
			if (window.innerWidth <= 480) {
				return this.openIframeSrc(popbox);
			}
			this.isIOS(popbox);
			this.setNewsletterHeight(popbox);
		}

		this.body.addClass('overlay');

		this.setContentHeight(popbox, false, function() {
			callback();
			jQuery(window).trigger('Popbox.open');
		});
	},

	isIOS: function(popbox) {
		if (CDQ.global.util.isIOS()) {
			var newsletter = popbox.find('.newsletter');
			newsletter.addClass('is-ios');
		}
	},

	openIframeSrc: function(popbox) {
		var src = popbox.find('iframe').attr('src');

		window.open(src, '_blank');
	},

	setContentHeight: function(popbox, force, cb) {
		var windowHeight = window.innerHeight;
		var popboxWrapper = popbox.find('.popbox__wrapper');
		var popboxContent = popbox.find('.popbox__content');
		var popboxHeader = popbox.find('.popbox__header');
		var popboxFooter = popbox.find('.popbox__footer');
		var popboxHeight = popboxWrapper.outerHeight(true);
		var callback = cb || function() {};

		if (popboxHeight > windowHeight || force) {
			popboxContent.height((windowHeight - popboxHeight - popboxHeader.outerHeight() - popboxFooter.outerHeight()) + popboxWrapper.outerHeight());
		}

		popboxWrapper.scrollTop(0);

		callback();
	},

	setNewsletterHeight: function(popbox) {
		var windowHeight = window.innerHeight;
		var newsletter = popbox.find('.newsletter');
		var newsletterHeight = newsletter.outerHeight();
		var popboxWrapper = popbox.find('.popbox__wrapper');
		var popboxHeight = popboxWrapper.outerHeight(true);

		newsletter.height(windowHeight - popboxHeight + newsletterHeight);
	},

	hasVideo: function (popbox) {
		var videoWrapper = popbox.find('.dynamic-player');

		return videoWrapper.length > 0 ? true : false;
	}
});
