/**
 * Build gateway infographic circles
 *
 * @class GatewayTitle
 * @constructor
 * @namespace component
 * @extend core.CanvasStage
 */
CDQ.component.GatewayTitle = CDQ.core.CanvasStage.extend({

	/**
	 * GatewayCircles constructor
	 *
	 * @method init
	 * @param {Object} settings Settings object
	 * @param {Object} parent GatewayInfographic methods and properties
	 */
	init: function(settings, parent) {
		this.inherited().init(settings);

		this.parent = parent;
		this.topY = 2.5; // from line: 2.5 + 5 = 7.5
		this.bottomY = 427.5;
		this.set = {
			top: this.paper.set(),
			bottom: this.paper.set()
		};

		this.drawTitle();
	},

	/**
	 * Draw infographic title
	 *
	 * @method drawTitle
	 */
	drawTitle: function() {
		var _this = this,
			titleTop,
			animation,
			titleBottom,
			duration = 500;

		titleTop = this.drawText('mainTitle', this.stage.xCenter, 8, {
			fill: this.settings.colors.bg1,
			font: '13px Arial',
			'text-anchor': 'middle',
			opacity: 0
		});


		titleBottom = this.drawText('bottomTitle', this.stage.xCenter, this.bottomY, {
			fill: this.settings.colors.bg1,
			font: '13px Arial',
			'text-anchor': 'middle',
			opacity: 0
		});

		animation = Raphael.animation({
			opacity: 1
		}, duration, function() {
			_this.drawTitleLines(titleTop, titleBottom);
		}).delay(duration);

		titleTop.animate(animation);
		titleBottom.animateWith(titleTop, animation, {
			opacity: 1
		}, duration);

		this.set.top.push(titleTop);
		this.set.bottom.push(titleBottom);
	},

	/**
	 * Draw infographic lines around title
	 *
	 * @method drawTitleLines
	 * @param {RaphaelJSPath} top Top title element
	 * @param {RaphaelJSPath} bottom Bottom title element
	 */
	drawTitleLines: function(top, bottom) {
		var linesAttr = {
				stroke: this.settings.colors.bg1,
				'stroke-width': 3
			},
			topLeft,
			topRight,
			bottomLeft,
			bottomRight,
			tBBox = top.getBBox(),
			bBBox = bottom.getBBox(),
			titleData = this.getTitleData();

		titleData.topLeft[0][2] = tBBox.x - 10;
		topLeft = this.drawLine(titleData.topLeft, linesAttr);

		titleData.topRight[0][0] = tBBox.x2 + 10;
		topRight = this.drawLine(titleData.topRight, linesAttr);

		titleData.bottomLeft[0][2] = bBBox.x - 10;
		bottomLeft = this.drawLine(titleData.bottomLeft, linesAttr);

		titleData.bottomRight[0][0] = bBBox.x2 + 10;
		bottomRight = this.drawLine(titleData.bottomRight, linesAttr);

		this.addToSet('top', [topLeft, topRight]);
		this.addToSet('bottom', [bottomLeft, bottomRight]);

		this.animateTitleLines(titleData);
	},

	/**
	 * Animate the drawn lines
	 *
	 * @method animateTitleLines
	 * @param {Object} titleData Title object
	 */
	animateTitleLines: function(titleData) {
		var _this = this,
			rect1,
			rect2,
			rect3,
			rect4,
			animation,
			duration = 500,
			bottomY = this.bottomY - 12,
			rectw = titleData.topLeft[0][2],
			rectAttr = {
				fill: this.settings.colors.stage,
				stroke: 'none'
			},
			anims = this.getAnimationData(rectw),
			hideElement;

		hideElement = function(el) {
			var params = {
				opacity: 0
			};
			if (el) {
				return el.attr(params);
			}

			return this.attr(params);
		};

		animation = Raphael.animation(anims.topLeft, duration, hideElement);

		this.set.top.push(rect1 = this.drawRectangle(0, this.topY, rectw, 15, rectAttr));
		this.set.top.push(rect2 = this.drawRectangle(0, this.topY, rectw, 15, rectAttr));
		this.set.bottom.push(rect3 = this.drawRectangle(0, bottomY, rectw, 15, rectAttr));
		this.set.bottom.push(rect4 = this.drawRectangle(0, bottomY, rectw, 15, rectAttr));

		rect2.transform("t" + titleData.topRight[0][0] + ",0");
		rect3.transform("t" + titleData.bottomLeft[0][0] + ",0");
		rect4.transform("t" + titleData.bottomRight[0][0] + ",0");

		rect1.animate(animation, duration);
		rect2.animateWith(rect1, animation, anims.topRight, duration, hideElement);
		rect3.animateWith(rect1, animation, anims.bottomLeft, duration, hideElement);
		rect4.animateWith(rect1, animation, anims.bottomRight, duration, function() {
			hideElement(this);
			_this.done();
		});
	},

	/**
	 * Return the title data object
	 *
	 * @method getTitleData
	 * @return {Object} Title object data
	 */
	getTitleData: function() {
		var lineTop = this.topY + 5,
			lineW = 246;

		return {
			topLeft: [
				[0, lineTop, lineW, lineTop],
				[1.5, lineTop, 1.5, lineTop + 10]
			],
			topRight: [
				[this.settings.width - lineW, lineTop, this.settings.width, lineTop],
				[this.settings.width - 1.5, lineTop, this.settings.width - 1.5, lineTop + 10]
			],
			bottomLeft: [
				[0, this.bottomY, lineW, this.bottomY],
				[1.5, this.bottomY, 1.5, this.bottomY - 10]
			],
			bottomRight: [
				[this.settings.width - lineW, this.bottomY, this.settings.width, this.bottomY],
				[this.settings.width - 1.5, this.bottomY, this.settings.width - 1.5, this.bottomY - 10]
			]
		};
	},

	/**
	 * Return animation data for each line
	 *
	 * @method getAnimationData
	 * @param {Integer} width Size of each line
	 * @return {Object} Animation data
	 */
	getAnimationData: function(width) {
		var obj = {
			topLeft: {
				transform: 't-' + width + ',0'
			},
			topRight: {
				transform: 't' + this.settings.width + ',0'
			},
			bottomLeft: {
				transform: 't-' + width + ',0'
			},
			bottomRight: {
				transform: 't' + this.settings.width + ',0'
			}
		};

		return obj;
	},

	/**
	 * Add elements to a set in order to provide smooth animations
	 *
	 * @method addToSet
	 * @param {String} position The position of set
	 * @param {Array} elements Paths of elements
	 */
	addToSet: function(position, elements) {
		var arr,
			arrLenth,
			i;

		for (arr in elements) {
			if (elements.hasOwnProperty(arr)) {
				arrLenth = elements[arr].length;

				for (i = arrLenth - 1; i >= 0; i--) {
					this.set[position].push(elements[arr][i]);
				}
			}
		}
	}
});
