/**
 * Build gateway infographic circles
 *
 * @class GatewayCircles
 * @constructor
 * @namespace component
 * @extend core.CanvasStage
 */
CDQ.component.GatewayCircles = CDQ.core.CanvasStage.extend({
	/**
	 * GatewayCircles constructor
	 *
	 * @method init
	 * @param {Object} settings Settings object
	 * @param {Object} parent GatewayInfographic methods and properties
	 */
	init: function(settings, parent) {
		this.inherited().init(settings);
		this.addCustomAttributes();

		this.parent = parent;

		this.topY = 34.5;
		this.xL = 7;
		this.rad = 61;
		this.strokeSize = 14;
		this.duration = 500;
		this.set = {
			circles: [],
			strokes: []
		};
		this.currentCircle = 0;

		this.animationData = this.getAnimationData();


		this.getCircles();
	},

	/**
	 * Get circles data and draw it in canvas
	 *
	 * @method getCircles
	 */
	getCircles: function() {
		var circles = this.settings.data.circles,
			circlesLength = circles.length,
			circle,
			i,
			x = -(this.rad - this.strokeSize) + this.xL,
			y = this.topY + this.rad + this.strokeSize,
			strokeTmp;

		for (i = 0; i < circlesLength; i++) {
			circle = circles[i];

			x += this.rad * 2;

			this.set.circles[i] = this.paper.set();
			this.set.circles[i].push(
				this.drawCircle(x, y),
				this.drawText(circle.text, x, y, {
					fill: this.settings.colors.bg1,
					font: '13px Arial',
					opacity: 0
				})
			);

			this.set.strokes[i] = [];
			strokeTmp = this.drawStroke(x, y, this.animationData[i].colors[0], 0);
			this.set.strokes[i].push(strokeTmp);
			this.drawFakeStrokes(x, y, this.animationData[i].pos[0]);
			CDQ.global.infograficColorsSet[this.animationData[i].colors[0]].push(strokeTmp); // apend to color set

			if (this.animationData[i].pos.length == 2) {
				strokeTmp = this.drawStroke(x, y, this.animationData[i].colors[1], this.animationData[i].pos[0]).toBack();
				this.set.strokes[i].push(strokeTmp);
				this.drawFakeStrokes(x, y, this.animationData[i].pos[1]);
				CDQ.global.infograficColorsSet[this.animationData[i].colors[1]].push(strokeTmp); // apend to color set
			}

			x += this.strokeSize * 3;

			if (i == circlesLength - 1) {
				this.animate();
				strokeTmp = null;
			} else if (i == 1) {
				this.createGap(this.set.strokes[i]);
			}
		}
	},

	/**
	 * Create a gap in the middle of drawn circles in order to give the sensation it is divided
	 *
	 * @method createGap
	 * @param {RaphaelJSSet} strokes The strokes of circles that will be cut
	 */
	createGap: function(strokes) {
		var bbox = strokes[0].getBBox(),
			gap,
			gap2,
			height = this.strokeSize + 1.5,
			width = 4;

		gap = this.drawRectangle(bbox.x - 4, bbox.y - (this.strokeSize / 2) - 0.5, width, height, {
			fill: this.settings.colors.stage,
			stroke: 0
		});

		gap2 = this.drawRectangle(bbox.x - 4, bbox.y + (this.rad * 2) + (this.strokeSize / 2) - 0.5, width, height, {
			fill: this.settings.colors.stage,
			stroke: 0
		});
	},

	/**
	 * Draw a circle fill in the same color of stage to avoid see something isn't desired, like arrows bellow it
	 *
	 * @method drawCircle
	 * @param {Integer} x Circle x position
	 * @param {Integer} y Circle y position
	 */
	drawCircle: function(x, y) {
		var circle = this.drawPath({
			opacity: 0,
			"stroke": 0,
			fill: this.settings.colors.stage,
			arc: [x, y, 100, 100, this.rad]
		});

		return circle;
	},

	/**
	 * Draw the circle stroke
	 *
	 * @method drawStroke
	 * @param {Integer} x Stroke x position
	 * @param {Integer} y Stroke y position
	 * @param {String} color Color property to get from {{#crossLink "core.CanvasStage/settings:property"}}{{/crossLink}}
	 * @param {Integer} startFrom A value that defines where the stroke starts
	 * @return {RaphaelJSPath} A stroke path
	 */
	drawStroke: function(x, y, color, startFrom) {
		var size = this.rad + (this.strokeSize / 2),
			arc = [x, y, startFrom, 100, size],
			stroke;

		stroke = this.drawPath({
			"stroke": this.settings.colors[color],
			"stroke-width": this.strokeSize,
			opacity: 0,
			arc: arc
		});

		stroke.data('arc', arc);

		return stroke;
	},

	/**
	 * Draw a fake stroke to workaround the opacity issue
	 *
	 * @method drawFakeStrokes
	 * @param {Integer} x Stroke x position
	 * @param {Integer} y Stroke y position
	 * @param {Integer} strokeTo A value that defines where the stroke starts
	 */
	drawFakeStrokes: function(x, y, strokeTo) {
		var size = this.rad + (this.strokeSize / 2),
			arc = [x, y, strokeTo, 100, size],
			stroke;

		stroke = this.drawPath({
			"stroke": this.settings.colors.stage,
			"stroke-width": this.strokeSize,
			opacity: 1,
			arc: arc
		}).toBack();
	},

	/**
	 * Draw the arc
	 *
	 * @method addCustomAttributes
	 * @return {Object} The drawn path
	 */
	addCustomAttributes: function() {

		var drawTheCircleVector = function(xloc, yloc, value, total, R) {
			var alpha = 360 / total * value,
				a = (90 - alpha) * Math.PI / 180,
				x = xloc + R * Math.cos(a),
				y = yloc - R * Math.sin(a),
				path;

			if (total == value) {
				path = [
					["M", xloc, yloc - R],
					["A", R, R, 0, 1, 1, xloc - 0.01, yloc - R]
				];
			} else {
				path = [
					["M", xloc, yloc - R],
					["A", R, R, 0, +(alpha > 180), 1, x, y]
				];
			}

			return {
				path: path
			};
		};

		this.paper.customAttributes.arc = drawTheCircleVector;
	},

	/**
	 * Animate stokes
	 *
	 * @method animate
	 */
	animate: function() {
		var strokes = this.set.strokes[this.currentCircle],
			data = this.animationData[this.currentCircle],
			_this = this,
			totalStrokes = data.pos.length,
			arc,
			x,
			y,
			rad,
			total = 100,
			duration1 = this.getProportionalDuration(data.pos[0]),
			duration2 = this.getProportionalDuration(total - data.pos[0]),
			callback;

		arc = strokes[0].data('arc');

		x = arc[0];
		y = arc[1];
		total = arc[3];
		rad = arc[4];

		callback = function() {
			_this.currentCircle++;

			if (_this.currentCircle < _this.animationData.length) {
				_this.animate();
			} else {
				_this.done();
			}
		};

		// stroke 1
		strokes[0].attr({
			opacity: 1
		}).animate({
			arc: [x, y, data.pos[0], total, rad]
		}, duration1, function() {
			if (totalStrokes == 2) {
				// stroke 2
				strokes[1].attr({
					opacity: 1
				}).animate({
					arc: [x, y, data.pos[1], total, rad]
				}, duration2, function() {
					// text
					_this.animateText(callback, strokes);

				});
			} else {
				// text
				_this.animateText(callback, strokes);
			}
		});
	},

	/**
	 * Animate text inside circles
	 *
	 * @method animateText
	 * @param {Function} callback to be executed on animation end
	 * @param {Array} strokes An array containing the strokes to fix
	 */
	animateText: function(callback, strokes) {
		// gmb ie7 and ie8
		if (!Modernizr.svg || jQuery.browser.opera) {
			strokes[0].attr({
				'stroke-linecap': 'round'
			});
			if (strokes.length > 1) {
				strokes[1].attr({
					'stroke-linecap': 'round'
				});
			}
		}
		// gmb end

		this.set.circles[this.currentCircle].animate({
			opacity: 1
		}, this.duration / 2, function() {
			callback();
		});
	},

	/**
	 * Get a duration relative to the proportion of strokes.
	 * @example n = 100 (full stroke) returns (1 * this.duration)
	 * @example n = 50 (half stroke) returns (0.5 * this.duration)
	 *
	 * @method getProportionalDuration
	 * @param {integer} n Stroke proportion
	 * @return {Integer} Proportional duration in ms
	 */
	getProportionalDuration: function(n) {
		return n / 100 * this.duration;
	},

	/**
	 * Returns an object with animation data for each circle to draw
	 *
	 * @method getAnimationData
	 * @return {Object} Animation data
	 */
	getAnimationData: function() {
		// pos: [finalPos in %, finalPos in %] => both should be equal to 100%
		// colors: [pos1 color, pos2 color] => max 2 colors per circle allowed
		var data = [{
			pos: [100],
			colors: ['bg2']
		}, {
			pos: [50, 100],
			colors: ['bg1', 'bg2']
		}, {
			pos: [100],
			colors: ['bg1']
		}, {
			pos: [100],
			colors: ['bg1']
		}];

		return data;
	}
});
