/**
 * Setup collapsible effects on tables
 *
 * 	// Settings object isn't required. The example bellow shows the default values
 *  	new CDQ.component.CollapsibleTables({
 *  		table: '.collapsible-table',
 *		  rows: 'thead tr, tbody tr',
 *		  limit: 5,
 *		  btn: '.show-all'
 *    	});
 *
 * @class CollapsibleTables
 * @constructor
 * @namespace component
 */

CDQ.component.CollapsibleTables = Class.extend({
	/**
	 * Constructor
	 *
	 * @method init
	 * @param {Object} settings receive an object that contains settings to be used in the method.
	 */
	init: function(settings) {
		this.settings = this.getSettings(settings);

		this.btn = jQuery(this.settings.btn);

		this.setup();
	},

	/**
	 * Setup all buttons available in the page
	 *
	 * @method setup
	 */
	setup: function() {
		var _this = this;

		this.btn.each(function() {
			_this.bind(jQuery(this));
		});
	},

	/**
	 * Override default settings by those sent via constructor
	 *
	 * @method gerSettings
	 * @param {Object} settings Settings to replace the default ones
	 * @return {Object} Default settings merged with constructors settings
	 */
	getSettings: function(settings) {
		var defaultSettings = {
			table: '.collapsible-table',
			rows: 'thead tr, tbody tr',
			limit: 5,
			btn: '.show-all'
		};

		return jQuery.extend(defaultSettings, settings);
	},

	/**
	 * Set click event on buttons to collapse table
	 *
	 * @method bind
	 * @param {HTMLElement} btn Buttons
	 */
	bind: function(btn) {
		this.$table = jQuery(this.settings.table + '[data-table="' + btn.attr('data-table') + '"]'),
		this.$rows = this.$table.find(this.settings.rows),
		this.$wrapper = this.$table.parent();
		this.toggleTable(this.$wrapper, this.getMaxHeight(this.$rows));

		jQuery(window).resize(this.draw.bind(this));

		btn.on('click', this.draw.bind(this));
	},

	draw: function () {
		this.toggleTable(this.$wrapper, this.getMaxHeight(this.$rows));
		if (!this.$wrapper.hasClass('collapsed')) {
			this.$wrapper.addClass('collapsed');
			return this.toggleTable(this.$wrapper, 'auto');
		}

		this.$wrapper.removeClass('collapsed');
		return this.toggleTable(this.wrapper, this.getMaxHeight(this.$rows));
	},

	/**
	 * Get the height off first rows, from o to settings.limit
	 *
	 * @method getMaxHeight
	 * @param  {HTMLElement} rows Table rows
	 * @return {Integer} Visible rows height
	 */
	getMaxHeight: function(rows) {
		var h = 0;
		for (var i = 0; i < this.settings.limit; i++) {
			h += jQuery(rows[i]).outerHeight(true);
		}

		return h;
	},

	/**
	 * Give a new height for table
	 *
	 * @method toggleTable
	 * @param {HTMLElement} wrapper Table body element
	 * @param {Integer|String} css property to define a new height. Number or 'auto'.
	 */
	toggleTable: function(wrapper, height) {
		if(!wrapper) {
			return;
		}

		wrapper.css({
			height: height
		});
	}
});
