/**
 * Geographic exposures bar chart
 *
 *	new CDQ.component.GeographicExposuresBarChart(Object);
 *
 * @class GeographicExposuresBarChart
 * @constructor
 * @extends core.Charts
 * @namespace component
 */
CDQ.component.GeographicExposuresBarChart = CDQ.component.HorizontalBarChart.extend({
	setupChart: function(obj) {
		this.data = obj;

		var seriesData = [],
			categories = [],
			i;

		for (i = 0; i < obj.Countries.length; i++) {
			seriesData.push(obj.Countries[i].Exposure);
			categories.push(obj.Countries[i].Country);
		}

		this.chartData.series[0].data = seriesData;
		this.chartData.xAxis.categories = categories;

		this.createChart();
	},

	callback: function() {
		new CDQ.component.GeographicExposuresMap({
			data: this.data
		});
	}
});
