/**
 * Horizontal bar chart widget
 *
 *	new CDQ.component.HorizontalBarChart(Object);
 *
 * @class HorizontalBarChart
 * @constructor
 * @namespace component
 * @extends core.Charts
 */
CDQ.component.HorizontalBarChart = CDQ.core.Charts.extend({

	getChartData: function(chartData) {
		var defaultChartData = {
			// colors: ['#98a2ad'],
			colors: [
				'#e39318',
				'#eaad52',
				'#efc98b',
				'#001645',
				'#6e7794',
				'#a4a7ba',
				'#768998',
				'#a9b0b6',
				'#d9e3e5'
			],
			chart: {
				backgroundColor: '#f4f6f7',
				spacing: [1, 0, 0, 0],
				type: 'bar',
				height: 400
			},
			title: {
				align: 'left',
				style: {
					color: '#3d4855',
					fontFamily: 'UniversDeutscheBank, Helvetica, Arial, sans-serif',
					fontSize: '18px'
				},
				text: 'Sector Allocation <small>As of: 15 Jul 2014 04:00 PM</small>',
				useHTML: true
			},
			xAxis: {
				categories: [], // labels names
				labels: {
					style: {
						color: '#4f5356',
						fontSize: '14px'
					}
				},
				tickWidth: 0,
				lineWidth: 0,
				title: {
					text: null
				},
				stackLabels: {
					enabled: true,
					style: {
						fontWeight: 'bold',
						color: '#4f5356',
						fontSize: '13px'
					}
				}
			},
			yAxis: {
				gridLineWidth: 0,
				labels: {
					enabled: false
				},
				title: {
					text: null
				}
			},
			tooltip: {
				enabled: false
			},
			plotOptions: {
				bar: {
					borderWidth: 0,
					dataLabels: {
						enabled: false
					},
					groupPadding: 0,
					pointWidth: 34,
					pointPadding: 0,
					colorByPoint: true,
					minPointLength: 1
				}
			},
			legend: {
				enabled: false
			},
			credits: {
				enabled: false
			},
			series: [{
				dataLabels: {
					enabled: true,
					color: '#4f5356',
					align: 'right',
					x: 50,
					// y: 0,
					format: '{y:.2f}%',
					inside: false,
					style: {
						fontWeight: 'bold',
						fontSize: '13px',
						fontFamily: 'Arial, sans-serif'
					}
				}
			}]
		};

		return jQuery.extend(true, defaultChartData, chartData);
	},

	setupChart: function(obj) {
		var seriesData = [],
			categories = [],
			i;

		for (i = 0; i < obj.data.length; i++) {
			if (!(obj.data[i].Weighting < 0.005 && obj.data[i].Weighting >= 0) && !(obj.data[i].Weighting <= 0 && obj.data[i].Weighting > -0.005)) {
				seriesData.push(obj.data[i].Weighting);
				categories.push(obj.data[i].AllocationName);
			}
		}

		this.chartData.series[0].data = seriesData;
		this.chartData.xAxis.categories = categories;
		this.chartData.chart.height = obj.data.length <= 8 ? this.chartData.chart.height : this.chartData.chart.height + (obj.data.length - 8) * 60;
		this.createChart();
	}
});
