CDQ.component.Gallery = Class.extend({
	init: function(settings) {
		this.settings = this.getSettings(settings);

		this.bind();
	},

	getSettings: function(settings) {
		return jQuery.extend(true, {
			element: jQuery('.open-gallery'),
			options: {
				thumbWidth: 165,
				thumbMargin: 10,
				thumbContHeight: 120,
				download: false,
				fullScreen: false,
				autoplayControls: false,
				zoom: false,
				dynamic: true,
				dynamicEl: null
			}
		}, settings);
	},

	bind: function() {
		var _this = this;

		this.settings.element.on('click', function() {
			_this.setup(jQuery(this));
			return false;
		});
	},

	setup: function(element) {
		var _this = this;

		this.gallery = element.lightGallery(this.settings.options);

		this.gallery.on('onAfterOpen.lg', function() {
			_this.adjustmentsHandler();
		});
	},

	adjustmentsHandler: function() {
		this.wrapThumbnails();
		this.defineHeightHelper();
		this.bindResize();
	},

	wrapThumbnails: function() {
		var thumbnails = jQuery('.lg-thumb.group');
		var wrapper = jQuery(document.createElement('div')).addClass('lg-wrapper-fix');

		thumbnails.wrap(wrapper);
	},

	bindResize: function() {
		var _this = this;

		jQuery(window).on('resize', function() {
			_this.defineHeightHelper();
		});
	},

	defineHeightHelper: function() {
		var winHeight = window.innerHeight;
		var gallery = jQuery('.lg-outer');

		if (winHeight > 768) {
			gallery.removeClass('lg-large').addClass('lg-xlarge');
		} else if (winHeight <= 768 && winHeight > 400) {
			gallery.removeClass('lg-xlarge lg-medium').addClass('lg-large');
		} else {
			gallery.removeClass('lg-large lg-xlarge').addClass('lg-medium');
		}
	}
});
