/**
 * Custom fonts preloader
 *
 *	// Settings object isn't required. The example bellow shows the default values
 *	new CDQ.core.PreloadFont(Array);
 *
 * @class PreloadFont
 * @constructor
 * @namespace core
 */
CDQ.core.PreloadFont = Class.extend({

	/**
	 * Font preloader constructor
	 *
	 * @method init
	 * @param {Array} fonts Fonts to preload
	 */
	init: function(fonts) {
		this.loadedFonts = 0;
		this.interval;
		this.fonts = fonts;
		this.preloadFont();
	},

	/**
	 * Callback fired when all fonts have been preloaded
	 *
	 * @method callback
	 */
	callback: function() {
		jQuery(window).trigger('Scrollable.SetSizes');
	},

	/**
	 * Start to load fonts
	 *
	 * @method preloadFont
	 */
	preloadFont: function() {
		for (var i = 0, l = this.fonts.length; i < l; ++i) {
			this.loadFont(this.fonts[i]);
		}
	},

	/**
	 * Load font
	 *
	 * @method loadFont
	 * @param {String} font Font name
	 */
	loadFont: function(font) {
		var _this = this,
			node = document.createElement('span'),
			width;

		node.innerHTML = 'giItT1WQy@!-/#';
		node.style.position = 'absolute';
		node.style.left = '-10000px';
		node.style.top = '-10000px';
		node.style.fontSize = '300px';
		node.style.fontFamily = 'sans-serif';
		node.style.fontVariant = 'normal';
		node.style.fontStyle = 'normal';
		node.style.fontWeight = 'normal';
		node.style.letterSpacing = '0';
		document.body.appendChild(node);

		width = node.offsetWidth;

		node.style.fontFamily = font;

		if (!this.checkFont(node, width)) {
			this.interval = setInterval(function() {
				_this.checkFont(node, width);
			}, 50);
		}
	},

	/**
	 * Check if font have been loaded
	 *
	 * @method checkFont
	 * @param {HTMLObject} node HTML node object
	 * @param {Integer} width Current font width
	 */
	checkFont: function(node, width) {
		if (node && node.offsetWidth != width) {
			++this.loadedFonts;
			node.parentNode.removeChild(node);
			node = null;
		}

		if (this.loadedFonts >= this.fonts.length) {
			if (this.interval) {
				clearInterval(this.interval);
			}
			if (this.loadedFonts == this.fonts.length) {
				this.callback();
				return true;
			}
		}
	}
});
