/**
 * Component responsible for load and instantiate the Financial Weather and check if it's
 * necessary a resizing behavior, binding the necessary events
 *
 * @class FinancialWeather
 * @constructor
 * @namespace component
 */
CDQ.component.FinancialWeather = Class.extend({
	/**
	 * Constructor that receives some settings and start to add the financial weather containers
	 *
	 * @method init
	 * @param {Object} [settings] receive an object that contains settings to be used in the method.
	 *     @param  {Object} [settings.small='{}'] If it's necessary to instantiate a small version, the settings should be passed by this object
	 *			@param  {String} [settings.small.assetsPath='features/financial-weather'] A string passing the path where the root folder of financial weather is located
	 *			@param  {String} [settings.small.containerWidth='100%'] A string with the width of the financial weather, for small version probably will be 100%
	 *			@param  {String} [settings.small.weatherMapLocaleId='en'] Grabs the data related to the locale passed
	 *			@param  {String} [settings.small.weatherMapCountryId='UK'] Call data related to the country ID
	 *			@param 	{String} [settings.small.configFile='features/financial-weather/server/data/UK_en.json'] config json path (full including name)
	 *			@param 	{String} [settings.small.indexFile='features/financial-weather/server/fwglobalindexlist.json'] index file json path (full including name)
	 *			@param 	{String} [settings.small.containerID='fwt-container-small'] container id to include the whole financial weather
	 *			@param 	{String} [settings.small.mapID='map-small'] Map SVG container, needs to be different from the regular if both are instantianting in the same page
	 *			@param 	{String} [settings.small.canvasId='svg_fw_map_small'] Id to use to differentiate regular from small, small needs to be different to regular
	 *		@param 	{Object} [settings.regular={}] receive an object that contains settings to be used in the method
	 *		    @param 	{String} [settings.regular.assetsPath='features/financial-weather'] A string passing the path where the root folder of financial weather is located
	 *		    @param 	{String} [settings.regular.factBookLocaleId='ENG']
	 *			@param 	{String} [settings.regular.configFile='features/financial-weather/server/data/UK_en.json'] config json path (full including name)
	 *			@param 	{String} [settings.regular.containerID='fwt-container-small'] container id to include the whole financial weather
	 *			@param 	{String} [settings.regular.mapID='map-small'] Map SVG container, needs to be different from the small if both are instantianting in the same page
	 *			@param 	{String} [settings.regular.canvasId='svg_fw_map_small'] Id to use to differentiate regular from small, small needs to be different to regular
	 */
	init: function(settings) {
		this.settings = this.getSettings(settings);
		this.load();
	},

	/**
	 * Define the settings extending from the parameters received in the class
	 *
	 * @method getSettings
	 * @param  {Object} settings documented in the method init
	 * @return {Object} the extended settings that it's gonna be used
	 */
	getSettings: function(settings) {
		var defaultSettings = {
			jsPath: 'features/financial-weather/js/',
			small: null,
			regular: null
		};

		return jQuery.extend(true, defaultSettings, settings);
	},

	/**
	 * Check the used settings and load the right script
	 *
	 * @method load
	 */
	load: function() {
		var _this = this,
			path;

		if (this.settings.small && this.settings.regular) {
			path = this.settings.jsPath + 'application-mixed.js'
			this.isMixed = true;
		}
		if (this.settings.small && !this.settings.regular) {
			path = this.settings.jsPath + 'application-small.js'
		}
		if (!this.settings.small && this.settings.regular) {
			path = this.settings.jsPath + 'application.js'
		}

		jQuery.getScript(path, function() {
			_this.setup();
		});
	},

	/**
	 * Instantiate the financial weather, if it's needed to instantiate both, calls setupResizable
	 *
	 * @method Setup
	 * @return void
	 */
	setup: function() {
		var _this = this;

		if (_this.isMixed) {
			return this.setupResizable(function() {
				_this.bind();
			});
		}
		if (this.settings.small) {
			new FWAPP.component.FWSmall(this.settings.small);

			if (this.settings.small.containerHeight == '100%') {
				this.bindSmall();
			}
		} else if (this.settings.regular) {
			new FWAPP.component.FW(this.settings.regular);
		}
	},

	/**
	 * Instantiate the financial weather depending on the screensize and waits for be called again if the screen gets resized
	 *
	 * @method setupResizable
	 * @param  {Function} callback function that will be called after instantiate, can be not called too.
	 */
	setupResizable: function(cb) {
		var callback = cb || function() {};

		if (jQuery(window).width() > 768 && !this.fwRegular) {
			this.fwRegular = new FWAPP.component.FW(this.settings.regular);
		} else if (!this.fwSmall) {
			this.fwSmall = new FWAPP.component.FWSmall(this.settings.small);
		}

		callback();
	},

	/**
	 * Bind the resize event
	 *
	 * @method bind
	 */
	bind: function() {
		var _this = this;

		jQuery(window).resize(function() {
			if (!this.fwSmall || !this.fwRegular) {
				_this.setupResizable();
			}
		})
	},

	bindSmall: function() {
		var box = jQuery('.fw-box'),
			row = box.closest('.row'),
			resizeContents = function() {
				if (CDQ.global.util.getViewportSize().width <= CDQ.global.util.getBreakpoints('large')) {
					box.css('height', '348px');
				} else {
					box.css('height', 'auto');
					box.css('height', row.height());
				}

			},
			timer;

		jQuery(window).on('resize', function() {
			clearTimeout(timer);
			timer = setTimeout(resizeContents, 50);
		});

		resizeContents();
	}
});
